import React from "react";

import {
  Button,
  Card,
  CardBody,
  Collapse,
  Modal, 
  ModalBody, 
  ModalFooter,
  ModalHeader
} from "reactstrap";

import axios from 'axios';
import NotificationAlert from "react-notification-alert";

import MaterialsForm from '../components/MaterialsCustomComponents/MaterialsForm';
import MaterialsHistory from '../components/MaterialsCustomComponents/MaterialsHistory';
import '../components/QuotationElements/loader.min.css';

class Materials extends React.Component {

  _isMounted = false;
  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  _loadingMaterials = false;


  constructor(props) {
    super(props)
    this.state = {
      formdata: {},
      materials: [],
      clientOptions: [],
      openedCollapses: ["new"],
      SeasonsOps: [],
      action: 'insert',
      actionText: 'Add',
      deleteModal: false,
      toDeleteMaterial: {"codigo":''},
      loading: false,
      importLoading: false,
      importId: null,
      importModal: false,
      materialInfo: {},
    }
  }

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    if (response.status!==undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  toggleDeleteModal = () => {
    this._isMounted && this.setState({
      deleteModal: !this.state.deleteModal
    });
  }

  checkPageMode = () => {
    let pageMode=document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    let modalClass;
    switch(pageMode){
      case true:
        bgclass="bg-ligh";
        headerclass="";
        modalClass="modal-white";
        break;
      default:
        bgclass="bg-dark";
        headerclass="text-primary";
        modalClass="modal-black";
        break;
    }
    return {bg: bgclass,hc: headerclass, mc: modalClass};
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert(options);
    }
  }

  collapsesToggle = collapse => {
    let openedCollapses = this.state.openedCollapses;
    if (openedCollapses.includes(collapse)) {
      this._isMounted && this.setState({
        openedCollapses: []
      });
    } else {
      this._isMounted && this.setState({
        openedCollapses: [collapse]
      });
    }
  };

  handleSubmit = (event,action,data) => {
    if(action === "insert") {
      //add material
      this._isMounted && this.setState({
        formdata: data,
        loading: true
      });
      this.addMaterial(data)
    }
    if(action === "update") {
      this._isMounted && this.setState({ loading: true });
      this.updateMaterial(data)
    }
  }

  formNotification = (type,msg) => {
    this.notify("tr", type, msg);
  }

  updateMaterial = (data) => {
    let changes = {};
    Object.keys(data).map( (element) => {
      let compareTo = "";
      if (element === "client") {
        compareTo = "id_client";
      } else if (element === "season") {
        compareTo = "id_season";
      } else {
        compareTo = element
      }
      if(data[element] !== this.state.formdata[compareTo]) {
        changes[element] = data[element];
      }
      return element;
    });

    if(Object.keys(changes).length>0) {
      axios.patch(process.env.REACT_APP_IP + '/materials/' + data.id + '/', 
        changes,
        { headers: { "Authorization": "Token " + this.userToken } }
      ).then(function () {
        this.notify("tr", "success", "Edit completed!");
        this.setState({
          formdata: {},
          loading: false
        });
        this.componentDidMount();
      }.bind(this)).catch(function(error){
        this.setState({
          formdata: {},
          loading: false
        });
        this.handleError(error,'update material');
      }.bind(this));
    }else{
      this.notify("tr", "info", "There are no changes on this material.");
    }
  }

  /**Materials API - add material */
  addMaterial = (data) => {
    axios.post(process.env.REACT_APP_IP + '/materials/', 
      data, 
      { headers: {
        "Authorization": "Token " + this.userToken
      } }
    ).then(function () {
      this.notify("tr", "success", "Material added!"); 
      this.setState({
        formdata: {},
        loading: false
      });
      this.componentDidMount();
    }.bind(this)).catch(function(error){
      this.setState({
        formdata: {},
        loading: false
      });
      this.handleError(error,'add material');
    }.bind(this));
  }

  /**Clients API - proveedores */
  getClients = () => {
    const clients = [];
    axios.get(process.env.REACT_APP_IP + '/clients/',
      { headers:{
        "Authorization": "Token " + this.userToken
      } }
    )
    .then( function(response) {
      response.data.map( (client, index) => {
        clients.push(
          {value: client.id, label: client.name}
        );
        return client;
      });
      this._isMounted && this.setState({clientOptions: clients})
    }.bind(this)).catch( function(error) {
      //console.log(error)
      this.handleError(error,"get clients");
    }.bind(this));
  }

  /**Seasons API - historico temporadas */
  seasonsHistory = () => {
    axios.get(process.env.REACT_APP_IP + '/seasons/',
      {headers:{
        "Authorization":"Token " + this.userToken
      }}
    ).then(function (response) {
      let seasonsData = [];
      response.data.map( (season, index) => 
        seasonsData.push(
          {value: season.id, label: season.name}
        ) 
      );
      this._isMounted && this.setState({SeasonsOps: seasonsData});
    }.bind(this)).catch(function(error){
      this.handleError(error,'seasons history');
    }.bind(this));
  }

  /**Materials API - historico materiales */
  getMaterials = () => {
    axios.get(process.env.REACT_APP_IP + '/materials/',
      {headers:{
        "Authorization": "Token " + this.userToken
      }}
    )
    .then( function(response) {
      this._loadingMaterials=true;
      this._isMounted && this.setState({materials: response.data});
    }.bind(this)).catch( function(error) {
      this.handleError(error,'get materials');
    }.bind(this));
  }

  handleEditMaterial = (data) => {
    this.collapsesToggle("new");
    this._isMounted && this.setState({
      formdata: data,
      action: "update",
      actionText: "Update"
    });
  }

  handelRemoveMaterial = (material) => {
    this._isMounted && this.setState({toDeleteMaterial: material});
    this.toggleDeleteModal();
  }

  removeSelectedMaterial = () => {
    this.setState({
      loading: true
    })
    axios.delete(
      process.env.REACT_APP_IP + '/materials/' + this.state.toDeleteMaterial.codigo + '/', 
      {headers:{
        "Authorization": "Token " + this.userToken
      }}
    ).then(function() {
      this._isMounted && this.setState( {toDeleteDM: {"id": ''} } );
      this.toggleDeleteModal();
      this.setState({
        formdata: {},
        loading: false
      });
      this.componentDidMount();
      this.notify("tr", "success", "Material deactivated.");
    }.bind(this)).catch(function(error) {
      this.setState({
        formdata: {},
        loading: false
      });
      this.handleError(error,'remove material');
    }.bind(this));
  }

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.seasonsHistory();
    this.getMaterials();    
    this._isMounted && this.setState({
      action:"insert",
      actionText: "Add"
    });
  }

  componentWillUnmount () {
    this._isMounted = false;
  }

  handleReset = () => {
    this.setState({
      formdata: {},
      loading: false
    });
    this.componentDidMount();
  }

  render() {
    const pageMode=this.checkPageMode();
    let inverted = pageMode.bg==="bg-ligh" ? 'inverted' : '';
    return (<>
      <div className="blackdiv" id="blackdiv" style={this.state.loading?{display:'flex'}:{display:'none'}}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{minHeight:400}}><p>&nbsp;</p></div>
        </div>
      </div>
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Modal isOpen={this.state.deleteModal}
          toggle={this.toggleDeleteModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleDeleteModal}>
            Deactivate Material.
          </ModalHeader>
          <ModalBody>
            <h6 className="text-danger">Warning!</h6>
            <p>
              If you deactivate the material <b className="text-warning">{this.state.toDeleteMaterial.codigo}</b> 
              you'll be unnable to use it in other costing sheets.
              Contact the system administrator to recover a material.
            </p>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleDeleteModal}>
              Close
            </Button>
            <Button color="primary" onClick={this.removeSelectedMaterial}>
              deactivate Material
            </Button>
          </ModalFooter>
        </Modal>
        
        <Modal isOpen={this.state.importModal}
          toggle={this.toggleImportModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Import Material.
          </ModalHeader>
          <ModalBody>
            {this.state.importId}
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleImportModal}>
              Close
            </Button>
            <Button color="primary" onClick={()=>this.fnImportMaterial(this.state.importId)}>
              Import
            </Button>
          </ModalFooter>
        </Modal>

        <Button color="primary" onClick={(e) => {e.preventDefault();this.collapsesToggle("new")}}>
          Create Material
        </Button>
        <Button color="primary" onClick={(e) => {e.preventDefault();this.collapsesToggle("table")}}>
          Materials Table
        </Button>
        <Collapse
          isOpen={this.state.openedCollapses.includes("new")}
        >
          <Card>
            <CardBody>
              {this._loadingMaterials && <MaterialsForm 
                action={this.state.action}
                actionText={this.state.actionText}
                onSubmit={this.handleSubmit}
                dataForm = {this.state.formdata}
                providers={this.state.clientOptions}
                seasons={this.state.SeasonsOps}
                reset={this.handleReset}
                notification= {this.formNotification}
              />}
            </CardBody>
          </Card>
        </Collapse>
        <Collapse
          isOpen={this.state.openedCollapses.includes("table")}>
          <Card>
            <CardBody>
              {this.Access.materials.view && <MaterialsHistory 
                dataTable={this.state.materials}
                editMaterial={this.handleEditMaterial}
                removeMaterial={this.handelRemoveMaterial}
                itemsPages={5}
              />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
    </>);
  }
}

export default Materials;
