/*eslint-disable array-callback-return*/
import React from "react";
import axios from "axios";
import StylePoResume from "../components/CombosComponents/StylesPoResume";
import NotificationAlert from "react-notification-alert";

import "../assets/css/tracker.css";

import {
  Form,
  Button,
  Row,
  Col,
  FormGroup,
  Label,
  Card,
  CardBody,
  Modal,
  ModalBody,
  ModalHeader,
} from "reactstrap";

import Select from "react-select";
class Tracker extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      showTracker: false,
      combosExist: true,
      resumeModal: false,
      combos: {},
      styleWcombo: [],
      combosList: [],
      clientsList: [],
      seasonList: [],
      comboState: "",
      comboName: "",
      comboCStyle: "",
      comboClient: "",
      comboSeason: "",
      clientId: "",
    };
  }
  userToken = localStorage.getItem("userToken") || "";

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  toggleresumeModal = () => {
    this.setState({
      resumeModal: !this.state.resumeModal
    });
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      // this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  getClients = () => {
    this.componentWillUnmount();
    let cliente = [];
    axios
      .get(
        process.env.REACT_APP_IP + `/clients/`,
        // data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          response.data.map((season) => {
            cliente.push({
              value: season.id,
              label: season.name,
            });
          });
          this.setState({ clientsList: cliente });

          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };
  

  getActiveCombos = () => {
    let listCombo = [];

    axios
      .get(
        process.env.REACT_APP_IP +
          `/combos/combo_status_tracker/?id=${this.state.clientId}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          if (response.data.length === 0) {
            this.toggleLoading();
            this.notify("tr", "warning", "Not found combos!");
            this.setState({
              combosExist: false,
              showTracker: false,
            });
          } else {
            this.toggleLoading();
            this.notify("tr", "success", "Found Combos!");
            this.setState({ combosExist: true });
            this.setState({
              styleWcombo: response.data,
            });
            this.state.styleWcombo.map((style) => {
              listCombo.push({
                label: style.base_style,
                name: style.costing_style,
                value: style.id,
                combo_status: style.approvalStatus,
                client: style.client,
                season: style.costing_season,
              });
            });
            this.setState({
              combosList: listCombo,
            });
          }
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error);
        }.bind(this)
      );
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  render() {
    const { loading } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <>
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="content">
          <div className="react-notification-alert-container">
            <NotificationAlert ref="notificationAlert" />
          </div>
          
          <Modal
          
            isOpen={this.state.resumeModal}
            toggle={this.toggleresumeModal}
            size={"lg"}
            style={{ margin: "1vh auto 0px"}}
          >
            <ModalHeader toggle={this.toggleresumeModal} tag={"h2"}>
              {this.state.setName}
            </ModalHeader>
            <ModalBody>
              <StylePoResume resume={true} />
            </ModalBody>
          </Modal>
          <Card>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Row>
                <Col xs={2}>
                  <h6>Combo Status Tracker</h6>
                  <Col>
                    <Form>
                      <Label>
                        <h4>Search Combos</h4>
                      </Label>
                      <FormGroup>
                        <Select
                          options={this.state.clientsList}
                          placeholder="Select Client..."
                          onChange={(e) => {
                            this.setState({
                              comboClient: e.label,
                              clientId: e.value,
                            });
                          }}
                        />
                        <br />

                        <Button
                          onClick={() => {
                            if (!this.state.comboClient) {
                              this.notify(
                                "tr",
                                "warning",
                                "You should select a client!"
                              );
                            } else {
                              this.setState({
                                showTracker: true,
                                setInTracker: this.state.comboState,
                                setName: this.state.comboName,
                                setCosting: this.state.comboCStyle,
                                setClient: this.state.comboClient,
                                setSeason: this.state.comboSeason,
                              });
                              this.getActiveCombos();
                            }
                          }}
                          color="info"
                          size="sm"
                        >
                          Search Combos <i className="fa fa-search" />
                        </Button>

                        {this.state.showTracker && this.state.combosExist ? (
                          <>
                            <hr />
                            <Label>
                              <h4>Select Combo</h4>
                            </Label>

                            <Select
                              options={this.state.combosList}
                              placeholder="Select Combo..."
                              onChange={(e) => {
                                this.setState({
                                  comboSelected1: e,
                                  comboName: e.label,
                                  comboState: e.combo_status,
                                  comboCStyle: e.name,
                                  comboClient: e.client,
                                  comboSeason: e.season,
                                });
                              }}
                            />

                            <br />
                            <Button
                              onClick={() => {
                                if (!this.state.comboState) {
                                  this.notify(
                                    "tr",
                                    "warning",
                                    "You should select a combo!"
                                  );
                                } else {
                                  this.setState({
                                    setInTracker: this.state.comboState,
                                    setName: this.state.comboName,
                                    setCosting: this.state.comboCStyle,
                                    setClient: this.state.comboClient,
                                    setSeason: this.state.comboSeason,
                                  });
                                }
                              }}
                              color="info"
                              size="sm"
                            >
                              Review Combo Status <i className="fa fa-search" />
                            </Button>
                          </>
                        ) : (
                          <></>
                        )}
                      </FormGroup>
                    </Form>
                  </Col>
                </Col>
              </Row>

              {this.state.setInTracker === "N" && this.state.combosExist ? (
                <>
                  {this.state.combosExist ? (
                    <>
                      <hr />
                      <Row>
                        <Col xs={12}>
                          <Col>
                            <Form>
                              <h4>Combo Details:</h4>
                              <FormGroup>
                                <ul>
                                  <li>
                                    <h5>Combo Style: {this.state.setName}</h5>
                                  </li>
                                  <li>
                                    <h5>Client: {this.state.setClient}</h5>
                                  </li>
                                  <li>
                                    <h5>Season: {this.state.setSeason}</h5>
                                  </li>
                                  <li>
                                    <h5>
                                      Costsheet Style: {this.state.setCosting}
                                    </h5>
                                  </li>
                                </ul>
                              </FormGroup>
                            </Form>
                          </Col>
                        </Col>
                      </Row>
                    </>
                  ) : (
                    <></>
                  )}
                  <div class="card card-timeline px-2 border-none">
                    <ul class="bs4-order-tracking">
                      <li class="step active">
                        <div 
                          class="stepOption"
                          id="poStep"
                          title="Click para revisar las POs que contienen este estilo" 
                          onClick={() => {
                            this.toggleresumeModal();
                          }}>
                          <i class="tim-icons icon-bag-16"></i>
                        </div>
                        <br />
                        CPO Ingresada
                        
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-components"></i>
                        </div>
                        <br />
                        Ingreso de Componentes
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-zoom-split"></i>
                        </div>
                        <br />
                        Aprobacion de Componentes
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-notes"></i>
                        </div>
                        <br />
                        Ingreso de Consumos
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-upload"></i>
                        </div>
                        <br />
                        Aprobacion de Consumos
                      </li>
                    </ul>
                  </div>
                  <hr />
                  <div className="messageCombo">
                    <h4 className="messTxt">
                      El combo {this.state.setName} se encuentra pendiente de
                      ingreso de componentes y envio a aprobacin
                    </h4>
                  </div>
                </>
              ) : this.state.setInTracker === "A" && this.state.combosExist ? (
                <>
                  {this.state.combosExist ? (
                    <>
                      <hr />
                      <Row>
                        <Col xs={12}>
                          <Col>
                            <Form>
                              <h4>Combo Details:</h4>
                              <FormGroup>
                                <ul>
                                  <li>
                                    <h5>Combo Style: {this.state.setName}</h5>
                                  </li>
                                  <li>
                                    <h5>Client: {this.state.setClient}</h5>
                                  </li>
                                  <li>
                                    <h5>Season: {this.state.setSeason}</h5>
                                  </li>
                                  <li>
                                    <h5>
                                      Costsheet Style: {this.state.setCosting}
                                    </h5>
                                  </li>
                                </ul>
                              </FormGroup>
                            </Form>
                          </Col>
                        </Col>
                      </Row>
                    </>
                  ) : (
                    <></>
                  )}
                  <div class="card card-timeline px-2 border-none">
                    <ul class="bs4-order-tracking">
                      <li class="step active">
                        <div 
                          class="stepOption"
                          id="poStep"
                          onClick={() => {
                            this.toggleresumeModal();
                          }}>
                          
                          <i class="tim-icons icon-bag-16"></i>
                        </div>
                        <br />
                        CPO Ingresada
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-components"></i>
                        </div>
                        <br />
                        Ingreso de Componentes
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-zoom-split"></i>
                        </div>
                        <br />
                        Aprobacion de Componentes
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-notes"></i>
                        </div>
                        <br />
                        Ingreso de Consumos
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-upload"></i>
                        </div>
                        <br />
                        Aprobacion de Consumos
                      </li>
                    </ul>
                  </div>
                  <hr />
                  <div className="messageCombo">
                    <h4 className="messTxt">
                      El combo {this.state.setName} se encuentra pendiente de
                      aprobacin de componentes y envio a ingreso de consumos
                    </h4>
                  </div>
                </>
              ) : this.state.setInTracker === "C" && this.state.combosExist ? (
                <>
                  {this.state.combosExist ? (
                    <>
                      <hr />
                      <Row>
                        <Col xs={12}>
                          <Col>
                            <Form>
                              <h4>Combo Details:</h4>
                              <FormGroup>
                                <ul>
                                  <li>
                                    <h5>Combo Style: {this.state.setName}</h5>
                                  </li>
                                  <li>
                                    <h5>Client: {this.state.setClient}</h5>
                                  </li>
                                  <li>
                                    <h5>Season: {this.state.setSeason}</h5>
                                  </li>
                                  <li>
                                    <h5>
                                      Costsheet Style: {this.state.setCosting}
                                    </h5>
                                  </li>
                                </ul>
                              </FormGroup>
                            </Form>
                          </Col>
                        </Col>
                      </Row>
                    </>
                  ) : (
                    <></>
                  )}
                  <div class="card card-timeline px-2 border-none">
                    <ul class="bs4-order-tracking">
                      <li class="step active">
                      <div 
                          class="stepOption"
                          id="poStep"
                          onClick={() => {
                            this.toggleresumeModal();
                          }}>
                          
                          <i class="tim-icons icon-bag-16"></i>
                        </div>
                        <br />
                        CPO Ingresada
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-components"></i>
                        </div>
                        <br />
                        Ingreso de Componentes
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-zoom-split"></i>
                        </div>
                        <br />
                        Aprobacion de Componentes
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-notes"></i>
                        </div>
                        <br />
                        Ingreso de Consumos
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-upload"></i>
                        </div>
                        <br />
                        Aprobacion de Consumos
                      </li>
                    </ul>
                  </div>
                  <hr />
                  <div className="messageCombo">
                    <h4 className="messTxt">
                      El combo {this.state.setName} se encuentra pendiente de
                      ingreso de consumos y envio a aprobacin
                    </h4>
                  </div>
                </>
              ) : this.state.setInTracker === "F" && this.state.combosExist ? (
                <>
                  {this.state.combosExist ? (
                    <>
                      <hr />
                      <Row>
                        <Col xs={12}>
                          <Col>
                            <Form>
                              <h4>Combo Details:</h4>
                              <FormGroup>
                                <ul>
                                  <li>
                                    <h5>Combo Style: {this.state.setName}</h5>
                                  </li>
                                  <li>
                                    <h5>Client: {this.state.setClient}</h5>
                                  </li>
                                  <li>
                                    <h5>Season: {this.state.setSeason}</h5>
                                  </li>
                                  <li>
                                    <h5>
                                      Costsheet Style: {this.state.setCosting}
                                    </h5>
                                  </li>
                                </ul>
                              </FormGroup>
                            </Form>
                          </Col>
                        </Col>
                      </Row>
                    </>
                  ) : (
                    <></>
                  )}
                  <div class="card card-timeline px-2 border-none">
                    <ul class="bs4-order-tracking">
                      <li class="step active">
                      <div 
                          class="stepOption"
                          id="poStep"
                          onClick={() => {
                            this.toggleresumeModal();
                          }}
                          >
                          
                          <i class="tim-icons icon-bag-16"></i>
                        </div>
                        <br />
                        CPO Ingresada
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-components"></i>
                        </div>
                        <br />
                        Ingreso de Componentes
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-zoom-split"></i>
                        </div>
                        <br />
                        Aprobacion de Componentes
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-notes"></i>
                        </div>
                        <br />
                        Ingreso de Consumos
                      </li>
                      <li class="step active">
                        <div class="stepOption">
                          <i class="tim-icons icon-upload"></i>
                        </div>
                        <br />
                        Aprobacion de Consumos
                      </li>
                    </ul>
                  </div>
                  <hr />
                  <div className="messageCombo">
                    <h4 className="messTxt">
                      El combo {this.state.setName} se encuentra pendiente de
                      aprobacin de consumos para ingresar a Sistemas Integrados
                    </h4>
                  </div>
                </>
              ) : (
                <>
                  <hr />
                  <div class="card card-timeline px-2 border-none">
                    <ul class="bs4-order-tracking">
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-bag-16"></i>
                        </div>
                        <br />
                        CPO Ingresada
                        
                      </li>

                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-components"></i>
                        </div>
                        <br />
                        Ingreso de Componentes
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-zoom-split"></i>
                        </div>
                        <br />
                        Aprobacion de Componentes
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-notes"></i>
                        </div>
                        <br />
                        Ingreso de Consumos
                      </li>
                      <li class="step">
                        <div class="stepOption">
                          <i class="tim-icons icon-upload"></i>
                        </div>
                        <br />
                        Aprobacion de Consumos
                      </li>
                    </ul>
                  </div>
                </>
              )}
            </CardBody>
          </Card>
        </div>
      </>
    );
  }
}

export default Tracker;
