import React from "react";
import axios from 'axios';
import { PoPool } from "../components/PoComponents/PoPool";
import { PoInfo } from "../components/PoComponents/PoPool";
import NotificationAlert from "react-notification-alert";
import Select from "react-select";

// reactstrap components
import {
  Row,
  Col,
  Card,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Label,
  Button,
  Input
} from "reactstrap";
//import Input from "react-select/src/components/Input";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: '#e14eca'
  })
}

class PoAccount extends React.Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      productOrder: null,
      POSelected: null,
      styles_bom_for_po: null,
      styles_per_po: [],
      unique_styles_po: [],
      editModal: false,
      editModalStyle: false,
      editModalColor: false,
      editModalPrice: false,
      editModalScale: false,
      editModalPlace: false,
      newModalPlace: false,
      styleHasCHange: false,
      style_costing: [],
      days: [],
      months: [],
      year: [],
      styles: [],
      styles1:[], 
      colors: []
    }
  }
  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  toggleEditModal = () => {
    this.setState({
      editModal: !this.state.editModal
    });
  }

  toggleEditModalStyle = () => {
    this.setState({
      editModalStyle: !this.state.editModalStyle
    });
  }

  toggleEditModalColor = () => {
    this.setState({
      editModalColor: !this.state.editModalColor
    });
  }

  toggleEditModalPrice = () => {
    this.setState({
      editModalPrice: !this.state.editModalPrice
    });
  }

  toggleEditModalScale = () => {
    this.setState({
      editModalScale: !this.state.editModalScale
    });
  }

  toggleEditModalPlace = () => {
    this.setState({
      editModalPlace: !this.state.editModalPlace
    });
  }

  toggleModalNewPlace = () => {
    this.setState({
      newModalPlace: !this.state.newModalPlace
    });
  }

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  _loadingStyles = false;
  getStyles = () => {
    axios.get(
      this.costingURL + '/style/',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        styles1: response.data.map(costsheet => {
          return {
            value: costsheet.id,
            label: `${costsheet.costing}`,
            costing_id: costsheet.costing_id,
            name: costsheet.name,
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'costing History');
    }.bind(this));
  }

  getColors = (styleId) => {
    axios.get(
      this.costingURL + `/color_by_client/`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        colors: response.data.map(color => {
          return {
            value: color.id,
            label: `${color.name}`,
            name: color.name,
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Failed Load Colors');
    }.bind(this));
  }

  getColorsFromStyle = (id) => {
    if (null) {

      this.notify("tr", "warning", "Please select a Style before");

      return;

    }

    else {

      axios.get(

        this.costingURL + `/po/get_colors_for_style_po/?bom_id=${id}`,

        {

          headers: {

            "Authorization": "Token " + this.userToken

          }

        }

      ).then(function (response) {

        //this._loadingStyles = true;

        this.setState({

          //colorsInBom: [],

          colorInBom: response.data.map(color => {

            return {

              value: color.id,

              label: `${color.name}`,

              name: color.name,

            }

          })

        });

        this.toggleEditModalColor();

      }.bind(this)).catch(function (error) {

        this.handleError(error, 'Failed Load Colors');

      }.bind(this));

    }
  }

  
  getAllColors = () => {
    axios.get(
      this.costingURL + '/color_by_client/',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        colorsAll: response.data.map(color => {
          return {
            value: color.id,
            label: `${color.name}`,
            name: color.name,
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Failed Load Colors');
    }.bind(this));
  }

  getStylesFromBOM = (clientId) => {
    axios.get(
      this.costingURL + `/po/get_styles_from_bom/?client_id=${clientId}`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        styles: response.data.map(costsheet => {
          return {
            value: costsheet.id,
            label: `${costsheet.name}`,
            costing_id: costsheet.costing_id,
            name: costsheet.name,
            id_style: costsheet.id_style,
            id: costsheet.id
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Failed Load Colors');
    }.bind(this));
  }

  getColorFromPO = (clientId) => {
    if (null) {

      this.notify("tr", "warning", "Please select a Style before");

      return;

    }

    else {

      axios.get(

        this.costingURL + `/color_by_client/get_colors_by_client/?client=${clientId}`,

        {

          headers: {

            "Authorization": "Token " + this.userToken

          }

        }

      ).then(function (response) {

        this._loadingStyles = true;

        this._isMounted && this.setState({

          colors: response.data.map(color => {

            return {

              value: color.id,

              label: `${color.name}`,

              name: color.name,

            }

          })

        });

        this.toggleEditModalColor();

      }.bind(this)).catch(function (error) {

        this.handleError(error, 'Failed Load Colors');

      }.bind(this));

    }
  }

  getScales = () => {
    axios.get(
      this.costingURL + '/material_scale/',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        scales: response.data.map(scale => {
          return {
            value: scale.id,
            label: `${scale.name} / ${scale.client.name}`,
            name: scale.name,
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Failed Load Sizes Scales');
    }.bind(this));

  }

  getPlaces = () => {
    axios.get(
      this.costingURL + '/destionation/',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadingStyles = true;
      this._isMounted && this.setState({
        places: response.data.map(place => {
          return {
            value: place.id,
            label: `${place.name} / ${place.clients}`,
            name: place.name,
          }
        })
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Failed Load Sizes Scales');
    }.bind(this));
  }

  getPoList = () => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/po/get_my_po/?flag=U`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({ productOrder: response.data })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
    }.bind(this))
  }


  postApprove = () => {
    this.toggleLoading()
    axios.post(
      process.env.REACT_APP_IP + `/po/`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({ productOrder: [response.data] })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
    }.bind(this))
  }

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios.get(
      this.costingURL + '/clients/',
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._clientsLoaded = true;
      this._isMounted && this.setState({
        clients: response.data.map(client => {
          return {
            value: client.id,
            label: client.name,
            abbreviation: client.abbreviation,
          }
        })
      });
      //this.toggleLoading();
    }.bind(this)).catch(function (error) {
      //this.toggleLoading();
      this.handleError(error, 'clients');
    }.bind(this));
  }

  getCallendar = () => {
    let days = [];
    let months = [];
    let years = [];
    for (let step = 1; step < 32; step++) {
      days.push({
        label: step
      })
    }
    for (let step = 1; step < 13; step++) {
      months.push({
        label: step
      })
    }
    for (let step = 2021; step < 2121; step++) {
      years.push({
        label: step
      })
    }
    this.setState({days: days,
      months: months,
      years: years})
  }

  componentDidMount() {
    this._isMounted = true;
    this.getPoList();
    this.getCallendar();
    this.getStyles();
    //this.getColors(); .|.
    this.getScales();
    this.getClients();
    this.getPlaces();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  OpenPO = (POSelected) => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/po/${POSelected}/`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({
        newStyle: null,
        selectedPO: response.data,
        deliveryPlaceInfo: {
          id: `${response.data.real_delivery_place !== "None" ? response.data.real_delivery_place.split(' ', 1) : "None"}`,
          name: `${response.data.real_delivery_place !== "None" ? response.data.real_delivery_place.split(" - ")[0].slice(1, -3) : "None"}`
        }
      })
      this.toggleLoading()
      this.getStylesFromBOM(this.state.selectedPO.client.id)
      this.setState({clientId: this.state.selectedPO.client.id})
      //this.getColorFromPO(this.state.selectedPO.client.id)

    }.bind(this)).catch(function (error) {
      this.toggleLoading();
    }.bind(this))
  }

  approvePo = (id) => {
    //console.log(this.state.selectedPO.details)
    let toapprove = [...this.state.selectedPO.details];
    //console.log('toapprove',toapprove)
    let details = [];
    for (const i in toapprove) {
      //console.log(inputObj2)
      if (toapprove[i].style === null) {
        //console.log('no hay estilo')
        this.notify("tr", "warning", "Please fill all styles");
        return;
      } else {
        if (toapprove[i].color === "None" || toapprove[i].color === null) {
          this.notify("tr", "warning", "Please fill all colors");
          return;
        }
        else {
          if (toapprove[i].exportation_date === null) {
            this.notify("tr", "warning", "Please fill all exportation dates");
            return;
          }
          else {
            if (toapprove[i].scale === "None") {
              this.notify("tr", "warning", "Please fill all scales");
              return;
            }
            else {
              if (this.state.selectedPO.real_delivery_place === "None") {
                this.notify("tr", "warning", "Please fill the Delivery Place");
                return;
              }
              else {
               // console.log('imprimo antes del final', toapprove[i].sizes[0].clients_unit_price)
                let inputObj = {
                  detail_id: toapprove[i].id,
                  style: `${Number.isInteger(toapprove[i].style) ? toapprove[i].style : toapprove[i].id_style}`,
                  color: `${toapprove[i].color.id ? toapprove[i].color.id : toapprove[i].color}`,
                  exportation_date: toapprove[i].exportation_date,
                  unit_price: `${toapprove[i].unit_price ? toapprove[i].unit_price :  toapprove[i].sizes[0].clients_unit_price}`,
                  scale: `${Number.isInteger(toapprove[i].scale) ? toapprove[i].scale : toapprove[i].id_scale}`,
                  //client info
                  clients_color: toapprove[i].clients_color,
                  clients_export_date: toapprove[i].clients_export_date,
                  clients_style: toapprove[i].clients_style,
                }
                details.push(inputObj)
                //console.log('cada for',inputObj)
              }
            }
          }
        }
      }
    }
    const final = {
      po_number: this.state.selectedPO.po_number,
      real_delivery_place: `${Number.isInteger(this.state.selectedPO.real_delivery_place) ? this.state.selectedPO.real_delivery_place : this.state.deliveryPlaceInfo.id}`,
      details: details
    }
    //console.log('DELIVERY PLACE',final)

   axios.post(
      this.costingURL + '/po/approve_po/',
      final,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.notify("tr", "success", "Po Approved!");
      //this.toggleLoading();
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      this.handleError(error, "PO not Approved")
      //this.toggleLoading();
    }.bind(this))
  }

  deletePO = (id) => {
    this.toggleLoading();
    let po = new FormData();
    po.append('id', id);
    po.append('flag', 'D')
    axios.post(
      this.costingURL + '/po/change_status_po/',
      po,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.notify("tr", "success", "Po Deleted!");
      this.toggleLoading();
    }.bind(this)).catch(function (error) {
      this.handleError(error, "PO not Deleted")
      this.toggleLoading();
    }.bind(this))
  }

  createPlace = () => {
    this.toggleLoading();
    if (this.state.newPlacename !== undefined || this.state.newPlaceClient !== undefined
      || this.state.newPlaceMin !== undefined || this.state.newPlaceMax !== undefined
      || this.state.newPlaceCode !== undefined
    ) {
      let newPlace = new FormData();
      newPlace.append('name', this.state.newPlacename);
      newPlace.append('client', this.state.newPlaceClient.value);
      newPlace.append('min', this.state.newPlaceMin);
      newPlace.append('max', this.state.newPlaceMax);
      newPlace.append('clients_code', this.state.newPlaceCode);
      newPlace.append('sales_person', this.state.newPlacePerson ? this.state.newPlacePerson : 'None');
      newPlace.append('commission', this.state.newPlaceCommision ? this.state.newPlaceCommision : '0.0');
      axios.post(
        this.costingURL + '/destionation/',
        newPlace,
        { headers: { "Authorization": "Token " + this.userToken } }
      ).then(function () {
        this.notify("tr", "success", "Place Created!");
        this.toggleLoading();

        this.componentDidMount();
      }.bind(this)).catch(function (error) {
        this.handleError(error, "PO not Deleted")
        this.toggleLoading();
      }.bind(this))

    }
    else {
      this.notify("tr", "warning", "Please fill all before create");
      return;
    }

  }

  editExpDate = () => {
    let po = new FormData();
    po.append('po_id', this.state.POSelected.id);
    //po.append('actual_style_id', this.state.selectedActualStyle.id);
    //year-month-day
    po.append('export_date', `${this.state.selectedYear.label}-${this.state.selectedMonth.label}-${this.state.selectedDay.label}`);
    this.toggleLoading();
    axios.post(
      this.costingURL + '/po/change_export_date_po/',
      po,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.toggleLoading();
      this.notify("tr", "success", "Exportation Date Changed!");
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, "Date not changed")
    }.bind(this))
  }

  editedProcecss = (id) => {
    this.setState({
      editedProcess: id
    })
    //console.log(id)
  }

  getColorsForStyle = () => {
    //console.log(id)
    //const styledata = this.state.selectedPO.details[id].style
    //console.log('styledata', styledata, id)
    if (null) {
      this.notify("tr", "warning", "Please select a Style before");
      return;
    }
    else {
      axios.get(
        this.costingURL + `/color_by_client/`,
        {
          headers: {
            "Authorization": "Token " + this.userToken
          }
        }
      ).then(function (response) {
        this._loadingStyles = true;
        this._isMounted && this.setState({
          colors: response.data.map(color => {
            return {
              value: color.id,
              label: `${color.name}`,
              name: color.name,
            }
          })
        });
        this.toggleEditModalColor();
      }.bind(this)).catch(function (error) {
        this.handleError(error, 'Failed Load Colors');
      }.bind(this));
    }
  }


  editStyle = (Styleid) => {
    let items = [...this.state.selectedPO.details];
    let editableProcess = items[this.state.editedProcess];
    editableProcess.style = Styleid.value
    editableProcess.style_label = Styleid.label

    this.setState({
      selectedPO: {
        ...this.state.selectedPO,
        details: items,
      }

    })
    //console.log('reset state', this.state.selectedPO)
    this.toggleEditModalStyle();
    //this.getColors(Styleid.value)
  }

  editDate = () => {
    let items = [...this.state.selectedPO.details];
    let editableProcess = items[this.state.editedProcess];
    editableProcess.exportation_date = `${this.state.selectedYear.label}-${this.state.selectedMonth.label}-${this.state.selectedDay.label}`
    this.setState({
      selectedPO: {
        ...this.state.selectedPO,
        details: items,
      }

    })
    //console.log('reset state', this.state.selectedPO)
    this.toggleEditModal();
  }

  editColor = (color) => {
    //console.log('color', color)
    let items = [...this.state.selectedPO.details];
    let editableProcess = items[this.state.editedProcess];
    editableProcess.color = color.value
    editableProcess.color_label = color.name
    this.setState({
      selectedPO: {
        ...this.state.selectedPO,
        details: items,
      }

    })
    //console.log('reset state', this.state.selectedPO)
    this.toggleEditModalColor();
  }

  editScale = (scale) => {
    //console.log('scale', scale)
    let items = [...this.state.selectedPO.details];
    let editableProcess = items[this.state.editedProcess];
    editableProcess.scale = scale.value
    editableProcess.scale_label = scale.name
    this.setState({
      selectedPO: {
        ...this.state.selectedPO,
        details: items,
      }

    })
    //console.log('reset state', this.state.selectedPO)
    this.toggleEditModalScale();
  }

  editPrice = (price) => {
    //console.log('price', price)
    let items = [...this.state.selectedPO.details];
    let editableProcess = items[this.state.editedProcess];
    editableProcess.unit_price = this.state.selectedPrice
    this.setState({
      selectedPO: {
        ...this.state.selectedPO,
        details: items,
      }

    })
    //console.log('reset state', this.state.selectedPO)
    this.toggleEditModalPrice();
  }

  editPlace = (place) => {
    //console.log('place', place)
    this.setState({
      selectedPO: {
        ...this.state.selectedPO,
        real_delivery_place: place.value,
        real_delivery_place_label: place.label
      }

    })
    //console.log('reset state', this.state.selectedPO)
    this.toggleEditModalPlace();
  }

  loadColors = (id) => {
    //console.log('el id del proceso', id)
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>" && this._isMounted) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  render() {

    const {
      loading,
      scales,
      styles,
      selectedPO,
      deliveryPlaceInfo,
      productOrder,
      editModal,
      editModalStyle,
      editModalColor,
      editModalPrice,
      editModalScale,
      editModalPlace,
      newModalPlace,
      days,
      months,
      years,
      places,
      clients,
      colorsAll,
    } = this.state;
    console.log('here:',styles)
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content"><div className="ui text loader">Loading</div></div>
            </div>
            <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
          </div>
        </div>
        <Modal
          isOpen={editModal}
          toggle={this.toggleEditModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Edit Exportation Date
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Exportation Date</Label>
                <Row>
                  <Col sm={4}>
                    <Select
                      placeholder="mm"
                      styles={customStyles}
                      options={months}
                      onChange={e => { this.setState({ selectedMonth: e }) }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="dd"
                      styles={customStyles}
                      options={days}
                      onChange={e => { this.setState({ selectedDay: e }) }}
                    />
                  </Col>
                  <Col sm={4}>
                    <Select
                      placeholder="yyyy"
                      styles={customStyles}
                      options={years}
                      onChange={e => { this.setState({ selectedYear: e }) }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleEditModal()}>
              Close
            </Button>
            <Button color="primary" onClick={() => this.editDate()}>
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>

        <Modal
          isOpen={editModalStyle}
          toggle={this.toggleEditModalStyle}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Edit Style PO
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label> Choose Style To Change</Label>
                <Row>
                  <Col sm={12}>
                    
                    {this._loadingStyles ? <Select
                      placeholder="Search Style"
                      styles={customStyles}
                      options={styles}
                      onChange={option => {
                        this.setState({
                          newStyle: option,
                          styleHasCHange: true
                          //...selectedPO,
                          //selectedStyle: option
                        })
                        //console.log(this.state.newStyle)
                      }}
                      //value={this.state.selectedStyle}
                    /> : <div className={"BOMSloader"}></div>}


                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleEditModalStyle}>
              Close
            </Button>
            <Button color="primary" onClick={() => this.editStyle(this.state.newStyle)}>
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={editModalColor}
          toggle={this.toggleEditModalColor}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Edit Color
          </ModalHeader>
          <ModalBody>
            {
              <Row>
                <Col sm={12}>
                  <Label>Select Color</Label>
                  <Row>
                    <Col sm={12}>
                      <Select
                        placeholder="Select Color"
                        styles={customStyles}
                        options={this.state.colorInBom ? this.state.colorInBom : colorsAll}
                        onChange={e => { this.setState({ selectedColor: e }) }}
                      />
                    </Col>

                  </Row>
                </Col>
              </Row>}
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => {
              this.toggleEditModalColor()
              this.setState({
                newStyle: null,
                colorsInBom: [],
              
              })
              }}>
              Close
            </Button>
            <Button color="primary" onClick={() => {
              this.editColor(this.state.selectedColor)
              this.setState({
                newStyle: null,
                colorsInBom: [],
              })
            }
            }>
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={editModalScale}
          toggle={this.toggleEditModalScale}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Edit Scale
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Select Scale</Label>
                <Row>
                  <Col sm={12}>
                    <Select
                      placeholder="Select Scale"
                      styles={customStyles}
                      options={scales}
                      onChange={e => { this.setState({ selectedScale: e }) }}
                    />
                  </Col>

                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleEditModalScale()}>
              Close
            </Button>
            <Button color="primary" onClick={() => this.editScale(this.state.selectedScale)}>
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={editModalPrice}
          toggle={this.toggleEditModalPrice}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Edit Unit Price
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Change Unit Price</Label>
                <Row>
                  <Col sm={12}>
                    <Input
                      type='text'
                      placeholder="Input Unit Price"
                      onChange={e => { this.setState({ selectedPrice: e.target.value }) }}
                    />
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleEditModalPrice()}>
              Close
            </Button>
            <Button color="primary" onClick={() => this.editPrice(this.state.selectedPrice)}>
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={editModalPlace}
          toggle={this.toggleEditModalPlace}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Select Delivery Place
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>Select Delivery Place</Label>
                <Row>
                  <Col sm={10}>
                    <Select
                      placeholder="Select Place"
                      styles={customStyles}
                      options={places}
                      onChange={e => { this.setState({ selectedPlace: e }) }}
                    />
                  </Col>
                  <Col sm={2}>
                    <Button
                      color="primary"
                      size="sm"
                      onClick={() => {
                        this.toggleEditModalPlace()
                        this.toggleModalNewPlace()
                      }}><i class="fas fa-plus"></i></Button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleEditModalPlace()}>
              Close
            </Button>
            <Button color="primary" onClick={() => this.editPlace(this.state.selectedPlace)}>
              Save Changes
            </Button>
          </ModalFooter>
        </Modal>
        <Modal
          isOpen={newModalPlace}
          toggle={this.toggleModalNewPlace}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Create New Delivery Place
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={12}>
                <Label>
                  Create New Delivery Place
                </Label>
                <Row>
                  <Col sm={12}>
                    Place Name
                    <Input
                      type='text'
                      placeholder="Place Name"
                      onChange={e => { this.setState({ newPlacename: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={6}>
                    Client
                    <Select
                      placeholder="Select Client "
                      styles={customStyles}
                      options={clients}
                      onChange={e => { this.setState({ newPlaceClient: e }) }}
                    />
                  </Col>
                  <Col sm={3}>
                    Min
                    <Input
                      type='text'
                      placeholder="Min"
                      onChange={e => { this.setState({ newPlaceMin: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={3}>
                    Max
                    <Input
                      type='text'
                      placeholder="Max"
                      onChange={e => { this.setState({ newPlaceMax: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={6}>
                    Sales Person
                    <Input
                      type='text'
                      placeholder="Sales Person"
                      onChange={e => { this.setState({ newPlacePerson: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={6}>
                    Commission
                    <Input
                      type='text'
                      placeholder="Commission"
                      onChange={e => { this.setState({ newPlaceCommision: e.target.value }) }}
                    />
                  </Col>
                  <Col sm={12}>
                    Client Code
                    <Input
                      type='text'
                      placeholder="Code for the place"
                      onChange={e => { this.setState({ newPlaceCode: e.target.value }) }}
                    />
                  </Col>

                </Row>
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={() => this.toggleModalNewPlace()}>
              Close
            </Button>
            <Button color="primary" onClick={() => {
              this.createPlace()
              this.toggleModalNewPlace()
              this.toggleEditModalPlace()
            }
            }>
              Create Place
            </Button>
          </ModalFooter>
        </Modal>
        <Row className="justify-content-center mt-5">
          <Col sm={3}>
            <Card>
              <PoPool
                poList={productOrder}
                OpenPO={this.OpenPO}
              />
            </Card>
          </Col>
          {/*console.log(this.state.selectedPO)*/}
          <Col sm={9}>
            {/*console.log(this.state.styles_per_po, this.state.unique_styles_po)*/}

            <PoInfo
              selectedPO={selectedPO}
              deliveryPlaceInfo={deliveryPlaceInfo}
              newStyle={this.state}
              //StylesArray={this.state.styles_per_po}
              //styles_in_po={this.state.unique_styles_po}
              approvePo={this.approvePo}
              //deletePO={this.deletePO}
              getColorsForStyle={this.getColorsForStyle}
              getColorFromPO = {this.getColorFromPO}
              getColor = {this.state.colors}
              editedProcecss={this.editedProcecss}
              openModal={this.toggleEditModal}
              getColorsFromStyle = {this.getColorsFromStyle}
              openModalStyle={this.toggleEditModalStyle}
              openModalColor={this.toggleEditModalColor}
              openModalPrice={this.toggleEditModalPrice}
              openModalScale={this.toggleEditModalScale}
              openModalPlace={this.toggleEditModalPlace}
            />

          </Col>
        </Row>
        {/*< PoHistory />*/}
        {/*<PoManual />*/}
      </div>
    );
  }
}
export default PoAccount;