/*eslint-disable array-callback-return*/
import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import StylePoResume from "../components/CombosComponents/StylesPoResume";
import { matchSorter } from "match-sorter";

import {
  Row,
  Col,
  FormGroup,
  Card,
  Form,
  CardBody,
  ModalHeader,
  Label,
  Button,
  Modal,
  ModalBody,
  TabPane,
  TabContent,
} from "reactstrap";

import NotificationAlert from "react-notification-alert";

class activeCombos extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      vTabs: "vt",
      showCombos: false,
      combosExist: true,
      activeCombosList: [],
      clientsList: [],
      resumeModal: false,
      combosList: [],
      comboState: "",
      comboName: "",
      styleWcombo: [],

      clientId: "",
      comboClient: "",
    };
  }

  userToken = localStorage.getItem("userToken") || "";
  

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  toggleresumeModal = () => {
    this.setState({
      resumeModal: !this.state.resumeModal
    });
  }

  getClients = () => {
    this.componentWillUnmount();
    let cliente = [];
    axios
      .get(
        process.env.REACT_APP_IP + `/clients/`,
        // data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          response.data.map((season) => {
            cliente.push({
              value: season.id,
              label: season.name,
            });
          });
          this.setState({ clientsList: cliente });

          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      // this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  getActiveCombosStyles = () => {
    let listCombo = [];
    const { clientId } = this.state;
    axios
      .get(
        process.env.REACT_APP_IP + `/combos/get_active_combos/?id=${clientId}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          if (response.data.length === 0) {
            this.toggleLoading();
            this.notify("tr", "warning", "No Data Available");
            this.setState({
              combosExist: false,
              showCombos: false,
            });
          } else {
            this.toggleLoading();
            this.notify("tr", "sucess", "Found Combos!");
            this.setState({ combosExist: true });
            let styleswCombos = response.data;
            this.setState({
              styleWcombo: styleswCombos.map((style) => {
                return {
                  label: style.name,
                  state: style.state,
                  actions: (
                    <>
                      {style.state === "Combo Created" ? (
                        <>
                          <div className="">
                            <Button
                              onClick={() => {
                                this.toggleresumeModal();
                                this.setState({setName: style.name})
                              }}
                              color="info"
                              size="sm"
                            >
                              POs Review <i className="fa fa-search" />
                            </Button>
                            <br></br>
                          </div>
                        </>
                      ) : (
                        <>
                          <div className="">
                            <Button
                              onClick={() => {
                                this.toggleresumeModal();
                              }}
                              color="info"
                              disabled={true}
                              size="sm"
                            >
                              POs Review <i className="fa fa-search" />
                            </Button>
                            <br></br>
                          </div>
                        </>
                      )}
                    </>
                  ),
                };
              }),
            });
            this.setState({
              combosList: listCombo,
            });
          }
        }.bind(this)
      )
      .catch(
        function(errors) {
          this.handleError(errors);
        }.bind(this)
      );
  };

  downloadCombosReport = () => {
    const { clientId } = this.state;
    axios
      .get(
        process.env.REACT_APP_IP + `/admin/get_active_combos/?id=${clientId}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
              `/admin/get_active_combos/?id=${clientId}`
          );
          this.notify("tr", "success", "Report downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Download failed");
        }.bind(this)
      );
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  render() {
    const { loading } = this.state;

    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Modal

          isOpen={this.state.resumeModal}
          toggle={this.toggleresumeModal}
          size={"lg"}
          style={{ margin: "1vh auto 0px"}}
        >
          <ModalHeader toggle={this.toggleresumeModal} tag={"h2"}>
            {this.state.setName}
          </ModalHeader>
          <ModalBody>
            <StylePoResume
              resume={true}
            />
          </ModalBody>
        </Modal>
        <Card>
          <CardBody>
            <div className="react-notification-alert-container">
              <NotificationAlert ref="notificationAlert" />
            </div>
            <Row>
              <Col xs={2}>
                <h6>Active Combos List</h6>
                <Col>
                  <Form>
                    <Label>
                      <h4>Search Combos</h4>
                    </Label>
                    <FormGroup>
                      <Select
                        options={this.state.clientsList}
                        placeholder="Select Client..."
                        onChange={(e) => {
                          this.setState({
                            comboClient: e.label,
                            comboState: e.state,
                            clientId: e.value,
                          });
                        }}
                      />
                      <br />

                      <Button
                        onClick={() => {
                          if (!this.state.comboClient) {
                            this.notify(
                              "tr",
                              "warning",
                              "You should select a client!"
                            );
                          } else {
                            this.setState({
                              showCombos: true,
                              setInTracker: this.state.comboState,
                              setName: this.state.comboName,
                              setCosting: this.state.comboCStyle,
                              setClient: this.state.comboClient,
                              setSeason: this.state.comboSeason,
                            });
                            this.getActiveCombosStyles();
                          }
                        }}
                        color="info"
                        size="sm"
                      >
                        Search Combos <i className="fa fa-search" />
                      </Button>
                      {this.state.showCombos && this.state.combosExist ? (
                        <>
                          <hr />
                          <Button
                            onClick={() => this.downloadCombosReport()}
                            color="info"
                            className="btn-simple"
                            size="sm"
                          >
                            Download Combos Report{" "}
                            <i className="tim-icons icon-cloud-download-93" />
                          </Button>
                        </>
                      ) : (
                        <></>
                      )}
                    </FormGroup>
                  </Form>
                </Col>
              </Col>
            </Row>

            {this.state.showCombos && this.state.combosExist ? (
              <>
                <TabContent activeTab={this.state.vTabs}>
                  <TabPane tabId="vt">
                    <Row>
                      <Col>
                        <ReactTable
                          responsive
                          data={this.state.styleWcombo}
                          filterable
                          defaultFilterMethod={(filter, row) => {
                            const id = filter.pivotId || filter.id;
                            //console.log(filter,row[id],id);
                            const filterValue =
                              filter.id === "label"
                                ? parseFloat(filter.value)
                                : filter.value.toLowerCase();
                            const filterRow =
                              id === "state" ? row[id] : row[id].toLowerCase();
                            return row[id] !== undefined
                              ? String(filterRow).startsWith(filterValue)
                              : true;
                          }}
                          columns={[
                            {
                              Header: "style list",
                              accessor: "label",
                              filterMethod: (filter, row) =>
                                matchSorter(row, filter.value, {
                                  keys: ["label"],
                                }),
                              filterAll: true,
                              sortable: true,
                              filterable: true,
                            },

                            {
                              Header: "state",
                              accessor: "state",
                              filterMethod: (filter, row) =>
                                matchSorter(row, filter.value, {
                                  keys: ["state"],
                                }),
                              filterAll: true,
                              sortable: true,
                              filterable: true,
                            },

                            {
                              Header: "ACTIONS",
                              accessor: "actions",
                              sortable: false,
                              filterable: false,
                              width: 250,
                            },
                          ]}
                          defaultPageSize={5}
                          showPaginationTop
                          showPaginationBottom={false}
                          defaultSorted={[
                            {
                              id: "codigo",
                              desc: true,
                            },
                          ]}
                          className="-striped -highlight primary-pagination"
                        />
                      </Col>
                    </Row>
                  </TabPane>
                </TabContent>
              </>
            ) : (
              <></>
            )}
          </CardBody>
        </Card>
      </div>
    );
  }
}
export default activeCombos;
