import React from 'react';
import ReactTable from "react-table";
import {
  Button, Col, Label, FormGroup,
} from "reactstrap";

let Data=[];

function ReturnFlagTable({...props}) {
  Data = props.dataTable.map((prop, key) => {
    let notIcon=<b><i className={"tim-icons icon-simple-remove text-danger"} /></b>;
    let buttonSAM=notIcon,buttonMarker=notIcon,buttonPattern=notIcon,buttonDelete=notIcon,buttonUser=notIcon;
    if(prop.samFlag){
      buttonSAM= <Button
        color = "info"
        size = "sm"
        type= "button"
        onClick={(e)=>{
          e.preventDefault();
          props.returnFlag("sam",prop.id)
        }}
      >
        <i className={"tim-icons icon-double-left text-success"} /> Restart
      </Button>
    }
    if(prop.markerFlag){
      buttonMarker= <Button
        color = "info"
        size = "sm"
        type= "button"
        onClick={(e)=>{
          e.preventDefault();
          props.returnFlag("consumption",prop.id)
        }}
      >
        <i className={"tim-icons icon-double-left text-success"} /> Restart
      </Button>
    }
    if(prop.patternFlag){
      buttonPattern= <Button
        color = "info"
        size = "sm"
        type= "button"
        onClick={(e)=>{
          e.preventDefault();
          props.returnFlag("pattern",prop.id)
        }}
      >
        <i className={"tim-icons icon-double-left text-success"} /> Restart
      </Button>
    }
    if(prop.deletedFlag){
      buttonDelete= <Button
        color = "info"
        size = "sm"
        type= "button"
        onClick={(e)=>{
          e.preventDefault();
          props.returnFlag("delete",prop.id)
        }}
      >
        <i className={"tim-icons icon-double-left text-success"} /> Restart
      </Button>
    }
    buttonUser= <Button
      color = "warning"
      size = "sm"
      type= "button"
      onClick={(e)=>{
        e.preventDefault();
        props.returnFlag("user",prop.id)
      }}
    >
      <i className={"tim-icons icon-double-left text-success"} /> Release
    </Button>
    return {
      row: key,
      header_id: prop.id,
      detail_id: prop.detail_id,
      client: prop.client.name,
      season: prop.season.name,
      style: prop.style,
      flags: (<>
        <FormGroup row>
          <Label xs={3}>
            <b>SAM:</b>
          </Label>
          <Col xs={9}>
            {buttonSAM}
          </Col>
        </FormGroup>
        <FormGroup row>
          <Label xs={3}>
            <b>Marker:</b>
          </Label>
          <Col xs={7}>
            {buttonMarker}
          </Col>
        </FormGroup>
        <FormGroup row>
          <Label xs={3}>
            <b>Pattern:</b>
          </Label>
          <Col xs={7}>
            {buttonPattern}
          </Col>
        </FormGroup>
        <FormGroup row>
          <Label xs={3}>
            <b>Restore:</b>
          </Label>
          <Col xs={7}>
            {buttonDelete}
          </Col>
        </FormGroup>
        {prop.updating_user!==null?
        <FormGroup row>
          <Label xs={3}>
            <b>User:</b>
          </Label>
          <Col xs={7}>
            {buttonUser}
          </Col>
        </FormGroup>:<></>}
      </>)
    };
  });
  return (<>        
    <h4><b>{props.tableTitle}</b></h4>
    <ReactTable
      data = {Data}
      filterable
      columns = {[
        {
          Header: "Code",
          accessor: "header_id",
          width: 110
        },
        {
          Header: "Client",
          accessor: "client"
        },
        {
          Header: "Season",
          accessor: "season"
        },
        {
          Header: "Style",
          accessor: "style"
        },
        {
          Header: "Flags",
          accessor: "flags",
          sortable: false,
          filterable: false
        }
      ]}
      defaultPageSize={props.itemsPages}
      showPaginationTop
      showPaginationBottom={false}
      defaultSorted = {[
        {
          id: "header_id",
          desc: true
        }
      ]}
      className="-striped -highlight primary-pagination"
  />
  </>);
}

export default ReturnFlagTable;