import React from "react";
import NotificationAlert from "react-notification-alert";
import {
  NavLink,
  TabContent, 
  TabPane,
  Nav,
  NavItem,
  Card,
  Modal,
  ModalBody,
  ModalFooter,
  Button,
} from "reactstrap";
import axios from "axios";
import CostSheetTable from "../components/AdminPanelComponents/CostSheetTable.jsx";
import CostSheetResume from "../components/AdminPanelComponents/CostSheetResume.jsx";
import CostingCalculations from "../helpers/CostingCalculations.js";

class FinalApproval extends React.Component {
  Access = JSON.parse(localStorage.getItem('permissions') || '{}');
  _isMounted = false;
  _toAproved = false;
  userToken = localStorage.getItem('userToken') || '';

  constructor(props){
    super(props);
    this.state = {
      toAprovedataTable: null,
      vTabs: "vt1",
      ApprovalModal: false,
      selectedHeader: null,
      detailCalculation: {},
      dataKey: null
    };
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    if(pageMode !== true){
      return 'modal-black';
    }else{
      return 'modal-white';
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    //console.log(response,message,task);
    if (response.status!==undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      }else{
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  componentDidMount(){
    this._isMounted = true;
    this.toAproved();
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  toAproved = () => {
    axios.get(
      process.env.REACT_APP_IP  + '/administration/get_jj/',
      {headers:{"Authorization":"Token " + this.userToken}}
    ).then( function (response) {      
      let newData=response.data.map(data=>{
        return {
          "header": data.header,
          "detail": data.detail,
          "Calc": CostingCalculations(data.detail)
        }
      });
      this._toAproved=true;
      this._isMounted && this.setState({toAprovedataTable: newData});
    }.bind(this)).catch(function(error){
      this.handleError(error,"toAproved");
    }.bind(this));
  }

  handleApprove = () => {
    let data = new FormData();
    let {detail,header}=this.state.toAprovedataTable[this.state.dataKey];
    data.append('flag',3);//D
    data.append('id',header.id);
    data.append('pw',parseFloat(detail.profitWhite));
    data.append('pm',parseFloat(detail.profitMedium));
    data.append('pd',parseFloat(detail.profitDark));
    data.append('ps',parseFloat(detail.profitSpecial));
    axios.post(
      process.env.REACT_APP_IP  + '/administration/change_approve_status/',
      data,
      {headers: {"Authorization": "Token " + this.userToken} }
    ).then(function(){
      this.toggleApprovalModal();
      this.notify("tr", "success", "CostSheet Approved!");
      this.componentDidMount();
    }.bind(this)).catch(function(error){
      this.handleError(error,"handleApprove");
    }.bind(this));
  }

  handlePopUpApprove = (key) => {
    this.toggleApprovalModal();
    this.setState({
      detailCalculation: this.state.toAprovedataTable[key].Calc,
      dataKey: key
    });
  }

  handleReject = (header_id) => {
    axios.patch(
      process.env.REACT_APP_IP + '/costingHeader/' + header_id + '/',
      {"approvalStatus":"A"},
      {headers: {"Authorization": "Token " + this.userToken} }
    ).then(function(){
      this.notify("tr", "success", "CostSheet Returned!");
      this.componentDidMount();
    }.bind(this)).catch(function(error){
      this.handleError(error,"handleReject");
    }.bind(this));
  }

  toggleApprovalModal = () => {
    this.setState({
      ApprovalModal: !this.state.ApprovalModal
    });
  }

  handleCostSheetCal = (data) => {
    let {toAprovedataTable} = this.state;
    toAprovedataTable[this.state.dataKey]['detail'][data.key]=data.value;
    toAprovedataTable[this.state.dataKey]['Calc'] = CostingCalculations(
      toAprovedataTable[this.state.dataKey]['detail']
    );
    this.setState({
      toAprovedataTable: toAprovedataTable,
      detailCalculation: toAprovedataTable[this.state.dataKey].Calc
    })
  }

  render(){
    return (<div className="content" >
      
      <Modal isOpen={this.state.ApprovalModal}
        toggle={this.toggleApprovalModal}
        size={'lg'}
      >
        <div className="modal-header">
          <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={this.toggleApprovalModal}>
            <i className="tim-icons icon-simple-remove"></i>
          </button>
          <h5 className="modal-title">FINAL APPROVATION</h5>
        </div>
        <ModalBody>
          <CostSheetResume 
            detailCalculation={this.state.detailCalculation} 
            helper={this.handleCostSheetCal}
          />
        </ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={this.toggleApprovalModal}>
            Close
          </Button>
          <Button color="primary" onClick={event=>{
            event.preventDefault();
            this.handleApprove();
          }}>
            Send to Approve
          </Button>
        </ModalFooter>
      </Modal>

      <div className="react-notification-alert-container">
        <NotificationAlert ref="notificationAlert"/>
      </div>

      <Card>
        <Nav pills className="nav-pills-info nav-pills-icons">
          <NavItem>
            <NavLink
              className={this.state.vTabs === "vt1" ? "active":""}
              onClick={()=>this.setState({vTabs: "vt1"})}
            >
              Pending Approval
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={this.state.vTabs}>
          <TabPane tabId="vt1">
            {this._toAproved && <CostSheetTable 
              dataTable={this.state.toAprovedataTable}
              itemsPages={5}
              onApprove={this.handlePopUpApprove}
              onReject={this.handleReject}
            />}
          </TabPane>
        </TabContent>
      </Card>
      
    </div>);
  }
}

export default FinalApproval;