/*eslint-disable array-callback-return*/
import React from "react";
import axios from 'axios';
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import {
  Form,
  Button,
  Row,
  Col,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
} from "reactstrap";
import Select from "react-select";
import { matchSorter } from 'match-sorter';


//import FinalApproval from "./FinalApproval";

export default class FabricsColors extends React.Component {
  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;
  _ClientsLoaded = false;
  _UserLoaded = false;
  defaultColorForm = {
    client: null,
    name: '',
    lab_dip: ''
  }

  constructor() {
    super();
    this.state = {
      loading: false,
      pathToTechpack: undefined,
      clients: [],
      client: {},
      users: [],
      user: {},
      selectedUser: null,
      selectedClients: [],
      selectCompany: {},
      colorsClasses: [],
      selectedClient: [],
      clientsOptions: [],
      ColorsForm: this.defaultColorForm,
      loadingColors: false,
      colors: [],
    }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>" && this._isMounted) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  RemoveColor = (id) => {
    //console.log(id)
    this.setState({
      loadingColors: true,
    })
    axios.delete(
      process.env.REACT_APP_IP + `/colors_fabrics/${id}/`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function () {
      this.setState({
        loadingColors: false,
      })
      this.componentDidMount();
      this.notify("tr", "success", "Fabric Color Removed!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'RemoveColor');
      this.setState({
        loadingColors: false,
      });
      this.componentDidMount();
    }.bind(this))
  }

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  handleSelectChange = (event) => {
    let opts = [], opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
      }
    }
    this.setState({ selectedClients: opts });
  };

  handleUserSubmit = (event) => {
    event.preventDefault();
    this.updateUser(this.state.selectedUser.value, { "clients": this.state.selectedClients })
  }

  _loadedColorClass = false
  loadColorClass = () => {
    axios.get(
      process.env.REACT_APP_IP + '/color_by_client/get_color_class/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadedColorClass = true;
      this.setState({
        colorsClasses: response.data.map(color => ({
          value: color.id,
          label: color.name
        }))
      })
    }.bind(this))
  }

  loadFormColor = (color) => {
    this.setState({
      ColorsForm: color,
    })
  }

  _loadedColorClass = false
  loadColorClass = () => {
    axios.get(
      process.env.REACT_APP_IP + '/color_by_client/get_color_class/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadedColorClass = true;
      this.setState({
        colorsClasses: response.data.map(color => ({
          value: color.id,
          label: color.name
        }))
      })
    }.bind(this))
  }

  //este devuelve todos
  getClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/get_all_clients/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._ClientsLoaded = true;
      this._isMounted && this.setState({
        clients: response.data,
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getClients');
    }.bind(this));
  }

  createColor = (e) => {
    e.preventDefault();
    const {
      ColorsForm
    } = this.state;
    if (ColorsForm.name.length > 25) {
      this.notify("tr", "warning", "name cannot be more than 25 characters");
      return;
    }
    this.setState({
      loadingColors: true,
    })
    axios.post(
      process.env.REACT_APP_IP + '/colors_fabrics/',
      ColorsForm,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
          "Content-Type": "application/json"
        }
      }
    ).then(function () {
      this.setState({
        loadingColors: false,
      })
      this.componentDidMount();
      this.loadFormColor(this.defaultColorForm);
      console.log(this.defaultColorForm)
      this.notify("tr", "success", "Color created!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'createColor');
      this.setState({
        loadingColors: false,
      });
      this.loadFormColor(this.defaultColorForm);
      console.log(this.defaultColorForm)

      //this.componentDidMount();
    }.bind(this))
  }

  editColor = (e) => {
    e.preventDefault();
    const {
      ColorsForm
    } = this.state;
    if (ColorsForm.name.length > 25) {
      this.notify("tr", "warning", "name cannot be more than 25 characters");
      return;
    }
    this.setState({
      loadingColors: true,
    });
    const { id, ...updateData } = ColorsForm;
    axios.patch(
      process.env.REACT_APP_IP + `/color_by_client/${ColorsForm.id}/`,
      updateData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
          "Content-Type": "application/json"
        }
      }
    ).then(function () {
      this.setState({
        loadingColors: false,
      })
      this.componentDidMount();
      this.loadFormColor(this.defaultColorForm);
      this.notify("tr", "success", "Color edited!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'editColor');
      this.setState({
        loadingColors: false,
      });
      this.componentDidMount();
    }.bind(this))
  }

  _loadedColors = false
  loadColors = () => {
    axios.get(
      process.env.REACT_APP_IP + '/colors_fabrics/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadedColors = true;
      this.setState({
        colors: response.data
      })
    }.bind(this));
  }

  _userClientsLoaded = false;
  getUserClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      //console.log('clientesxPeople',response);
      this._userClientsLoaded = true;
      this._isMounted && this.setState({
        clientsOptions: response.data.map(client => {
          return {
            value: client.id,
            label: `${client.company} / ${client.name} / ${client.abbreviation}`
          }
        })
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'getUserClients');
    }.bind(this));
  }

  componentDidMount() {
    this.getClients();
    this.getUserClients();
    this.loadColorClass();
    this.loadColors();
    this._isMounted = true;
  }

  render() {
    const selectedClient = this.state.clientsOptions.find(
      o => o.value === this.state.ColorsForm.client
    )

    /*const {

    } = this.props;*/

    return (
      <div className="content" >
        <Card>

          <CardBody>
            <div className="react-notification-alert-container">
              <NotificationAlert ref="notificationAlert" />
            </div>
            <Row>
              <Col xs={4}>
                <h3>Create Fabric Color</h3>
                <Form onSubmit={e => {
                  if (this.state.ColorsForm.id) {
                    this.editColor(e)
                  } else {
                    this.createColor(e)
                  }
                }}>
                  <FormGroup>
                    <Label>Cliente</Label>
                    {this._ClientsLoaded &&
                      <Select
                        options={this.state.clientsOptions}
                        value={selectedClient ? selectedClient : null}
                        onChange={o => {
                          this.loadFormColor({
                            ...this.state.ColorsForm,
                            client: o.value
                          })
                        }}
                      />}
                  </FormGroup>
                  <FormGroup>
                    <Label>Color Name</Label>
                    <Input type="text"
                      value={this.state.ColorsForm.name}
                      onChange={e => {
                        this.loadFormColor({
                          ...this.state.ColorsForm,
                          name: e.target.value,
                        })
                      }}
                    />
                  </FormGroup>

                  <FormGroup>
                    <Label>Lab Dip</Label>
                    <Input type="text"
                      value={this.state.ColorsForm.lab_dip}
                      onChange={e => {
                        this.loadFormColor({
                          ...this.state.ColorsForm,
                          lab_dip: e.target.value,
                        })
                      }}
                    />
                  </FormGroup>
                 
                  <div className="clearfix">
                    {this.state.ColorsForm.id &&
                      <Button type="button"
                        size="sm"
                        color="secundary"
                        disabled={this.state.loadingColors}
                        className="float-left"
                        onClick={() => this.loadFormColor(this.defaultColorForm)}
                      >
                        Clear
                      </Button>}
                    <Button type="submit" color="primary"
                      size="sm"
                      disabled={this.state.loadingColors}
                      className="float-left"
                    >
                      {this.state.ColorsForm.id ? 'Update Color' : 'Create Fabric Color'}
                    </Button>
                    {this.state.loadingColors && <div className={"BOMSloader float-left"}></div>}
                  </div>
                </Form>
              </Col>
              <Col xs={8}>
                <h3>Color List</h3>
                {this._loadedColors && <ReactTable
                  data={this.state.colors.map(color => {
                    return {
                      ...color,
                      name: color.color,
                      client: color.color,
                      actions: (<div className="clearfix">
                        <Button size="sm"
                          color="danger"
                          className="float-right"
                          onClick={() => {
                            //console.log(color.id)
                            this.RemoveColor(color.id)
                          }}
                        >
                          <i className="tim-icons icon-simple-remove" />
                        </Button>
                        <Button size="sm"
                          color="info"
                          className="float-right"
                          onClick={() => {
                            //console.log(color)
                            this.loadFormColor({
                              id: color.id,
                              //color_class: color.color_class.id,
                              //client: color.client.id,
                              name: color.color,
                              lab_dip: color.lab_dip,
                            })
                          }}
                        >
                          <i className="tim-icons icon-pencil" />
                        </Button>
                      </div>)
                    }
                  })}
                  filterable
                  defaultPageSize={5}
                  columns={[
                    {
                      Header: "Client",
                      accessor: "client",
                      sortable: true,
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["client"] }),
                      filterAll: true,
                    },
                    {
                      Header: "Name",
                      accessor: "name",
                      sortable: true,
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["name"] }),
                      filterAll: true,
                    },

                    {
                      Header: "Actions",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 200
                    }
                  ]}
                />}
              </Col>
            </Row>
          </CardBody>
        </Card>
      </div>
    )
  }
}
