import React from "react";
import axios from 'axios';
import NotificationAlert from "react-notification-alert";
import Select from "react-select";
import style from '../components/PoComponents/PoPool.module.css';

import {
  Row,
  Col,
  Card,
  Button,
  Input,
  Table,
  CardBody
} from "reactstrap";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: '#e14eca'
  })
}

class PoExtraction extends React.Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      rejectDetail: [],
    }
  }

  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;

  componentDidMount() {
    this._isMounted = true;
    this.getPoList();
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>" && this._isMounted) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  getPoList = () => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/po/get_my_po/?flag=I`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({
        productOrder: response.data.map((pos, idx) => {
          return {
            value: pos.id,
            label: pos.po_number,
            delivery_place: pos.delivery_place,
            id: idx

          }
        })
      })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
    }.bind(this))
  }

  selectedPO = (poId) => {
    (function () {
      this.setState({
        selectedPO: poId
      })
    }).bind(this)
  }

  getSelectedPo = () => {
    this.toggleLoading()
    let poId = this.state.consultedPO
    let poData = new FormData();
    poData.append("po", poId.value);
    axios.post(
      process.env.REACT_APP_IP + `/po/validate_cuts_for_po/`,
      poData,
      {headers: { "Authorization": "Token " + this.userToken }},
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({ selectedPO: response.data.data })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleReject");
    }.bind(this))
  }

  processToReject = (id) => {
    var inlist = this.state.rejectDetail.includes(id)
    if (inlist) {
      this.state.rejectDetail.indexOf(id)
      this.state.rejectDetail.splice(this.state.rejectDetail.indexOf(id))
    }
    else {
      this.state.rejectDetail.push(id)
    }
  }

  rejectDetail = () => {
    this.toggleLoading();
    console.log(this.state.rejectDetail);
    var detail_rejected = this.state.rejectDetail.toString();
    let data = new FormData();
    data.append('po', this.state.selectedPO.id);
    data.append('details', detail_rejected);
    axios.post(
      process.env.REACT_APP_IP + '/po/extraction_with_no_cut/',
      data,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this._isMounted && this.notify("tr", "success", "Extraction Done!");
      this.setState({selectedPO: undefined,
         consultedPO: undefined  })
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleReject");
    }.bind(this));
  }

  _selectedPO = undefined;

  render() {
    const {
      loading,
    } = this.props;


    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content"><div className="ui text loader">Loading</div></div>
            </div>
            <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
          </div>
        </div>
        <Row className="justify-content-center mt-5">
          <Col sm={7}>
            <Card>
              <CardBody>
                <Row>
                  <Col sm={10}>
                    <Select
                      placeholder="Select PO"
                      styles={customStyles}
                      options={this.state.productOrder}
                      onChange={(e) => { this.setState({ consultedPO: e }) }}
                    />
                  </Col>
                  <Col sm={1}>
                    <Button
                      color="primary"
                      size={'sm'}
                      onClick={() => {
                        this.getSelectedPo();
                      }}><i className="tim-icons icon-zoom-split" /></Button>
                  </Col>
                  {this.state.selectedPO && <Col sm={1}>
                    <Button
                      color="danger"
                      size={'sm'}
                      onClick={() => {
                        this.rejectDetail();
                      }}>X</Button>
                  </Col>}
                </Row>
                {console.log(this.state.selectedPO)}
                {this.state.selectedPO &&
                  <>
                    {this.state.selectedPO.details.map((detail => (
                      <div className={`${style.borderedSmall}`}>
                        <Row>
                          <Col sm={1}>
                            <div className={style.checkbox}>
                              <Input
                                type={"checkbox"}
                                className={style.checkitem}
                                value={detail.value}
                                onChange={e => {
                                  this.processToReject(detail.id)
                                }}
                              />
                            </div>
                          </Col>
                         
                          <Col sm={10}>
                            {detail.style.name} - {detail.color.name}
                            <br />
                            <Table>
                              <tbody>
                                <tr>
                                  {detail.sizes.map((size) => {
                                    return (
                                      <td>
                                        {size.clients_size}
                                        <br />
                                        {size.quantity}
                                      </td>
                                    )
                                  })}
                                </tr>
                              </tbody>
                            </Table>
                          </Col>
                        </Row>
                      </div>
                    )))
                    }</>}
              </CardBody>
            </Card>
          </Col>
          <Col sm={5}>
            
          </Col>
        </Row>
      </div>
    )
  }
}

export default PoExtraction;