import React from 'react';
import ReactTable from "react-table";
import {
  Button,
} from "reactstrap";

let Data=[];

function CostSheetTable({...props}) {
  Data = props.dataTable.map((prop, key) => {
    return {
      row: key,
      header_id: prop.id,
      detail_id: prop.detail_id,
      client: prop.client.name,
      season: prop.season.name,
      style: prop.style,
      flags: (<>
        <p>
          SAM: <b>{
            prop.samFlag?
            <i className={"tim-icons icon-check-2 text-success"} />:
            <i className={"tim-icons icon-simple-remove text-danger"} />
          }</b>
        </p>
        <p>
          Marker: <b>{
            prop.markerFlag?
            <i className={"tim-icons icon-check-2 text-success"} />:
            <i className={"tim-icons icon-simple-remove text-danger"} />
          }</b>
        </p>
        <p>
          Pattern: <b>{
            prop.patternFlag?
            <i className={"tim-icons icon-check-2 text-success"} />:
            <i className={"tim-icons icon-simple-remove text-danger"} />
          }</b>
        </p>
      </>),
      actions: (
        <div className="" >
          <Button
            onClick={() => props.onApprove(prop.id)}
            color = "info"
            size = "sm"
          >
            Approve <i className = "fa fa-edit" />
          </Button>
          <hr/>
          <Button
            onClick={() => props.onReject(prop.id)}
            color = "danger"
            size = "sm"
          >
            Reject <i className={"fa fa-times"} />
          </Button>
          <hr/>
          <Button
            onClick={() => props.Resume(prop)}
            color = "danger"
            size = "sm"
          >
            Resume <i className={"fa fa-search"} />
          </Button>
        </div>
      )
    };
  });
  return (<>        
    <h4><b>{props.tableTitle}</b></h4>
    <ReactTable
      data = {Data}
      filterable
      columns = {[
        {
          Header: "Code",
          accessor: "header_id",
          width: 110
        },
        {
          Header: "Client",
          accessor: "client"
        },
        {
          Header: "Season",
          accessor: "season"
        },
        {
          Header: "Style",
          accessor: "style"
        },
        {
          Header: "State",
          accessor: "flags",
          sortable: false,
          filterable: false
        },
        {
          Header: "Aprove",
          accessor: "actions",
          sortable: false,
          filterable: false,
          width: 150
        }
      ]}
      defaultPageSize={props.itemsPages}
      showPaginationTop
      showPaginationBottom={false}
      defaultSorted = {[
        {
          id: "header_id",
          desc: true
        }
      ]}
      className="-striped -highlight primary-pagination"
  />
  </>);
}

export default CostSheetTable;