import React from "react";
import ReactTable from 'react-table';
import axios from 'axios';
import Select from "react-select";
import { matchSorter } from 'match-sorter';

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Label,
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  TabPane,
  TabContent,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";  
  class costingColorsC extends React.Component {
    constructor() {
      super()
      this.state = {
        vTabs: "vt",
        loading: false,
        styleOptions: [],
      styleOptionsCombo: [],
      style: {},
      styleComboSelected: null,
      stylesCombo: [],
      stylesStyle: [],
      styleSelected: null,
      costing_styles: [],
      colorSelected: [],
      costingIdSelected: [],
      description: [],
      clients: {},
      colors: [],
      colores_id: [],
      colorsSelect:[],
      modalAddColor: false,
      styles_po: [],
      costingSketch: null,
      techpack: null,
      combos: [],
      colorFabric: [],
      colorFabs: [],
      component_name: null,
      selectedStyle: [],
      costing_id: null,
      validate: false,
      styleWcombo: [],
      DMselect: null,
      ColorSelect: null,
      flag: '',
      clientSelect: null
      
    }
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };


  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }


  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  
  colorSelector = () =>{
    const colores = []
    /*eslint-disable array-callback-return*/
    this.state.colorFabric.map((val, idx) => { 
        colores[idx] = {id: val.id, color: val.color}
  })
    this.setState({colorFabs:colores})
    console.log(colores)
    return colores
  }

  toggleAddColor = () => {
    this.setState({
      modalAddColor: !this.state.modalAddColor,
    });
  };

  _loadedColors = false;
  getColors = (styleId) => {
    let colores = []
    let id_colores = []
    axios.get(
      this.costingURL + `/color_by_client/`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._loadedColors = true;
      this._clientsLoaded = true;
      let colors = response.data;
      colors.map((col)=>{
            colores.push({
                value:col.id,
                label:col.name,
            });
            id_colores.push(col.id)
      })
        this.setState({
            colors: colores,
        });
        this.colorSelector();
        console.log("el estado:" , this.state.colorFabric);
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Failed Load Colors');
    }.bind(this));
  }

  setCostingId(id) {
    this.setState({costing_id: id});
  }


  getCostingHeaders = () => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/costingHeader/get_costings_without_productions_colors/`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      let costings = response.data
      this.setState({
        costing_styles: costings.map((cost) => {
            return {
                id: cost.id,
                costingStyle : cost.costing_style,
                actions:(
                    <>
                    <div className="">
                      <Button
                        onClick={() => {
                            this.toggleAddColor()
                            this.setCostingId(cost.id)
                        
                        }}
                        color="info"
                        size="sm"
                      >
                        Add <i className="fa fa-edit" />
                      </Button>
                      <br></br>
                      
                    </div>
                  </>
                ),
            }
        })
      })
       this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'error Loading');
    }.bind(this))

  }

  _clientsLoaded = false;
  getProdColors = (costing_id, color_id) => {
    this.toggleLoading();
    const formData = new FormData();
    formData.append("costing_id", this.state.costing_id);
    formData.append("colors", this.state.colores_id.join(','));
    console.log(formData);
    axios.post(
        this.costingURL + `/costingHeader/update_production_colors/`,
        formData,
        {headers: {
            "Authorization": "Token " + this.userToken,
            "Content-Type": "multipart/form-data"
        }
    }).then(function (response) {
        this.toggleLoading();
        this.componentDidMount();
        this._clientsLoaded = true;
        this._isMounted &&
            this.setState({
                colorSelected: response.data.color_id,
                costingIdSelected : response.data.costing_id
            })
    }.bind(this)).catch(function (error) {
        this.toggleLoading();
        this.handleError(error, 'clients');
    }.bind(this));    
}

  handleSelectChange = (event) => {
    let opts = [], opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
        console.log('opt', opt.value)
      }
    }

    this.setState({ selectedStyle: opts });
  };


  componentDidMount() {
    this._isMounted = true;
    //this.getColorFabric()
    this.getCostingHeaders()
    this.getColors();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }
  render() {
    
    const {
      loading,
    } = this.state;
    
    const pageMode = this.checkPageMode();

    
   
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (
      <div className="content">
        <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content"><div className="ui text loader">Loading</div></div>
            </div>
            <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Modal
            isOpen={this.state.modalAddColor}
            toggle={this.toggleAddColor}
            modalClassName={this.checkPageMode().mc}
            >
            <ModalBody>
                <FormGroup row>
                <Label sm={6}>Colors</Label>
                
                </FormGroup>
                <FormGroup row>
                <Label sm={3}>Select Colors</Label>
                <Col sm={9}>
                    
                    <Select
                        options={this.state.colors}
                        onChange={(e) => {
                            this.setState({colorsSelect1: e})
                        }}
                        
                    ></Select>
                </Col>
                
                </FormGroup>
                <Row>
                    <Col sm={12}>
                        <Row>
                            <Col sm={12}>
                                Colores
                            </Col>
                        </Row>
                        {this.state.colorsSelect.map((col) =>{
                            return(
                                <>
                                <Row>
                                    <Col sm={12}>
                                        <Label>
                                            {col.label}
                                        </Label>
                                    </Col>
                                </Row>
                                </>
                            )
                        })}
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={()=>{

                    this.toggleAddColor()
                    this.setState({
                        colorsSelect: [],
                        colorsSelect1: [],
                        colores_id:[],
                        costing_id:null
                    });
                }}>
                    Close
                </Button>
                <Button
                    color="primary"
                    onClick={() => {
                        this.setState({
                            colorsSelect: [...this.state.colorsSelect, this.state.colorsSelect1],
                            colores_id: [...this.state.colores_id, this.state.colorsSelect1.value]
                        });
                    }}
                    >
                    ADD
                </Button>
                <Button
                    color="primary"
                    onClick={() => {
                        this.setState({
                            colorsSelect: [],
                            colores_id: []
                        });
                    }}
                    >
                    CLEAR
                </Button>
                <Button
                    color="success"
                    onClick={() => {
                        this.getProdColors();
                        this.setState({
                            colorsSelect: [],
                            colorsSelect1: [],
                            colores_id:[],
                            costing_id:null
                        });
                    }}
                    >
                    SAVE
                </Button>
            </ModalFooter>
        </Modal>
        <Nav pills className="nav-pills-info nav-pills-icons">
          <NavItem>
            <NavLink
            >
              Costing Colors
            </NavLink>
          </NavItem>
          <><br /></>
        </Nav>

        <TabContent activeTab={this.state.vTabs}>
          <TabPane tabId="vt">
            <Row>
              <Col>
                <ReactTable responsive
                  data={this.state.costing_styles}
                  filterable
                  defaultFilterMethod={(filter, row,) => {
                    const id = filter.pivotId || filter.id
                    //console.log(filter,row[id],id);
                    const filterValue = filter.id === "costingStyle" ? parseFloat(filter.value) : filter.value.toLowerCase();
                    const filterRow = id === "costing_id" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
                  }}
                  columns={[
                    {
                      Header: "style list",
                      accessor: "costingStyle",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["costingStyle"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                     
                    },
                    
                    {
                      Header: "ACTIONS",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 150,
                    }
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true
                    }
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </Col>
            </Row>
          </TabPane>
        </TabContent>

      </div>
    );
  }
}
export default costingColorsC;




