import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import MaterialOrder from "../components/Purchases/orderMaterialCpo";
import { matchSorter } from "match-sorter";

// reactstrap components
import {
  Row,
  Col,
  Card,
  CardBody,
  CardHeader,
  Button,
  Label,
  Input,
  ModalHeader,
  Modal,
  ModalBody,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";
import { style } from "@mui/system";

class orderP extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      materialOrder: null,
      materialProvider: null,
      allMaterials: null,
      filesForm: ["1"],
      selectedFile: null,
      uploadPO: false,
      po_number: "",
      uploadAllPO: false,
      resumePO: false,
      clientSelected: null,
      selectedPo: [],
      selectedMaterial: [],
      fileList: [],
      cliente: null,
      material_id: null,
      checked: {},
      orderNumber: null,
      order: {},
      po_array: [],
      accountArr: [],
      pedido:null,
      mat_code: '',
      price: null,
      accountArrJson: [],
      total: null,
      accountArrP: [],
      accountArrPJson: [],
      resumeModal: false,
      description: ''
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  getDate = () => {
    var today = new Date();
    var day = parseInt(today.getDate());
    var month = parseInt(today.getMonth() + 1);
    var year = parseInt(today.getFullYear());
    var date = day + "/" + month + "/" + year;
    this.setState({ date: date });
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  handleCheckPo = (e) => {
    this.state.selectedPo.push(e);
    console.log("Este es la PO", e.id);
  };
  handleCheckMaterial = (e) => {
    this.state.selectedMaterial.push(e);
    console.log("Este es el material", e.id);
  };

  removePO = (e) => {
    let element = [...this.state.selectedPo];
    const po = element.findIndex((item) => item === e);
    const list = [...this.state.selectedPo];
    list.splice(po, 1);
    this.setState({
      selectedPo: list,
    });
  };

  _MaterialProvider = false;
  getMaterialP = () => {
    let provider = this.state.providerSelected;
    this.setState({ provedor: provider }, () => { });
    this.toggleLoading();
    // console.log(JSON.stringify(provider));
    this.state.Prov = provider;
    //console.log('id de provider', this.state.Prov)
    axios
      .get(
        process.env.REACT_APP_IP +
        `/provider/get_type_for_provider/?provider_id=${provider}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function (response) {
          if (response.data.length === 0) {
            this._MaterialProvider = true;
            this.toggleLoading();
            this.notify("tr", "warning", "NO MATERIAL DATA FOR THIS PROVIDER");
          } else if (response.data.length > 1) {
            this.toggleLoading();
            this._MaterialProvider = true;
            this.setState({
              materialProvider: response.data.map((materialP, key1) => {
                return {
                  value: materialP.id,
                  label: materialP.name,
                  actions: (
                    <>
                      <input
                        color="info"
                        type="checkbox"
                        onChange={(e) => {
                          if (this.state.accountArrP[key1]) {
                            this.state.accountArrP.splice(key1, 1);
                          } else {
                            this.state.accountArrP[key1] = materialP.id;
                          }
                        }}
                        name="selectedMaterial"
                        size="sm"
                        className="btn-icon btn-link"
                      ></input>
                    </>
                  ),
                };
              }),
            });
            this.toggleLoading();
            this.notify("tr", "success", "Successfully loaded!");
          }
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  _materialLoaded = false;
  getMaterialOrderPo = () => {
    let client = this.state.clientSelected;
    this.toggleLoading();
    this.setState({ cliente: client }, () => { });
    axios
      .get(
        process.env.REACT_APP_IP + `/material_order/po_order/?client=${client}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function (response) {
          if (response.data.length === 0) {
            this._materialLoaded = true;
            //console.log('NO')
            this.toggleLoading();
            this.notify("tr", "warning", "NO ORDER DATA FOR THIS CLIENT");
          } else if (response.data.length > 1) {
            //console.log('si')
            this._materialLoaded = true;
            this.setState({
              materialOrder: response.data.map((mat, key) => {
                return {
                  value: mat.id,
                  date_order: mat.date_order,
                  po_number: mat.po_number,
                  status: mat.status,
                  actions: (
                    <>
                      <input
                        color="info"
                        type="checkbox"
                        onChange={(e) => {
                          this.state.po_number = mat.po_number;
                          if (this.state.accountArr[key]) {
                            this.state.accountArr.splice(key, 1);
                          } else {
                            this.state.accountArr[key] = mat.id;
                          }
                        }}
                        checked={this.state.checked[mat.id]}
                        name="selectedMaterial"
                        size="sm"
                        className="btn-icon btn-link"
                      ></input>
                    </>
                  ),
                };
              }),
            });
            this.toggleLoading();
            this.notify("tr", "success", "Successfully loaded!");
          }
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  _loadingShowData = false;
  ShowData = () => {
    this.toggleLoading();
    let updateJSON = {
      //"id_client": this.state.clientSelected,
      provider: this.state.providerSelected,
      type_material: this.state.accountArrPJson,
      pos_list: this.state.accountArrJson,
      //"commentsLabel": this.state.commentsLabel,
    };
    //this.toggleLoading();
    axios
      .post(this.costingURL + `/material_order/search_mat/`, updateJSON, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this.toggleLoading();
          console.log(response.data);
          if (response.data.length === 0) {
            this._loadingShowData = true;
            this.notify("tr", "warning", "NO ORDER DATA FOR THIS CLIENT");
          } else if (response.data.length > 1) {
            this._loadingShowData = true;
            const { data } = response;
            this.setState({
              allMaterials: data.map((info, index) => {
                return {
                  value: info.id,
                  id_material: info.material,
                  description: info.description,
                  balance: info.balance,
                  total: info.total,
                  price: info.price,
                 
                  actions: (
                    <>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "row",
                          justifyContent: "space-around",
                          alignItems: "center",
                        }}
                      >
                        <Col>
                          <Button
                            color="info"
                            size="sm"
                            className="btn-icon btn-link"
                            onClick={() => {

                              this.state.material_id = parseInt(info.id);
                              this.state.mat_code = info.material;
                              this.state.total = info.total;
                              this.state.price = info.price;
                              this.state.description = info.description;

                              let matJSON = {
                                material_id: this.state.material_id,
                                quantity: this.state.total,
                                po: this.state.accountArrJson[0],
                              };

                              if (this.state.selectedMaterial[index]) {
                                this.state.selectedMaterial.splice(index, 1);
                              } else {
                                this.state.selectedMaterial[index] = matJSON;
                              }
                              this.toggleresumeModal();
                            }}
                          >
                            <i className="fa fa-edit" />
                          </Button>
                        </Col>
                        <Col>
                          <Label>{this.state.orderNumber}</Label>
                        </Col>
                      </div>
                    </>
                    /*
                     */
                  ),
                };
              }),
            });
            this._isMounted && this.notify("tr", "success", "Generated!");
          }
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function (error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  _loadingProviders = false;
  getProductionProvider = () => {
    axios
      .get(this.costingURL + "/provider/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadingProviders = true;
          const { data } = response;
          this.setState({
            providers: data.map((provider) => {
              return {
                value: provider.id,
                label: provider.name,
                currency: provider.currency,
              };
            }),
          });
        }.bind(this)
      );
  };

  CreateOrder = (data) => {
    this.toggleLoading();
    axios
      .post(this.costingURL + `/order_mat/create_order/`, data, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this.toggleLoading();
          this.notify("tr", "success", "Order Generated!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  toggleresumeModal = () => {
    this.setState({
      resumeModal: !this.state.resumeModal,
    });
  };

  toggleUploadAllPOsModal = () => {
    this.setState({
      uploadAllPO: !this.state.uploadAllPO,
    });
  };

  toggleUploadPOModal = () => {
    this.setState({
      uploadPO: !this.state.uploadPO,
    });
  };

  toggleGetResumeModal = () => {
    this.setState({
      resumePO: !this.state.resumePO,
    });
  };

  addClientToList = (clientId) => {
    const list = [...this.state.clientList];
    list.push(clientId.value);
    this.setState({
      clientList: list,
    });
  };

  addProviderToList = (providerId) => {
    const list = [...this.state.providerList];
    list.push(providerId.value);
    this.setState({
      providerList: list,
    });
  };

  addFileToList = (event) => {
    this.setState({ selectedFile: event.target.files[0] });
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  addEvent = () => {
    const listForm = [...this.state.filesForm];
    listForm.push("1");
    this.setState({
      filesForm: listForm,
    });
  };

  resetModal = () => {
    const listForm = ["1"];
    this.setState({
      filesForm: listForm,
    });
  };

  loadComment = (comment) => {
    this.setState({
      commentForm: comment,
    });
    console.log(comment);
  };

  componentDidMount() {
    this._isMounted = true;
    this.getDate();
    this.getClients();
    this.getProductionProvider();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const {
      loading,
      materialOrder,
      materialProvider,
      allMaterials,
      orderData,
    } = this.state;
    const pageMode = this.checkPageMode();
    //console.log('access', this.Role)
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Modal
          isOpen={this.state.resumeModal}
          toggle={this.toggleresumeModal}
          size={"lg"}
          style={{ margin: "1vh auto 0px" }}
        >
          <ModalHeader toggle={this.toggleresumeModal} tag={"h2"}>
            Material Order
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col>
                <Label>Cdigo:</Label>
                <Input value={this.state.mat_code} />
                <Label>% Adicional:</Label>
                <Input
                  placeholder="5"
                  onChange={(o) => {
                    this.loadFormColor({
                      ...this.state.ColorsForm,
                      client: o.value,
                    });
                  }}
                />
              </Col>
              <Col>
                <Label>Precio de Unidad:</Label>
                <Input
                  placeholder={this.state.price}
                  onChange={(o) => {
                    this.loadFormColor({
                      ...this.state.ColorsForm,
                      client: o.value,
                    });
                  }}
                />
                <Label>Este Pedido:</Label>
                <Input
                  onChange={(e) => {
                    this.setState({
                      pedido: e.target.value
                    })
                    console.log(this.state.pedido);
                  }}
                />
              </Col>
              <Col>
                <br />
                <Button
                  type="submit"
                  color="primary"
                  size="sm"
                  onClick={() => {

                    this.setState({
                      orderNumber : this.state.pedido !== null ? this.state.pedido : 0,
                    })


                    this.toggleresumeModal();
                    this.notify("tr", "success", "Pedido Guardado");

                  }}
                  className="float-left"
                >
                  Save <i className="fa fa-save"></i>
                </Button>
              </Col>
            </Row>
            <Row>
              <Col>
                <Label>Descripcin:</Label>
                <Input
                  style={{ width: "500px" }}
                  value={this.state.description}
                />
              </Col>
            </Row>
            <br />
            <br />
            <MaterialOrder
              po_number={this.state.po_number}
              total={this.state.total}
            />
          </ModalBody>
        </Modal>
        <Row className="justify-content-center mt-5">
          <Col md={{ size: 12, order: 2 }}>
            <Card>
              <CardBody>
                <CardHeader>
                  <Row>
                    <Col sm="9">
                      <h1>Ordenes de Compra por CPO</h1>
                    </Col>
                    <CardBody>
                      <Row>
                        <Col sm="7">
                          <Row>
                            <Col>
                              <Label>Client</Label>
                              {this.Access.approve_boms.edit ? (
                                <Select
                                  options={this.state.clients}
                                  //value={selectedClient ? selectedClient : null}
                                  onChange={(o) => {
                                    this.setState({ clientSelected: o.value });
                                  }}
                                />
                              ) : (
                                <></>
                              )}
                            </Col>
                            <Col>
                              <Label for="exampleDate">Date</Label>
                              <Input
                                type="readonly"
                                name="date"
                                id="exampleDate"
                                value={this.state.date}
                                placeholder={this.state.date}
                              />
                            </Col>
                          </Row>
                          <Row>
                            <Col>
                              <Label>Provider</Label>
                              <Select
                                options={this.state.providers}
                                //value={selectedClient ? selectedClient : null}
                                onChange={(o) => {
                                  this.setState({ providerSelected: o.value });
                                }}
                              />
                            </Col>
                          </Row>
                          <Row>
                            <Col>
                              <Label>Comments</Label>
                              <Input
                                onChange={(e) => {
                                  this.setState({
                                    commentsLabel: e.target.value,
                                  });
                                }}
                              />
                            </Col>
                            <Col sm="3">
                              <Label>% Aditional</Label>
                              <Input
                                type="text"
                                id="exampleDate"
                                placeholder="5"
                                style={{ textAlign: "center" }}
                              />
                            </Col>
                          </Row>
                          <Row>
                            <Col>
                              <Label>Precio</Label>
                              <Input
                                onChange={(o) => {
                                  this.loadFormColor({
                                    ...this.state.ColorsForm,
                                    client: o.value,
                                  });
                                }}
                              />
                            </Col>
                            <Col>
                              <Label>P. Minimo</Label>
                              <Input
                                onChange={(o) => {
                                  this.loadFormColor({
                                    ...this.state.ColorsForm,
                                    client: o.value,
                                  });
                                }}
                              />
                            </Col>
                            <Col>
                              <Label>Moneda</Label>
                              <Input
                                options={this.state.providers}
                                type="text"
                                id="exampleDate"
                                style={{ textAlign: "center" }}
                                onChange={(o) => {
                                  this.setState({
                                    providerSelected: o.currancy,
                                  });
                                }}
                              />
                            </Col>
                            <Col>
                              <Label>Procedencia</Label>
                              <Input
                                type="text"
                                id="exampleDate"
                                //placeholder="5"
                                style={{ textAlign: "center" }}
                              />
                            </Col>
                          </Row>
                        </Col>
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                              this.getMaterialOrderPo();
                              this.getMaterialP();
                            }}
                            className="float-left"
                          >
                            Search <i className="fa fa-search"></i>
                          </Button>
                        </Col>

                        <div
                          style={{
                            display: "flex",
                            flexDirection: "row",
                            justifyContent: "center",
                            alignItems: "center",
                            width: "100%",
                            marginTop: "10vh",
                          }}
                        >
                          <Row
                            className={`${pageMode.bg} border`}
                            style={{ width: "100%" }}
                          >
                            {materialProvider && (
                              <>
                                <Col>
                                  <br></br>
                                  <br></br>
                                  <Card>
                                    <CardBody>
                                      <ReactTable
                                        responsive
                                        defaultPageSize={5}
                                        data={materialProvider}
                                        filterable
                                        showPaginationBottom={true}
                                        className="-striped -highlight primary-pagination"
                                        columns={[
                                          {
                                            Header: "Id_material",
                                            accessor: "value",
                                            sortable: true,
                                            filterMethod: (filter, row) =>
                                              matchSorter(row, filter.value, {
                                                keys: ["value"],
                                              }),
                                            filterAll: true,
                                          },
                                          {
                                            Header: "Material",
                                            accessor: "label",
                                            sortable: true,
                                            filterMethod: (filter, row) =>
                                              matchSorter(row, filter.value, {
                                                keys: ["label"],
                                              }),
                                            filterAll: true,
                                          },
                                          {
                                            Header: "SELECCIONAR",
                                            accessor: "actions",
                                            sortable: true,
                                            filterMethod: (filter, row) =>
                                              matchSorter(row, filter.value, {
                                                keys: ["actions"],
                                              }),
                                            filterAll: true,
                                            width: 100,
                                          },
                                        ]}
                                      />
                                      <Button
                                        type="submit"
                                        color="primary"
                                        size="1sm"
                                        onClick={(event) => {
                                          this.state.accountArrJson = [];
                                          this.state.accountArrPJson = [];
                                          for (
                                            let i = 0;
                                            i < this.state.accountArrP.length;
                                            i++
                                          ) {
                                            if (this.state.accountArrP[i]) {
                                              this.state.accountArrPJson.push(
                                                this.state.accountArrP[i]
                                              );
                                            }
                                          }
                                          for (
                                            let i = 0;
                                            i < this.state.accountArr.length;
                                            i++
                                          ) {
                                            if (this.state.accountArr[i]) {
                                              this.state.accountArrJson.push(
                                                this.state.accountArr[i]
                                              );
                                            }
                                          }
                                          this.ShowData();
                                        }}
                                        className="Create"
                                      >
                                        Create <i className="fa fa-plus"></i>
                                      </Button>
                                    </CardBody>
                                  </Card>
                                </Col>
                              </>
                            )}
                            {materialOrder && (
                              <>
                                <Col>
                                  <br></br>
                                  <br></br>
                                  <Card>
                                    <CardBody>
                                      <ReactTable
                                        responsive
                                        defaultPageSize={5}
                                        data={materialOrder}
                                        filterable
                                        showPaginationBottom={true}
                                        className="-striped -highlight primary-pagination"
                                        columns={[
                                          {
                                            Header: "CPO",
                                            accessor: "po_number",
                                            sortable: true,
                                            filterMethod: (filter, row) =>
                                              matchSorter(row, filter.value, {
                                                keys: ["po_number"],
                                              }),
                                            filterAll: true,
                                          },
                                          {
                                            Header: "SELECCIONAR",
                                            accessor: "actions",
                                            sortable: true,
                                            filterMethod: (filter, row) =>
                                              matchSorter(row, filter.value, {
                                                keys: ["actions"],
                                              }),
                                            filterAll: true,
                                            width: 100,
                                          },
                                        ]}
                                      />
                                    </CardBody>
                                  </Card>
                                </Col>
                              </>
                            )}
                          </Row>
                        </div>
                      </Row>
                    </CardBody>
                    <br></br>
                    {allMaterials && (
                      <>
                        <Card className={`${pageMode.bg} border`}>
                          <CardBody>
                            <ReactTable
                              responsive
                              defaultPageSize={5}
                              data={allMaterials}
                              filterable
                              showPaginationBottom={true}
                              className="-striped -highlight primary-pagination"
                              columns={[
                                {
                                  Header: "CODIGO MATERIAL",
                                  accessor: "id_material",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["id_material"],
                                    }),
                                  filterAll: true,
                                  width: 132,
                                },
                                {
                                  Header: "DESCRIPCION",
                                  accessor: "description",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["description"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "BALANCE",
                                  accessor: "balance",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["balance"],
                                    }),
                                  filterAll: true,
                                  width: 150,
                                },
                                {
                                  Header: "ESTE PEDIDO",
                                  accessor: "pedido",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["pedido"],
                                    }),
                                  filterAll: true,
                                  width: 150,
                                },
                                {
                                  Header: "PEDIR?",
                                  accessor: "actions",
                                  sortable: true,
                                  filterable: true,
                                  width: 100,
                                },
                              ]}
                            />
                            <Button
                              type="submit"
                              color="primary"
                              size="1sm"
                              onClick={(event) => {
                                let dataToSend = {
                                  client: this.state.clientSelected,
                                  provider: this.state.providerSelected,
                                  comments: this.state.commentsLabel,
                                  type_material: this.state.accountArrPJson[0],
                                  mats: this.state.selectedMaterial,
                                };

                                let matsArr = [];
                                for (
                                  let i = 0;
                                  i < this.state.selectedMaterial.length;
                                  i++
                                ) {
                                  if (this.state.selectedMaterial[i]) {
                                    matsArr.push(
                                      this.state.selectedMaterial[i]
                                    );
                                  }
                                }

                                this.CreateOrder(dataToSend);
                              }}
                              className="Generate"
                            >
                              Generate Order <i className="fa fa-check"></i>
                            </Button>
                          </CardBody>
                        </Card>
                      </>
                    )}
                  </Row>
                </CardHeader>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}
export default orderP;
