import React from 'react';
import ReactTable from "react-table";
import {
  Button,
} from "reactstrap";

let Data=[];

function ClientList({...props}) {
  const Access = JSON.parse(localStorage.getItem('permissions') || '{}');
  const companies = [
    {value: 'T', label: 'TRECENTO'},
    {value: 'Z', label: 'ZUNTEX'},
    {value: 'J', label: 'JT'},
  ]
  Data = props.dataTable.map((prop, key) => {
    const company = companies.find(c=>c.value===prop.company)
    return {
      row: key,
      id: prop.id,
      name: prop.name,
      abbreviation: prop.abbreviation,
      company: company.label,
      actions: (
        <div className="actions-right" >
          <Button
            onClick={() => {
              props.editClient(prop);
            }}
            color = "info"
            size = "sm"
            className = "btn-icon btn-link edit"
          >
            <i className = "tim-icons icon-pencil" />
          </Button>
          {Access.clients.delete && <Button
            onClick={() => {
              props.removeClient(prop);
            }}
            color = "warning"
            size = "sm"
            className = "btn-icon btn-link edit"
          >
            <i className = "tim-icons icon-simple-remove" />
          </Button>}
        </div>
      )
    };
  });
  return (<>        
    <h4><b>{props.tableTitle}</b></h4>
    <ReactTable
      data = {Data}
      filterable
      columns = {[
        {
          Header: "Code",
          accessor: "id"
        },
        {
          Header: "Name",
          accessor: "name"
        },
        {
          Header: "Abbreviation",
          accessor: "abbreviation"
        },
        {
          Header: "Company",
          accessor: "company"
        },
        {
          Header: "Actions",
          accessor: "actions",
          sortable: false,
          filterable: false
        }
      ]}
      defaultPageSize={props.itemsPages}
      showPaginationTop
      showPaginationBottom={false}
      defaultSorted = {[
        {
          id: "id",
          desc: true
        }
      ]}
      className="-striped -highlight primary-pagination"
    />
  </>);
}

export default ClientList;