import React from "react";
import {
  Form,
  Table,
  Button,
  Row, Col,
  Input,
  FormGroup,
  Label,
  Modal,
  ModalBody,
  ModalFooter
} from "reactstrap";
import NotificationAlert from "react-notification-alert";
import axios from 'axios';
import { Redirect, Switch } from "react-router-dom";
import { Prompt } from 'react-router';

//import FabricsCard from "../components/QuotationElements/FabricsCard.jsx";
import MaterialsCard from "../components/QuotationElements/MaterialsCard.jsx";
import EmbellishmentsCard from "../components/QuotationElements/EmbellishmentsCard.jsx";
import CostingHeaderCard from '../components/QuotationElements/CostingHeaderCard.jsx';
import VariablesCostingCard from '../components/QuotationElements/VariablesCostingCard.jsx';
import ManufacturingCostsCard from '../components/QuotationElements/ManufacturingCostsCard.jsx';
import SAMsCard from '../components/QuotationElements/SAMsCard.jsx';
import ConsumptionCard from '../components/QuotationElements/ConsumptionCard.jsx';
import FOBCostCard from '../components/QuotationElements/FOBCostCard.jsx';
import TotalFOBCostCard from '../components/QuotationElements/TotalFOBCostCard.jsx';
import { downloadFileFromS3, uploadFileToS3 } from '../helpers/awsS3';
import { generatePdfDocument, CloneCosting } from '../helpers/costing.js';
import '../components/QuotationElements/loader.min.css';
import FabricsCardBeta from '../components/QuotationElements/FabricsCardBeta.jsx';
import CostingCalculations from "../helpers/CostingCalculations.js";
import QuotationTabs from '../components/CustomElements/QuotationTabs';
import { TableValues } from '../components/QuotationElements/TableValues';

class Quotation extends React.Component {
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  Views = {
    Consumption: true,
    Fabrics: true,
    Materials: true,
    Manufacturing: true,
    Logistics: true,
    Embellishments: true,
    Sumary: true
  }
  _isMounted = false;
  _loadingFabric = false;
  _loadingMaterial = false;
  _loadingEmbellishment = false;

  _loadedHeader = false;
  _loadedDetail = false;
  _loadedSketch = false;
  _loadedTemplates = false;
  _loadingSeasons = false;

  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';
  user_id = localStorage.getItem('user_id') || '0';

  header = {
    headers: {
      "Authorization": "Token " + this.userToken,
      "Content-Type": "multipart/form-data"
    }
  };

  headerJson = {
    headers: {
      "Authorization": "Token " + this.userToken,
      "Content-Type": "application/json"
    }
  };

  _Collapses = [
    "fabrics",
    "material",
    "manufacturing",
    "consumption",
    "embellishment",
    "Logistics",
    "variables",
    "comment"
  ];

  FixElementValue(value) {
    if (value === null || value === undefined || value === "" || isNaN(value)) {
      return '0';
    }
    return value;
  }

  constructor(props) {
    super(props);
    this.state = {
      CostingHeaderId: 0,
      CostingHeader: {},
      openedCollapses: this._Collapses,
      fabricsCatalog: [],
      materialsCatalog: [],
      embellishmentCatalog: [],
      fabricStyleCatalog: [],
      fabrics: [],
      materials: [],
      embellishments: [],
      sumWhite: 0.00,
      sumMedium: 0.00,
      sumDark: 0.00,
      sumSpecial: 0.00,
      sumMaterials: 0.00,
      detail: {},
      embellishment: 0.00,
      marginDecorations: 0,
      _dataIsValid: false,
      Reload: false,
      totalSumWhite: 0.0,
      totalSumMedium: 0.0,
      totalSumDark: 0.0,
      totalSumSpecial: 0.0,
      pathToSketchData: "",
      ApprovalModal: false,
      MarkerBlock: false,
      loading: false,
      seasons: null,
    }
  }

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : ''; console.log(response, message, task)
    if (
      response.status !== undefined &&
      response.status === 401
    ) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  toggleApprovalModal = () => {
    this.setState({
      ApprovalModal: !this.state.ApprovalModal
    });
  }

  /**
   * get all fabrics for catalog select
   */
  getFabrics = () => {
    axios.get(
      process.env.REACT_APP_IP + '/fabrics/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingFabric = true;
      this._isMounted && this.setState({ fabricsCatalog: response.data });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'get fabrics');
    }.bind(this));
  }

  /**
   * get all materials for catalog select
   */
  getMaterials = () => {
    axios.get(
      process.env.REACT_APP_IP + '/materials/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingMaterial = true;
      this._isMounted && this.setState({ materialsCatalog: response.data })
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'get materials');
    }.bind(this));
  }

  /**
   * @param {number} Id costing header coming from Costing.jsx
   * @param {boolean} buildModule if build sketch and detail
   */
  costingHeader = (Id, buildModule = false) => {
    axios.get(
      process.env.REACT_APP_IP + '/costingHeader/' + Id + '/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadedHeader = true;
      this._isMounted && this.setState({
        CostingHeader: response.data
      });
      if (buildModule) {
        if (response.data.pathToSketch !== "") {
          this.costingSketch(response.data.pathToSketch);
        }
        this.costingDetail(response.data.detail_id);
      }
    }.bind(this)).catch(function (error) {
      this.setState({
        loading: false
      })
      this.handleError(error, 'costingHeader');
    }.bind(this));
  }

  getSeasons = () => {
    axios.get(process.env.REACT_APP_IP + '/seasons/', {
      headers: {
        "Authorization": "Token " + this.userToken
      }
    }).then(function (response) {
      this._loadingSeasons = true;
      this._isMounted && this.setState({ seasons: response.data });
    }.bind(this)).catch(function (error) {
      this.handleError(error);
    }.bind(this));
  }



  /**
   * @param {number} detailId costing header id 
   */
  costingDetail = (detailId) => {
    axios.get(
      process.env.REACT_APP_IP + '/costingDetail/' + detailId + '/',
      this.headerJson
    ).then(function (detailResponse) {
      this._loadedDetail = true;
      let detail = detailResponse.data;
      // console.log(detail);
      detail.wcomplex = detail.extra_time === 4.655 ? true : false;
      let dms = detail.dms;
      localStorage.setItem("dms", JSON.stringify(detail.dms));
      let materials = detail.materials;
      let embellishments = detail.embellishments;
      dms.sort((a, b) => (a.line_id > b.line_id) ? 1 : ((b.line_id > a.line_id) ? -1 : 0))
      materials.sort((a, b) => (a.line_id > b.line_id) ? 1 : ((b.line_id > a.line_id) ? -1 : 0))
      embellishments.sort((a, b) => (a.line_id > b.line_id) ? 1 : ((b.line_id > a.line_id) ? -1 : 0))
      detail.dms = dms;
      detail.materials = materials;
      detail.embellishments = embellishments;
      this.setState({
        detail: detail,
        loading: false
      });
    }.bind(this)).catch(function (error) {
      this.setState({
        loading: false
      })
      this.handleError(error, 'costingDetail');
    }.bind(this));
  }

  /**
   * @param {string} keyPath header's sketch key
   */
  costingSketch = (keyPath) => {
    if (keyPath) {
      const url = process.env.REACT_APP_IP + '/costingHeader/download/?key=' + keyPath;
      const config = {
        params: { "key": keyPath },
        headers: { "Authorization": "Token " + this.userToken }
      }
      downloadFileFromS3(url, config).then(function (key) {
        this._loadedSketch = true;
        this.setState({
          pathToSketchData: key
        });
      }.bind(this)).catch(function (error) {
        this.handleError(error, 'downloadFileFromS3 mount');
      }.bind(this));
    }
  }

  /**
   * get all information for the costing sheet.
   */
  BuildModule = () => {
    this.setState({ loading: true });
    this.getFabrics();
    this.getMaterials();
    this.getSeasons();
    this.costingHeader(this.props.location.state.costingData, true)
  }

  /**
   * assemble dms, materials and embellishments
   * @returns {Object}
   */
  buildDetailDB = () => {

    // this.setState({
    //   detail: [
    //     ...prevState.detail,
    //     {
    //       dms: prevState.detail.dms.map((fabric) => ({

    //       }))
    //     }
    //   ]
    // })

    let { detail } = this.state;
    //console.log(detail)
    let dms = [];
    detail.dms.map(fabric => {
      dms.push({
        "line_id": fabric.line_id,
        "id": fabric.id,
        "dm": fabric.dm,
        "priceWhite": fabric.priceWhite,
        "priceMedium": fabric.priceMedium,
        "priceDark": fabric.priceDark,
        "priceSpecial": fabric.priceSpecial,
        "priceWhiteC": 0,
        "priceMediumC": 0,
        "priceDarkC": 0,
        "priceSpecialC": 0,
        "wasted_flag": fabric.waste_flag,
        "Blong": this.FixElementValue(fabric.Blong),
        "Bwidth": this.FixElementValue(fabric.Bwidth),
        "fabricType": fabric.fabricType,
        "weightBWashOriginal": this.FixElementValue(fabric.weightBWashOriginal),
        "weightBWashCalculated": this.FixElementValue(fabric.weightBWashCalculated),
        "dm_width": this.FixElementValue(fabric.dm_width),
        "markerWidth": this.FixElementValue(fabric.markerWidth),
        "dm_pt": fabric.dm_pt,
        "yieldydslbs": this.FixElementValue(fabric.yieldydslbs),
        "markerEfficiency": this.FixElementValue(fabric.markerEfficiency),
        "markerLong": this.FixElementValue(fabric.markerLong),
        "pieceByMarker": this.FixElementValue(fabric.pieceByMarker),
        "lbsunit": this.FixElementValue(fabric.lbsunit),
        "lbsunitwaste": this.FixElementValue(fabric.lbsunitwaste),
        "sqinchunit": this.FixElementValue(fabric.sqinchunit),
        "ydsunit": this.FixElementValue(fabric.ydsunit),
        "bydspunit": this.FixElementValue(fabric.bydspunit),
        "blbspunit": this.FixElementValue(fabric.blbspunit),
      })

      // this.setState({detail: {...detail, dms}})
      // this.state.detail.ldms = detail.dms
      // console.log('dets: ',this.state.detail.dms)
      return fabric;
    });

    //assemble materials
    let materials = [];
    detail.materials.map(material => {
      materials.push({
        "line_id": material.line_id,
        "id": material.id,
        "unitPrice": parseFloat(this.FixElementValue(material.unitPrice)),
        "transport": parseFloat(this.FixElementValue(material.transport))
      });
      return material;
    });

    let embellishments = [];
    detail.embellishments.map(embellishment => {
      if (embellishment.name !== "") {
        embellishments.push({
          "id": embellishment.id,
          "name": embellishment.name,
          "status": "A",
          "price": embellishment.price ? embellishment.price : 0.00,
          "margin": embellishment.margin ? parseFloat(embellishment.margin) : 0.00,
          "description": embellishment.description ? embellishment.description : ''
        })
      }
      return embellishment;
    })

    let calcs = CostingCalculations(this.state.detail)
    detail.style = this.state.CostingHeader.style;
    detail.totalTime = calcs.totalTime
    detail.tpMaterials = calcs.pricesMaterials
    detail.tpFabricsWhite = calcs.sellingPrices.white
    detail.tpFabricsMedium = calcs.sellingPrices.medium
    detail.tpFabricsDark = calcs.sellingPrices.dark
    detail.tpFabricsSpecial = calcs.sellingPrices.special
    detail.pathToSAM = detail.pathToSAM ? detail.pathToSAM : '...'
    detail.sewing = calcs.sewing
    detail.cutting = calcs.cutting
    detail.cxm = calcs.cxm
    detail.decorationMargin = calcs.decorationMargin
    detail.broker = this.state.detail.broker
    detail.freightPrice = calcs.freightPrice
    detail.freightQuantity = calcs.freightQuantity
    detail.freightTotal = calcs.freightTotal
    detail.factoring = detail.factoring ? detail.factoring : 0.00
    detail.profitWhite = detail.profitWhite ? detail.profitWhite : 0.00
    detail.profitMedium = detail.profitMedium ? detail.profitMedium : 0.00
    detail.profitDark = detail.profitDark ? detail.profitDark : 0.00
    detail.profitSpecial = detail.profitSpecial ? detail.profitSpecial : 0.00
    detail.sell = detail.sell ? detail.sell : 0.00
    detail.pfabrics = detail.pfabrics ? detail.pfabrics : 0
    detail.pmaterials = detail.pmaterials ? detail.pmaterials : 0
    detail.pir = detail.pir ? detail.pir : 0
    detail.comment = detail.comment ? detail.comment : ''
    detail.dms = dms;
    detail.materials = materials
    detail.embellishments = embellishments;
    detail.id = this.props.location.state.costingData;
    detail.drawcore = detail.drawcore ? detail.drawcore : '0/0'
    detail.twillTape = detail.twillTape ? detail.twillTape : '0/0'
    detail.clearElastic = detail.clearElastic ? detail.clearElastic : '0/0'
    detail.lining = detail.lining ? detail.lining : '0/0'
    detail.selfFabricTape = detail.selfFabricTape ? detail.selfFabricTape : '0/0'
    detail.elastic = detail.elastic ? detail.elastic : '0/0'
    detail.waistbandElastic = detail.waistbandElastic ? detail.waistbandElastic : '0/0'
    detail.pathToSketch = this.state.CostingHeader.pathToSketch
    detail.colors = detail.colors ? detail.colors : ''
    detail.proto_status = this.state.CostingHeader.proto_status
    detail.season = this.state.CostingHeader.season.id;
    detail.whodie = this.state.detail.whodie;
    detail.wtshirt = this.state.detail.wtshirt;
    detail.extra_time = this.state.detail.extra_time;
    delete detail.created_at;
    delete detail.updated_at;
    delete detail.deletedFlag;

    return detail;
  }

  handleSaveProto = (event) => {
    event.preventDefault();
    this.setState({ loading: true });
    console.log(this.state.detail.dms)
    const buildDetailDB = this.buildDetailDB();
    console.log(buildDetailDB)
    axios.post(
      process.env.REACT_APP_IP + '/costingDetail/update_costing/',
      buildDetailDB,
      this.headerJson
    ).then(function () {
      this.setState({ loading: false });
      this.notify("tr", "success", "CostSheet updated.");
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      //console.log(error)
      this.setState({ loading: false });
      this.notify("tr", "warning", "Cannot save costsheet");
      this.componentDidMount();
    }.bind(this));
  }

  newProto = () => {
    this.setState({ loading: true });
    CloneCosting(this.props.location.state.costingData, 1).then(function () {
      this.setState({ loading: false });
      this.props.history.push("/admin/costing");
    }.bind(this)).catch(function () {
      this.setState({ loading: false });
      this.notify("tr", "warning", "Cannot save new proto");
      this.componentDidMount();
    });
  }

  handleSubmit = (event) => {
    event.preventDefault();
  }

  sendtoApprove = () => {
    this.setState({ loading: true });
    let data = new FormData();
    data.append('flag', 1);//A
    data.append('id', this.props.location.state.costingData);
    axios.post(
      process.env.REACT_APP_IP + '/administration/change_approve_status/',
      data,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.setState({ loading: false });
      this.notify("tr", "success", "CostSheet updated.");
      this.toggleApprovalModal();
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      this.setState({ loading: false });
      this.handleError(error);
    }.bind(this));
  }

  freeCostSheet = async () => {
    if (this.props.location && (this.Role !== "Consulting" || this.Role !== "Telas")) {
      const Id = this.props.location.state.costingData;
      return await axios.patch(
        process.env.REACT_APP_IP + '/costingHeader/' + Id + '/',
        { "updating_user": "" },
        this.headerJson
      )
    }
  }

  beforeunload(e) {
    if (this.props.dataUnsaved) {
      e.preventDefault();
      e.returnValue = true;
    }
  }

  componentDidMount() {
    this._isMounted = true;
    if (this.props.location.state !== undefined) {
      this.BuildModule();
    } else {
      // Redirect to costing page
      this.props.history.push("/admin/costing");
    }
    if (this.Role === 'Telas') {
      this.Views = {
        ...this.Views,
        Materials: false,
        Manufacturing: false,
        Logistics: false,
        Embellishments: true,
        Sumary: false
      }
    } else if (this.Role === 'Ingenieria') {
      this.Views = {
        ...this.Views,
        Consumption: false,
        Fabrics: false,
        Materials: false,
        Manufacturing: true,
        Logistics: false,
        Embellishments: false,
        Sumary: false
      }
    }
  }

  componentDidUpdate() {
  }

  componentWillUnmount() {
    window.removeEventListener('beforeunload', this.beforeunload.bind(this));
    this.freeCostSheet().then(function (response) {
      this._isMounted = false;
      this._loadingFabric = false;
      this._loadingMaterial = false;
      this._loadingEmbellishment = false;
    }.bind(this));
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  handleDownloadFile = (filekye, type) => {
    if (filekye === "" || filekye === "..." || filekye === null) {
      this.notify("tr", "warning", "No " + type + " has been uploaded.");
    } else {
      let url = "";
      if (type === "marker" || type === "pattern" || type === "SAM") {
        url = process.env.REACT_APP_IP + '/costingDetail/download/?key=' + filekye;
      } else {
        url = process.env.REACT_APP_IP + '/costingHeader/download/?key=' + filekye;
      }
      let config = {
        params: { "key": filekye },
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
      downloadFileFromS3(url, config).then(function (response) {
        const result = { file: response };
        window.open(result.file);
        this.notify("tr", "success", type + " downloaded!");
      }.bind(this)).catch(function (error) {
        this.handleError(error, 'download file');
      }.bind(this));
    }
  }

  handleSketchChange = (sketchFile, sketchData) => {
    const fileData = new FormData();
    fileData.append('file', sketchFile);
    fileData.append('id', this.props.location.state.costingData)
    uploadFileToS3(
      process.env.REACT_APP_IP + '/costingHeader/post_sketch/',
      this.header,
      fileData,
      'file',
      ["jpg", "jpeg", "png", "JPG", "PNG", "JPEG"]
    ).then(function (data) {
      this.notify("tr", "success", "Sketch Updated.");
      this.setState({
        CostingHeader: { ...this.state.CostingHeader, pathToSketch: data },
        pathToSketchData: sketchData
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'upload to s3 sketch');
    }.bind(this));
  }

  /** universal CostingHeader helper
   * @param {string} action
   * @param {Object} data
   */
  CostingHeaderHelper = (action, data) => {
    if (
      action === "techPack" || action === "pattern" ||
      action === "marker" || action === "SAM"
    ) {
      this.handleDownloadFile(data, action)
    } else if (action === "onSketchChange") {
      this.handleSketchChange(data.file, data.data);
    }
    if (action === "style") {
      this.setState({
        CostingHeader: { ...this.state.CostingHeader, "style": data }
      });
    }
    if (action === "proto_status") {
      this.setState({
        CostingHeader: { ...this.state.CostingHeader, "proto_status": data }
      });
    }
    if (action === "colors") {
      this.setState({
        detail: { ...this.state.detail, "colors": data }
      });
    }

    if (action === "teckpack") {
      this.setState({ loading: true });
      let formData = new FormData();
      formData.append('file', data)
      formData.append('id', this.state.CostingHeader.id)
      axios.post(
        process.env.REACT_APP_IP + '/costingHeader/post_techpack/',
        formData,
        this.header
      ).then(function () {
        this.notify("tr", "success", "CostSheet TechPack Changed");
        this.componentDidMount();
      }.bind(this)).catch(function (error) {
        this.setState({ loading: false });
        this.handleError(error, 'saveStyle');
      }.bind(this))
    }

    if (action === "saveStyle") {
      this.setState({ loading: true });
      axios.patch(
        process.env.REACT_APP_IP + '/costingHeader/'
        + this.props.location.state.costingData + '/',
        { "style": this.state.CostingHeader.style },
        this.headerJson
      ).then(function () {
        this.setState({ loading: false });
        this.notify("tr", "success", "CostSheet style Changed");
      }.bind(this)).catch(function (error) {
        this.setState({ loading: false });
        this.handleError(error, 'saveStyle');
      }.bind(this))
    }

    if (action === "season") {
      let { CostingHeader } = this.state;
      CostingHeader.season = { id: data.value, name: data.label }
      this.setState({
        CostingHeader: CostingHeader
      })
    }

    if (action === "detail") {
      let { detail } = this.state;
      let originalValue = "0/0";
      if (data.key in detail) {
        originalValue = detail[data.key]
      }
      let valueArray = originalValue.split("/")
      valueArray[data.position] = data.value
      this.setState({
        detail: { ...this.state.detail, [data.key]: valueArray.join("/") }
      })
    }
  }

  /** fabrics module helper
   * @param {string} action
   * @param {Object} data
   */
  fabricsHelper = (action, data) => {
    if (action === "pfabrics") {
      let { detail } = this.state;
      detail.pfabrics = data;
      this.setState({ detail: detail });
    }
    if (action === "selectDM") {
      let { dms } = this.state.detail;
      dms[data.dmIndex] = data.line;
      this.setState({ detail: { ...this.state.detail, dms: dms } });
    }
    if (action === "newFabric") {
      let { dms } = this.state.detail;
      dms.push(data);
      //console.log(dms)
      this.setState({ detail: { ...this.state.detail, dms: dms } });
    }
    if (action === "removeLine") {
      let { dms } = this.state.detail;
      delete dms[data]
      this.setState({ detail: { ...this.state.detail, dms: dms } });
    }
    if (action === "markerBlock") {
      this.setState({
        MarkerBlock: !this.state.MarkerBlock
      })
    }
    if (action === "downloadDM") {
      const url = process.env.REACT_APP_IP + '/fabrics/download/?key=' + data;
      let config = {
        params: { "key": data },
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
      downloadFileFromS3(url, config).then(function (response) {
        const result = { file: response };
        window.open(result.file);
        this.notify("tr", "success", "DM downloaded!");
      }.bind(this)).catch(function (error) {
        this.handleError(error, 'cannot download file');
      }.bind(this));
    }
  }

  /** materials module helper
   * @param {string} action
   * @param {Object} data
   */
  materialsHelper = (action, data) => {
    let { detail } = this.state;
    let { materials } = this.state.detail;
    if (action === "pmaterials") {
      detail.pmaterials = data;
      this.setState({ detail: detail });
    }
    if (action === "selectMaterial") {
      materials[data.materialIndex] = data.line;
      this.setState({ detail: { ...this.state.detail, materials: materials } });
    }
    if (action === "newMaterial") {
      materials.push(data);
      this.setState({ detail: { ...this.state.detail, materials: materials } });
    }
    if (action === "removeLine") {
      delete materials[data]
      //materials.splice(data, 1);
      this.setState({ detail: { ...this.state.detail, materials: materials } });
    }
  }

  /** embellishments module helper
   * @param {string} action
   * @param {Object} data
   */
  embellishmentHelper = (action, data) => {
    let { detail } = this.state;
    let { embellishments } = this.state.detail;
    if (action === "DecorationMargin") {
      detail.decorationMargin = data;
      this.setState({ detail: detail });
    }
    if (action === "selectEmbellishment") {
      embellishments[data.embellishmentIndex] = data.line;
      this.setState({ detail: { ...this.state.detail, embellishments: embellishments } });
    }
    if (action === "newEmbellishment") {
      embellishments.push(data);
      this.setState({ detail: { ...this.state.detail, embellishments: embellishments } });
    }
    if (action === "removeLine") {
      delete embellishments[data];
      this.setState({ detail: { ...this.state.detail, embellishments: embellishments } });
    }
  }

  /** sams module helper
   * @param {string} action
   * @param {Object} data
   */
  samsHelper = (action, data) => {
    /*let {detail} = this.state;
    let {totalTime} = detail;
    totalTime=data*/
    if (action === "sam") {
      this.setState({
        detail: { ...this.state.detail, totalTime: data }
      })
    }
    if (action === "operation_flag") {
      var isTrueSet = (data === 'true');
      this.setState({
        detail: { ...this.state.detail, operation_flag: isTrueSet }
      })
    }
    if (action === "wash_flag") {
      const {
        wash,
      } = data;
      if (wash === "whodie") {
        this.setState({
          detail: {
            ...this.state.detail,
            whodie: !this.state.detail.whodie,
            wcomplex: false,
            extra_time: !this.state.detail.whodie ? 3.800 : 0.00,
            wtshirt: false
          }
        })
      } else if (wash === "wtshirt") {
        this.setState({
          detail: {
            ...this.state.detail,
            whodie: false,
            wcomplex: false,
            wtshirt: !this.state.detail.wtshirt,
            extra_time: !this.state.detail.wtshirt ? 2.945 : 0.00,
          }
        })
      } else if (wash === "wcomplex") {
        this.setState({
          detail: {
            ...this.state.detail,
            whodie: false,
            wtshirt: false,
            wcomplex: !this.state.detail.wcomplex,
            extra_time: !this.state.detail.wcomplex ? 4.655 : 0.00,
          }
        })
      }
    }
  }

  /** manufacturing module helper
   * @param {string} action
   * @param {Object} data
   */
  manufacturingHelper = (action, data) => {
    let { detail } = this.state;
    if (action === "cxm") {
      let sewing = data * detail.totalTime;
      this.setState({
        detail: {
          ...this.state.detail,
          cxm: data,
          sewing: sewing
        }
      })
    }
    if (action === "cutting") {
      this.setState({
        detail: { ...this.state.detail, cutting: data }
      })
    }
    if (action === 'pir') {
      this.setState({
        detail: {
          ...this.state.detail,
          pir: data
        }
      })
    }
  }

  /** FOB module helper
   * @param {string} action
   * @param {Object} data
   */
  helperFOB = (action, data) => {
    if (action !== "") {
      this.setState({
        detail: { ...this.state.detail, [action]: data }
      })
    }
  }

  /** FOB module helper
   * @param {string} action
   * @param {Object} data
   */
  helperTotalFOB = (action, data) => {
    if (action !== "") {
      this.setState({
        detail: { ...this.state.detail, [action]: data }
      })
    }
  }

  /**
   * weightBWashCalculated function
   * Will watch weightBWashOriginal and handle that change.
   */
  weightBWashCalculatedHandle = (inputId, outputId, fabricsKey, valueChanged) => {
    let input = document.getElementById(inputId);
    let output = document.getElementById(outputId);

    if (input.value !== null && input.value !== undefined && input.value !== "") {
      let { dms } = this.state.detail;
      dms[fabricsKey][valueChanged] = (input.value * 1);

      let weightBWashCalculated = (dms[fabricsKey].weightBWashOriginal / 33.9057475);

      dms[fabricsKey].weightBWashCalculated = isNaN(weightBWashCalculated) ? 0.0 : weightBWashCalculated;

      this.setState({
        detail: { ...this.state.detail, dms: dms }
      })

      output.value = this.state.detail.dms[fabricsKey].weightBWashOriginal;
    }
  }

  /**
   * yieldydslbs function
   * Will watch weightBWashCalculated, dm_width, plies and handle that change.
   */
  yieldydslbsHandle = (inputId, outputId, fabricsKey, valueChanged) => {
    let input = document.getElementById(inputId);
    let output = document.getElementById(outputId);

    if (input.value !== null && input.value !== undefined && input.value !== "") {
      let { dms } = this.state.detail;
      dms[fabricsKey][valueChanged] = (input.value * 1);
      let fabric = dms[fabricsKey]
      let plies = dms[fabricsKey].dm_pt ? 1 : 2;

      let yieldydslbs = (576 / (fabric.weightBWashCalculated * fabric.dm_width * plies));

      dms[fabricsKey].yieldydslbs = isNaN(yieldydslbs) ? 0.0 : yieldydslbs;

      this.setState({
        detail: { ...this.state.detail, dms: dms }
      })

      output.value = this.state.detail.dms[fabricsKey].yieldydslbs;
    }
  }

  /**
   * lbsunit function
   * Will watch weightBWashOriginal, yieldydslbs, 
   * markerLong, pieceByMarker, plies and handle that change.
   */
  lbsunitHandle = (inputId, outputId, fabricsKey, valueChanged) => {
    let input = document.getElementById(inputId);
    let output = document.getElementById(outputId);

    if (input.value !== null && input.value !== undefined && input.value !== "") {
      let { dms } = this.state.detail;
      dms[fabricsKey][valueChanged] = (input.value * 1);

      let fabric = dms[fabricsKey]
      let plies = fabric.dm_pt ? 1 : 2;
      let ydsXunit = (fabric.Bwidth / 36) * (1 / (fabric.dm_width * plies)) * fabric.Blong;
      let lbsXunit = (ydsXunit / fabric.yieldydslbs);
      let lbsunit = ((fabric.markerLong / fabric.pieceByMarker) / (fabric.yieldydslbs)) + lbsXunit

      dms[fabricsKey].lbsunit = isNaN(lbsunit) ? 0.0 : lbsunit

      this.setState({
        detail: { ...this.state.detail, dms: dms }
      })

      output.value = this.state.detail.dms[fabricsKey].lbsunit;
    }
  }

  /**
   * lbsunitwaste function
   * Will watch weightBWashOriginal and handle that change.
   */
  lbsunitwasteHandle = (inputId, outputId, fabricsKey, valueChanged) => {
    let input = document.getElementById(inputId);
    let output = document.getElementById(outputId);

    if (input.value !== null && input.value !== undefined && input.value !== "") {
      let { dms } = this.state.detail;
      dms[fabricsKey][valueChanged] = (input.value * 1);
      let fabric = dms[fabricsKey]

      let lbsunitwaste = fabric.lbsunit * (1.05);

      dms[fabricsKey].lbsunitwaste = isNaN(lbsunitwaste) ? 0.0 : lbsunitwaste;

      this.setState({
        detail: { ...this.state.detail, dms: dms }
      })

      output.value = this.state.detail.dms[fabricsKey].lbsunitwaste;
    }
  }

  /**
   * sqinchunit function
   * Will watch markerLong, mkWidth, markerEfficiency, 
   * pieceByMarker and handle that change.
   */
  sqinchunitHandle = (inputId, outputId, fabricsKey, valueChanged) => {
    let input = document.getElementById(inputId);
    let output = document.getElementById(outputId);

    if (input.value !== null && input.value !== undefined && input.value !== "") {
      let { dms } = this.state.detail;
      dms[fabricsKey][valueChanged] = (input.value * 1);
      let fabric = dms[fabricsKey]
      let markerEfficiency = fabric.markerEfficiency / 100;

      let sqinchunit = (fabric.markerWidth * fabric.markerLong * 36 * markerEfficiency) / fabric.pieceByMarker;

      dms[fabricsKey].sqinchunit = isNaN(sqinchunit) ? 0.0 : sqinchunit;

      this.setState({
        detail: { ...this.state.detail, dms: dms }
      })

      output.value = this.state.detail.dms[fabricsKey].sqinchunit;
    }
  }

  /**
   * ydsunit function
   * Will watch yieldydslbs, sqinchunit and handle that change.
   */
  ydsunitHandle = (inputId, outputId, fabricsKey, valueChanged, waste) => {
    let input = document.getElementById(inputId);
    let output = document.getElementById(outputId);

    if (input.value !== null && input.value !== undefined && input.value !== "") {
      let { dms } = this.state.detail;
      dms[fabricsKey][valueChanged] = (input.value * 1);
      let fabric = dms[fabricsKey]

      let ydsunit = fabric.yieldydslbs * fabric.lbsunitwaste;
      //let ydsunit = fabric.yieldydslbs * fabric.lbsunit;
      //let ydsunit
      //if(waste){
      //  ydsunit = fabric.yieldydslbs * fabric.lbsunit;
      // }
      //else{
      //  ydsunit = fabric.yieldydslbs * fabric.lbsunitwaste;
      //}

      dms[fabricsKey].ydsunit = isNaN(ydsunit) ? 0.0 : ydsunit;

      this.setState({
        detail: { ...this.state.detail, dms: dms }
      })

      output.value = this.state.detail.dms[fabricsKey].ydsunit;
    }
  }

  addWaste = (idx) => {
    let dms = this.state.detail.dms
    dms[idx].wasted_flag = !this.state.detail.dms[idx].wasted_flag;
    this.setState({ detail: this.state.detail })
  }

  render() {
    if (this.state.Reload !== false) {
      return (<Switch>
        <Redirect push to={{ pathname: "/admin/costing" }} />
      </Switch>);
    }

    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';

    let detailCalcs = {};
    let headerFiles = {};
    if (this._loadedDetail) {
      detailCalcs = CostingCalculations(this.state.detail);
      headerFiles = {
        "pathToPattern": this.state.detail.pathToPattern,
        "pathToMarker": this.state.detail.pathToMarker,
        "pathToTechpack": this.state.CostingHeader.pathToTechpack,
        "pathToSAM": this.state.detail.pathToSAM,
        "pathToSketch": this.state.CostingHeader.PathToSketch
      };
    }

    let Tabs = [];

    this.Views.Consumption && this._loadedDetail &&
      this._loadingFabric && Tabs.push({
        collapse: 'consumption',
        title: 'Consumption',
        component: <ConsumptionCard
          fabricsCatalog={this.state.fabricsCatalog}
          fabrics={this.state.detail.dms}
          weightBWashCalculatedHandle={this.weightBWashCalculatedHandle}
          yieldydslbsHandle={this.yieldydslbsHandle}
          lbsunitHandle={this.lbsunitHandle}
          lbsunitwasteHandle={this.lbsunitwasteHandle}
          sqinchunitHandle={this.sqinchunitHandle}
          ydsunitHandle={() => this.ydsunitHandle()}
          quickCosting={false}
        />
      });

    this.Views.Fabrics && this._loadedDetail &&
      this._loadingFabric && this._loadedHeader &&
      Tabs.push({
        collapse: 'fabrics',
        title: 'Fabrics',
        component: <>
          <FabricsCardBeta
            fabricsCatalog={this.state.fabricsCatalog}
            detail={this.state.detail}
            header={this.state.CostingHeader}
            markerBlock={this.state.MarkerBlock}
            calcs={detailCalcs}
            quickCosting={false}
            helper={this.fabricsHelper}
            updater={this.addWaste}
          />
        </>
      });


    this.Views.Materials && this._loadingMaterial &&
      this._loadedDetail && Tabs.push({
        collapse: 'materials',
        title: 'Materials',
        component: <>
          <MaterialsCard
            materialsCatalog={this.state.materialsCatalog}
            detail={this.state.detail}
            header={this.state.CostingHeader}
            calcs={detailCalcs}
            quickCosting={false}
            helper={this.materialsHelper}
          />
        </>
      });

    this.Views.Manufacturing && this._loadedDetail &&
      Tabs.push({
        collapse: 'manufacturing',
        title: 'Manufacturing Costs',
        component: <>
          <Table className="tablesorter" >
            <tbody>
              <SAMsCard
                helper={this.samsHelper}
                detail={this.state.detail}
                quickCost={false}
                header={this.state.CostingHeader}
              />
              <ManufacturingCostsCard
                detail={this.state.detail}
                calcs={detailCalcs}
                quickCost={false}
                helper={this.manufacturingHelper}
                header={this.state.CostingHeader}
              />
            </tbody>
          </Table>
        </>
      });

    this.Views.Logistics && this._loadedDetail &&
      Tabs.push({
        collapse: 'logistics',
        title: 'Logistics',
        component: <>
          <FOBCostCard
            detail={this.state.detail}
            calcs={detailCalcs}
            helper={this.helperFOB}
            header={this.state.CostingHeader}
          />
          <TotalFOBCostCard
            detail={this.state.detail}
            calcs={detailCalcs}
            helper={this.helperTotalFOB}
            header={this.state.CostingHeader}
          />
        </>
      });
    this.Views.Embellishments && this._loadedDetail &&
      Tabs.push({
        collapse: 'embellishments',
        title: 'EMBELLISHMENTS/OTHERS',
        component: <EmbellishmentsCard
          detail={this.state.detail}
          header={this.state.CostingHeader}
          helper={this.embellishmentHelper}
          totalEmbellishments={detailCalcs.totalEmbellishments.toFixed(2)}
        />
      });

    this.Views.Sumary && this._loadedDetail &&
      Tabs.push({
        collapse: 'comment',
        title: 'COMMENT',
        component: <>
          <TableValues
            detail={this.state.detail}
            CostingHeaderHelper={this.CostingHeaderHelper}
            Access={this.Access}
            header={this.state.CostingHeader}
          />
          <FormGroup row>
            <Label for="exampleText" sm={4}>Comments: </Label>
            <Col sm={8}>
              <Input type="textarea" height={100}
                value={this.state.detail.comment ? this.state.detail.comment : ''}
                onChange={event => {
                  this.setState({
                    detail: { ...this.state.detail, comment: event.target.value }
                  })
                }}
                disabled={this.state.CostingHeader === "D" ? true : (this.Access.quotation.edit ? false : true)}
                style={{ resize: "vertical", Height: '100% !important' }}
              />
            </Col>
          </FormGroup>
        </>
      });

    this.Views.Sumary && this._loadedDetail &&
      Tabs.push({
        collapse: 'sumary',
        title: 'SUMMARY',
        component: <>
          <VariablesCostingCard
            sumWhite={detailCalcs.sellingPrices.white}
            sumMedium={detailCalcs.sellingPrices.medium}
            sumDark={detailCalcs.sellingPrices.dark}
            sumSpecial={detailCalcs.sellingPrices.special}
          />
          <Row>
            <Col>
              {this.Access.quotation.edit &&
                <Button className="btn-fill" color="success" type="button"
                  onClick={event => this.handleSaveProto(event)}
                >
                  Save Changes
                </Button>}
            </Col>
            <Col>
              {this.Access.quotation.view && (this.Role === 'admin' ||
                this.Role === 'Consulting' ||
                this.Role === 'Approve Costings' ||
                this.Role === 'Account Manager' ||
                this.Role === 'Account Combos' ||
                this.Role === 'Costing Manager' ||
                (this.Role === 'Costing Junior' &&
                  this.state.CostingHeader.client.name === 'RALPH LAUREN'
                ) ||
                (this.Role === 'Costing Junior' &&
                  this.state.CostingHeader.client.name === 'LACOSTE'
                )
              ) &&
                <Button className="btn-fill" color="info" type="button"
                  onClick={e => {
                    e.preventDefault();
                    window.open(process.env.REACT_APP_IP + '/get_costing/?id=' + this.props.location.state.costingData)
                  }}
                >
                  Save as Excel
                </Button>}
            </Col>
            <Col>
              {this.Access.quotation.edit &&
                <Button className="btn-fill"
                  color="danger"
                  type="button"
                  onClick={event => {
                    event.preventDefault();
                    this.toggleApprovalModal();
                  }}
                >
                  Send for Approval
                </Button>}
            </Col>
            <Col>
              <Button className="btn-fill" color="info"
                type="button"
                onClick={() => {
                  generatePdfDocument(
                    this.state.detail,
                    detailCalcs,
                    this.state.CostingHeader.PathToSketch,
                    {
                      "costSheet": this.props.location.state.costingData,
                      "clientName": this.state.CostingHeader.client.name,
                      "clientAbbreviation": this.state.CostingHeader.client.abbreviation,
                      "season": this.state.CostingHeader.season.name,
                      "style": this.state.CostingHeader.style
                    }
                  )
                }}
              >
                CostSheet
              </Button>
            </Col>
          </Row>
        </>
      })

    return (<>
      <div className="blackdiv" id="blackdiv" style={this.state.loading ? { display: '' } : { display: 'none' }}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
        </div>
      </div>

      <Prompt message="Your changes will not be saved. Are you sure you want to leave?" />

      <Modal isOpen={this.state.ApprovalModal} toggle={this.toggleApprovalModal}>
        <div className="modal-header">
          <button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={this.toggleApprovalModal}>
            <i className="tim-icons icon-simple-remove"></i>
          </button>
          <h5 className="modal-title">Send for approval to manager?</h5>
        </div>
        <ModalBody>
          <p>approve?</p>
        </ModalBody>
        <ModalFooter>
          <Button color="secondary" onClick={this.toggleApprovalModal}>
            Close
          </Button>
          <Button color="primary" onClick={event => {
            event.preventDefault();
            this.sendtoApprove();
          }}>
            Send to Approve
          </Button>
        </ModalFooter>
      </Modal>

      <div className="react-notification-alert-container">
        <NotificationAlert ref="notificationAlert" />
      </div>

      <div className="content">
        {this._loadedHeader && this._loadedDetail && <CostingHeaderCard
          costingHeader={this.state.CostingHeader}
          detail={this.state.detail}
          quickCost={false}
          documents={headerFiles}
          sketchData={this.state.pathToSketchData}
          helper={this.CostingHeaderHelper}
          seasons={this.state.seasons}
          header={this.state.CostingHeader}
        />}
        {this.Role === 'Auditoria Externa' ? <></> :
          <><Form onSubmit={event => this.handleSubmit(event, detailCalcs.sellingPrices)}>
            {Tabs.length > 0 ? <QuotationTabs tabs={Tabs} /> : null}
          </Form></>}
      </div>
    </>)
  }
}

export default Quotation;