import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  Button,
  Label,
  Input,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

class matIncomes extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      productOrder: null,
      styles_po: null,
      filesForm: ["1"],
      selectedFile: null,
      uploadPO: false,
      uploadAllPO: false,
      resumePO: false,
      clientList: [],
      fileList: [],
      flagBuy: false,
      
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  uploadChangeFilePO = (event) => {
    let selectedClientPo = this.state.selectedClientPo;
    this.toggleLoading();
    for (const i in this.state.fileList) {
      const formData = new FormData();
      formData.append("file", this.state.fileList[0]);
      formData.append(
        "clients",
        this.state.flagBuy ? "-1" : this.state.clientList[i]
      );
      axios
        .post(process.env.REACT_APP_IP + `/po/create_po/`, formData, {
          headers: {
            Authorization: "Token " + this.userToken,
            "Content-Type": "multipart/form-data",
          },
        })
        .then(
          function() {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "success", "Upload File PO ");
          }.bind(this)
        )
        .catch(
          function(error) {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "warning", "Bad document format ");
          }.bind(this)
        );
    }
    this.toggleUploadPOModal();
    selectedClientPo.label = "";
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  openPo = (po_id) => {
    const path = "/admin/resume_po";
    this.props.history.push({
      pathname: path,
      state: {
        poId: po_id,
      },
    });
  };

  getPOS = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + `/po/get_my_po/?flag=U`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            productOrder: response.data.map((pos) => {
              return {
                client_id: pos.client_id,
                create_date: pos.created_at.split(" ", 1),
                delivery_place: pos.delivery_place,
                po_number: pos.po_number,
                po_id: pos.id,
                status: pos.status,
                actions: (
                  <>
                    <Button
                      color="info"
                      size="sm"
                      className="btn-icon btn-link"
                      onClick={() => {
                        this.openPo(pos.id);
                      }}
                    >
                      <i className="fas fa-search" />
                    </Button>
                    <Button
                      color="danger"
                      size="sm"
                      className="btn-icon btn-link"
                      onClick={() => {
                        this.deletePO(pos.id);
                      }}
                    >
                      <i className="fas fa-trash-alt" />
                    </Button>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  getPoResume = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
              `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`
          );
          this.notify("tr", "success", "Resume downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  toggleUploadAllPOsModal = () => {
    this.setState({
      uploadAllPO: !this.state.uploadAllPO,
    });
  };

  toggleUploadPOModal = () => {
    this.setState({
      uploadPO: !this.state.uploadPO,
    });
  };

  toggleGetResumeModal = () => {
    this.setState({
      resumePO: !this.state.resumePO,
    });
  };

  addClientToList = (clientId) => {
    const list = [...this.state.clientList];
    list.push(clientId.value);
    this.setState({
      clientList: list,
    });
  };

  addFileToList = (event) => {
    this.setState({ selectedFile: event.target.files[0] });
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  addEvent = () => {
    const listForm = [...this.state.filesForm];
    listForm.push("1");
    this.setState({
      filesForm: listForm,
    });
  };

  getDate = () => {
    var today = new Date();
    var day = parseInt(today.getDate());
    var month = parseInt(today.getMonth() + 1);
    var year = parseInt(today.getFullYear());
    var date = day + "/" + month + "/" + year;
    this.setState({ date: date });
  };

  updateCurrency = () => {
    this.getCurrencyChange()
  }

  getCurrencyChange = () => {
    axios.get(
      `https://currency-converter-by-api-ninjas.p.rapidapi.com/v1/convertcurrency`,
      { 
        params: { have: "USD", want: "GTQ", amount: "1" },
        headers: {
        "X-RapidAPI-Key": "fc5a59240cmshadf1e1fff76c353p1d6b57jsnbc64aaaef1ec",
        "X-RapidAPI-Host": "currency-converter-by-api-ninjas.p.rapidapi.com",
        }
      }
    ).then(function(response) {
      const { data } = response;
      setTimeout(function() { //Start the timer
        this.setState({
          newAmount: data.new_amount
        })
        this.notify("tr", "success", "Exchange Updated!");
    }.bind(this), 800)
    }.bind(this))
  }

  resetModal = () => {
    const listForm = ["1"];
    this.setState({
      filesForm: listForm,
    });
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getDate();
    this.getPOS();
    this.getCurrencyChange();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const { loading, productOrder } = this.state;
    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Row className="justify-content-center mt-5">
          <Col md={{ size: 12, order: 2 }}>
            <Card>
              <CardBody>
                <CardHeader>
                  <Row>
                    <Col sm="9">
                      <h1>Ingreso de Materiales</h1>
                    </Col>

                    <CardBody>
                      <Row>
                        <Col>
                          <Row>
                            <Col>
                              <Label>Orden de Compra</Label>
                              <Select
                                options={this.state.clients}
                                //value={selectedClient ? selectedClient : null}
                                onChange={(o) => {
                                  this.setState({ clientSelected: o.value });
                                }}
                              />
                            </Col>
                            <Col>
                              <Label for="exampleDate">Fecha</Label>
                              <Input
                                type="readonly"
                                name="date"
                                disabled={true}
                                id="exampleDate"
                                value={this.state.date}
                                placeholder={this.state.date}
                              />
                            </Col>
                          </Row>
                          <br />
                          <Row>
                            <Col sm="6">
                              <Label>No. de Envo</Label>
                              <Input
                                onChange={(o) => {
                                  this.loadFormColor({
                                    ...this.state.ColorsForm,
                                    client: o.value,
                                  });
                                }}
                              />
                            </Col>
                          </Row>
                        </Col>
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                              this.getMaterialOrder();
                            }}
                            className="float-left"
                          >
                            Search <i className="fa fa-search"></i>
                          </Button>
                        </Col>
                        <Col sm="1" style={{ textAlign: "center", display:'flex', flexDirection:'column', justifyContent: 'center', alignItems: 'center' }}>
                          <Label>Tipo de cambio</Label>
                          <Input
                            type="readonly"
                            id="exampleDate"
                            disabled= {true}
                            placeholder={this.state.newAmount}
                            value={this.state.newAmount}
                            style={{ textAlign: "center" }}
                          />
                          <br />
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            onClick={() => {
                              this.updateCurrency();
                            }}
                            className="float-left"
                          >
                            <i className="tim-icons icon-refresh-01"></i>
                          </Button>
                        </Col>
                      </Row>
                    </CardBody>
                    <br></br>
                    {productOrder && (
                      <>
                        <Card className={`${pageMode.bg} border`}>
                          <CardBody>
                            <ReactTable
                              responsive
                              defaultPageSize={5}
                              data={productOrder}
                              filterable
                              showPaginationBottom={true}
                              className="-striped -highlight primary-pagination"
                              columns={[
                                {
                                  Header: "CPO",
                                  accessor: "po_number",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["po_number"],
                                    }),
                                  filterAll: true,
                                  width: 125,
                                },
                                {
                                  Header: "CODIGO",
                                  accessor: "delivery_place",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["delivery_place"],
                                    }),
                                  filterAll: true,
                                  width: 125,
                                },
                                {
                                  Header: "DESCRIPCION",
                                  accessor: "po_number",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["po_number"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "U. PEDIDAS",
                                  accessor: "delivery_place",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["delivery_place"],
                                    }),
                                  filterAll: true,
                                  width: 125,
                                },
                                {
                                  Header: "U. RECIBIDAS",
                                  accessor: "po_number",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["po_number"],
                                    }),
                                  filterAll: true,
                                  width: 125,
                                },
                                {
                                  Header: "UNIDADES",
                                  accessor: "delivery_place",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["delivery_place"],
                                    }),
                                  filterAll: true,
                                  width: 125,
                                },
                                {
                                  Header: "COSTO",
                                  accessor: "po_number",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["po_number"],
                                    }),
                                  filterAll: true,
                                  width: 125,
                                },
                              ]}
                            />
                          </CardBody>
                        </Card>
                      </>
                    )}
                  </Row>
                </CardHeader>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}
export default matIncomes;
