import React from "react";
import axios from "axios";
import Select from "react-select";
import ReactDatetime from "react-datetime";
import "react-datetime/css/react-datetime.css";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Label,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";



class FabricReport extends React.Component {
  constructor() {
    super();
    this.state = {
      vTabs: "vt1",
      loading: false,
      date: '',
      date1: '',
      modalReport: {
        isOpen: false,
        index: null,
        edit: false,
        matIndex: null,
        name: "",
      },
      clients: [],
      reports: [],
      clientSelect: null,
      season: [],
      seasonSelect: null,
      dm: [],
      dmSelect: null,
      selectedFile: null,
      tableDM: [],
      fileList: [],
    };
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  addFileToList = (event) => {
    this.setState({selectedFile: event.target.files[0]});
    // this.state.selectedFile = event.target.files[0];
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  changepage = (data, data2) => {
    console.log(this.state.styleSelected);

    const path = "/admin/account_components";
    this.props.history.push({
      pathname: path,
      state: {
        styleSelected: data2,
        stylesCombo: data,
        styles_po: this.state.styles_po,
        flag: true,
      },
    });
  };

  handleReview = (header_id) => {
    const formData = new FormData();

    let id = header_id;
    formData.append("style", id.id);
    axios
      .post(this.costingURL + "/combos_components/get_resume/", formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function (response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              stylesCombo: response.data["combos"],
            });
          this.getInfoStyle(id.id, response.data["combos"]);
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function (error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  handleSelectChange = (event) => {
    let opts = [],
      opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
        console.log("opt", opt.value);
      }
    }

    this.setState({ selectedStyle: opts });
  };


  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getSeasons();
    this.getDMReport();
    this.getReports();
  }

  toggleAddMaterial = (name) => {
    const { modalReport } = this.state;
    this.setState({
      modalReport: {
        ...modalReport,
        isOpen: !modalReport.isOpen,
        name: name,
      },
    });
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  getDMReport = () => {
    axios
      .get(process.env.REACT_APP_IP + "/fabrics/get_dms_for_report/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._ClientsLoaded = true;
          this._isMounted &&
            this.setState({
              dm: response.data.map((index) => {
                return {
                  value: index.id,
                  label: index.dm,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );
  };

  getClients = () => {
    axios
      .get(process.env.REACT_APP_IP + "/clients/get_all_clients/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._ClientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((index) => {
                return {
                  value: index.id,
                  label: index.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );

  };

  getSeasons = () => {
    axios
      .get(process.env.REACT_APP_IP + "/seasons/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingSeasons = true;
          this._isMounted &&
            this.setState({
              season: response.data.map((index) => {
                return {
                  value: index.id,
                  label: index.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error);
        }.bind(this)
      );
  };

  GETREPORTDM = (dm) => {
    axios
      .get(
        process.env.REACT_APP_IP + `/fabrics/get_historic_prices_dm/?dm=${dm}`,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function (response) {
          this._loadingSeasons = true;
          this._isMounted &&
            this.setState({
              tableDM: response.data,
            });
          this.openDmTable(dm);
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error);
        }.bind(this)
      );
  };


  // Gets all the reports 
  getReports = () => {
    axios.get(process.env.REACT_APP_IP + "/user/get_reports_for_users/", {
      headers: { Authorization: "Token " + this.userToken },
    })
      .then(
        function (response) {
          this._ClientsLoaded = true;
          this._isMounted &&
            this.setState({
              reports: response.data.map((index) => {
                return {
                  id: index.id,
                  name: index.name,
                  icon: index.icon,
                  link: index.link,
                };
              }),
            });
          console.log("los logs", this.state.reports);
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "getClients");
        }.bind(this)
      );
  }

  openDmTable = (dm) => {
    const path = "/admin/prices_for_dms";
    this.props.history.push({
      pathname: path,
      state: {
        history: this.state.tableDM,
        dm: dm,
      },
    });
  };

  downloadReport = (client, season) => {
    axios.get(
      process.env.REACT_APP_IP + `/export_fabrics_costing_report/?client=${client}&season=${season}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.componentDidMount();
      this.notify("tr", "success", "Report downloaded!");
      this.toggleAddMaterial()
      window.open(process.env.REACT_APP_IP + `/export_fabrics_costing_report/?client=${client}&season=${season}`)
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Report');
    }.bind(this));
  }

  downloadClientReport = (client, start, end) => {
    axios.get(
      process.env.REACT_APP_IP + `/admin/export_production_by_line/?start_date=${start}&end_date=${end}&client=${client}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.componentDidMount();
      this.notify("tr", "success", "Report downloaded!");
      this.toggleAddMaterial()
      window.open(process.env.REACT_APP_IP + `/admin/export_production_by_line/?start_date=${start}&end_date=${end}&client=${client}`)
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Report');
    }.bind(this));
  }

  clientCountReport = (client) => {
    axios.get(
      process.env.REACT_APP_IP + `/admin/get_prices_for_styles/?client=${client}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.componentDidMount();
      this.notify("tr", "success", "Report downloaded!");
      this.toggleAddMaterial()
      window.open(process.env.REACT_APP_IP + `/admin/get_prices_for_styles/?client=${client}`)
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Report');
    }.bind(this));
  }

  columbiasReport = (month) => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/administration/get_columbia_report/?month=${month}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.componentDidMount();
      this.notify("tr", "success", "Report downloaded!");
      this.toggleLoading()
      this.toggleAddMaterial()
      window.open(process.env.REACT_APP_IP + `/administration/get_columbia_report/?month=${month}`)
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Report');
      this.toggleLoading()
    }.bind(this));
  }

  getReportBuys = (month, year) => {
    this.toggleLoading();
    axios.get(
      process.env.REACT_APP_IP + `/admin/get_report_buys/?month=${month}&year=${year}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.componentDidMount();
      this.notify("tr", "success", "Report downloaded!");
      this.toggleLoading()
      window.open(process.env.REACT_APP_IP + `/admin/get_report_buys/?month=${month}&year=${year}`)
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Report');
      this.toggleLoading()
    }.bind(this));
  }

  getReportSecsThirds = (event) => {
    this.toggleLoading();
    const formData = new FormData();
    formData.append("file", this.state.fileList[0]);
    axios
      .post(
        process.env.REACT_APP_IP + `/bom_header/get_report_2nd_3rd/`,
        formData,
        {
          headers: {
            Authorization: "Token " + this.userToken,
            "Content-Type": "multipart/form-data",
          },
          method: 'GET',
          responseType: 'blob',
        }
      )
      .then(
        function (response) {

          this.toggleLoading();
          this.componentDidMount();
          //window.open(process.env.REACT_APP_IP + window.URL.createObjectURL(new Blob([response.data])))

          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', `Reporte 2das y 3ras.xlsx`);
          document.body.appendChild(link);
          link.click();
          console.log(response.headers)
          this.notify("tr", "success");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.componentDidMount();
          //console.log(response.data)
          this.notify("tr", "warning", "Bad document format ");
        }.bind(this)
      );
  }

  stylePerSeason = () => {
    const season = this.state.seasonSelect
    const client = this.state.clientSelect
    this.toggleLoading();
    axios.get(
      process.env.REACT_APP_IP + `/admin/get_report_season/?season=${season}&client=${client}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.componentDidMount();
      this.notify("tr", "success", "Report downloaded!");
      this.toggleLoading()
      window.open(process.env.REACT_APP_IP + `/admin/get_report_season/?season=${season}&client=${client}`)
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'Report');
      this.toggleLoading()
    }.bind(this));
  }

  addFileToList = (event) => {
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
      selectedFile:event.target.files[0]
    });
  };


  render() {
    const { loading, modalReport, clients, season, dm } = this.state;
    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (

      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Modal isOpen={modalReport.isOpen} toggle={this.toggleAddMaterial}>
          <div className="modal-header">
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={this.toggleAddMaterial}
            >
              <i className="tim-icons icon-simple-remove"></i>
            </button>
            <h5 className="modal-title">
              {modalReport.name === "DM Prices" ? "DM" : "REPORT"}
            </h5>
          </div>
          <ModalBody>
            {modalReport.name === "DM Prices" ? (
              <FormGroup row>
                <Label sm={3}>DM</Label>
                <Col sm={9}>
                  {
                    <Select
                      options={dm}
                      onChange={(o) => {
                        this.setState({
                          dmSelect: o.label,
                        });
                      }}
                    />
                  }
                </Col>
              </FormGroup>
            ) : modalReport.name === "Columbia's Report" ? (
              <>
                <h5 className="modal-title">
                  Ingrese el numero de mes
                </h5>
                <Input
                  type="number"
                  min="1"
                  max="12"
                  placeholder="Numero de mes"
                  id="columbia-month">
                </Input>
                <Button
                  color="primary"
                  onClick={() => this.columbiasReport(document.getElementById("columbia-month").value)}
                >
                  DOWNLOAD REPORT
                </Button>
              </>
            ) : modalReport.name === "Gasto de Compras" ? (
              <>
                <Col md='6'>
                  <FormGroup>
                    <Label>Mes</Label>
                    <Input
                      type="number"
                      min="1"
                      max="12"
                      placeholder="Numero de mes"
                      id="bought-month">
                    </Input>
                  </FormGroup>
                </Col>
                <Col md='6'>
                  <FormGroup>
                    <Label>Ao</Label>
                    <Input
                      type="number"
                      min="2005"
                      max="2025"
                      placeholder="Numero de ao"
                      id="bought-year">
                    </Input>
                  </FormGroup>
                </Col>
              </>
            ) : modalReport.name === "Production by Line" ? (
              <>
                <FormGroup row>
                  <Label sm={3}>CLIENT</Label>
                  <Col sm={9}>
                    {
                      <Select
                        options={clients}
                        onChange={(o) => {
                          this.setState({
                            clientSelect: o.value,
                          });
                        }}
                      />
                    }
                  </Col>
                </FormGroup>
                <Col md='6'>
                  <FormGroup>
                    <Label>Fecha Inicio</Label>
                    <ReactDatetime
                      inputProps={{
                        className: "form-control",
                      }}
                      timeFormat={false}
                      value={this.state.date}
                      onChange={value => {

                        this.setState({
                          date: `${(value._d).getFullYear()}-${(value._d).getMonth() + 1}-${(value._d).getDate()}`
                        })
                      }}
                      formNoValidate
                    />
                  </FormGroup>
                </Col>
                <Col md='6'>
                  <Label>Fecha Final</Label>
                  <ReactDatetime
                    inputProps={{
                      className: "form-control",
                    }}
                    timeFormat={false}
                    value={this.state.date1}
                    onChange={value => {

                      this.setState({
                        date1: `${(value._d).getFullYear()}-${(value._d).getMonth() + 1}-${(value._d).getDate()}`
                      })
                    }}
                    formNoValidate
                  />
                </Col>
              </>

            ) : modalReport.name === 'Prices for Styles' ? (
              <>
                <FormGroup row>
                  <Label sm={3}>CLIENT</Label>
                  <Col sm={9}>
                    {
                      <Select
                        options={clients}
                        //value={materialForm.material}
                        onChange={(o) => {
                          this.setState({
                            clientSelect: o.value,
                          });
                        }}
                      />
                    }
                  </Col>
                </FormGroup>

              </>
            ) : modalReport.name === 'Styles per Season' ? (
              <>
                <FormGroup row>
                  <Label sm={3}>CLIENT</Label>
                  <Col sm={9}>
                    {
                      <Select
                        options={clients}
                        onChange={(o) => {
                          this.setState({
                            clientSelect: o.value,
                          });
                        }}
                      />
                    }
                  </Col>
                </FormGroup>
                <FormGroup row>
                  <Label sm={3}>SEASON</Label>
                  <Col sm={9}>
                    <Select
                      options={season}
                      onChange={(o) => {
                        this.setState({
                          seasonSelect: o.value,
                        });
                      }}
                    />
                  </Col>
                </FormGroup>

              </>

            ) : modalReport.name === 'Report sec and third' ? (
              <>
                <FormGroup row>
                  <Label sm={6}>Upload File</Label>
                  <Col sm={6}>
                    <FormGroup
                      id="formGroup_pathToTechpack"
                      className="text-center"
                    >
                      <Button
                        className="btn-simple"
                        color="info"
                        size="sm"
                      >
                        <input
                          type="file"
                          onChange={this.addFileToList}
                          name="pathToCP_POS"
                          id="input_pathToTechpack"
                          multiple="multiple"
                        />
                        <i className="tim-icons icon-cloud-upload-94" />
                        &nbsp;  Upload File
                      </Button>
                    </FormGroup>
                  </Col>
                </FormGroup>
              </>

            ) : (
              <>
                <FormGroup row>
                  <Label sm={3}>CLIENT</Label>
                  <Col sm={9}>
                    {
                      <Select
                        options={clients}
                        onChange={(o) => {
                          this.setState({
                            clientSelect: o.value,
                          });
                        }}
                      />
                    }
                  </Col>
                </FormGroup>
                <FormGroup row>
                  <Label sm={3}>SEASON</Label>
                  <Col sm={9}>
                    <Select
                      options={season}
                      onChange={(o) => {
                        this.setState({
                          seasonSelect: o.value,
                        });
                      }}
                    />
                  </Col>
                </FormGroup>
              </>
            )}
          </ModalBody>

          <ModalFooter>
            <Button color="secondary" onClick={this.toggleAddMaterial}>
              Close
            </Button>
            {modalReport.name === "DM Prices" ? (
              <Button
                color="primary"
                onClick={() => {
                  this.GETREPORTDM(this.state.dmSelect);
                }}
              >
                SEARCH
              </Button>
            ) : modalReport.name === "Production by Line" ? (
              <Button
                color="primary"
                onClick={() => {
                  this.downloadClientReport(
                    this.state.clientSelect,
                    this.state.date,
                    this.state.date1
                  );
                }}
              >
                DOWNLOAD
              </Button>
            ) : modalReport.name === "Prices for Styles" ? (
              <Button
                color="primary"
                onClick={() => {
                  this.clientCountReport(this.state.clientSelect);
                }}
              >
                DOWNLOAD
              </Button>
            ) : modalReport.name === "Gasto de Compras" ? (
              <Button
                color="primary"
                onClick={() => this.getReportBuys(document.getElementById("bought-month").value, document.getElementById("bought-year").value)}

              >
                DOWNLOAD
              </Button>
            ) : modalReport.name === "Report sec and third" ? (
              <Button
                color="primary"
                onClick={(event) => {
                  this.getReportSecsThirds(event);
                }}
              >
                Generate Report
              </Button>
            ) : modalReport.name === 'Styles per Season' ? (
              <>
                <Button
                  color="primary"
                  onClick={() => {
                    this.stylePerSeason();
                  }}
                >
                  DOWNLOAD
                </Button>
              </>
            ) : (
              <Button
                color="primary"
                onClick={() => {
                  this.downloadReport(
                    this.state.clientSelect,
                    this.state.seasonSelect
                  );
                }}
              >
                DOWNLOAD
              </Button>
            )}
          </ModalFooter>
        </Modal>

        <Row>
          <Col className="ml-auto mr-auto" md="8">
            <Card className="card-plain card-subcategories">
              <CardHeader>
                <CardTitle className="text-center mt-5" tag="h4">
                  Reports
                </CardTitle>
                <br />
              </CardHeader>
              <CardBody>
                <Nav className="nav-pills-info nav-pills-icons justify-content-center" pills>
                  {this.state.reports.map((index, key) => (
                    <NavItem>
                      {//reportes que no requieren de un modal para ser utilizados
                        index.name.includes("Jersey") || index.name.includes("Inventory") || index.name.includes("Libras") || index.name.includes("SAM's") || index.name.includes("Rollos")?
                          <a href={`${process.env.REACT_APP_IP}${index.link}`} target="_blank" rel="noopener noreferrer" download>
                            <NavLink data-toggle="tab" >
                              <i className={`tim-icons ${index.icon}`} />
                              {index.name}
                            </NavLink>
                          </a>
                          :
                          <NavLink data-toggle="tab" onClick={() => this.toggleAddMaterial(index.name)}>
                            <i className={`tim-icons ${index.icon}`} />
                            {index.name}
                          </NavLink>
                      }
                    </NavItem>
                  ))}
                </Nav>
              </CardBody>
            </Card>
          </Col>
        </Row>

      </div>
    );
  }
}
export default FabricReport;
