import React from "react";
import axios from "axios";
import NotificationAlert from "react-notification-alert";
import { uploadFileToS3,downloadFileFromS3 } from '../helpers/awsS3.js';
import PatternTable from '../components/PatternCustomComponents/PatternTable';
import '../components/QuotationElements/loader.min.css';

import {
  Row,
  Col,
} from "reactstrap";

class Pattern extends React.Component {

  _isMounted = false;
  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';

  constructor() {
    super();
    this.state = { 
      file: undefined,
      costingHeaders: [],
      detail: {},
      selectedHeader: -1,
      loader: false
    };
    this.handleFileChangeMarkerUpload = this.handleFileChangeMarkerUpload.bind(this);
    this.handleFileChangePatternDownload = this.handleFileChangePatternDownload.bind(this);
    this.handleFileChangePatternUpload = this.handleFileChangePatternUpload.bind(this);
  }

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {    
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    //console.log(response,message,task)
    if (response.status!==undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      }else{
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };

    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  getCostingHeaders = () => {
    let jsonobj = [];
    axios.get(process.env.REACT_APP_IP + '/costingHeader/',
    {
      headers:{
        "Authorization":"Token "+this.userToken
      }
    })
    .then( function(response) {
      response.data.map( (row) => {
        if ((!row.markerFlag) || (!row.patternFlag)) {
          jsonobj.push(row);
        }
        return 1;
      });
      this._isMounted && this.setState({costingHeaders: jsonobj});
    }.bind(this)).catch( function(error) {
      this.handleError(error);
    }.bind(this));
  }

  getCostingDetail = (detail_id) => {    
    this.setState({ loader: true });
    axios.get(process.env.REACT_APP_IP + '/costingDetail/' + detail_id + '/', 
      { headers:{
          "Authorization": "Token " + this.userToken
      } }
    ).then(function(detailResponse) {
      const pathToPattern = detailResponse.data.pathToPattern;
      let url=process.env.REACT_APP_IP + '/costingDetail/download/?key='+pathToPattern;
      let config = {
        params: {
          "key": pathToPattern
        },
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
      downloadFileFromS3(
        url,
        config
      ).then(function(response){        
        this.setState({ loader: false });
        const result = {
          file: response,
        };
        window.open(result.file);
        this.notify("tr", "success", "Pattern downloaded!");  
      }.bind(this)).catch(function(error){
        this.setState({ loader: false });
        this.handleError(error);
      }.bind(this));
    }.bind(this)).catch(function(error){
      this.setState({ loader: false }); 
      this.handleError(error);
    });
  }

  isNumberKey(evt){
    var theEvent = evt || window.event;
    var key = theEvent.keyCode || theEvent.which;
    if (theEvent.type === 'paste') {
      key = theEvent.clipboardData.getData('text/plain');
    } else {
      key = String.fromCharCode(key);
    }
    var regex = /[0-9]|\./;
    if( !regex.test(key) ) {
      theEvent.returnValue = false;
      if(theEvent.preventDefault) theEvent.preventDefault();
    }
  }

  handleFileChangePatternUpload = (event) => {
    this.setState({ loader: true });
    const detailId = event.target.dataset.detailid;
    const file = event.target.files[0];
    const fileData = new FormData();
    fileData.append("file", file);
    fileData.append("id", detailId);
    const header = { headers: {
      "Authorization": "Token " + this.userToken,
      "Content-Type": "multipart/form-data"
    } }
    uploadFileToS3(
      process.env.REACT_APP_IP + '/costingDetail/post_pattern/',
      header,
      fileData,
      'file',
      ["zip", "rar", "7z"]
    ).then( function() {
      this.setState({ loader: false });
      this.notify("tr", "success", "Pattern upload");
      this.getCostingHeaders();
    }.bind(this)).catch(function(error) {
      this.setState({ loader: false });
      this.handleError(error);
    }.bind(this));
  }

  handleFileChangePatternDownload = (detail_id) => {
    this.getCostingDetail(detail_id)
  }

  handleFileChangeMarkerUpload = (event) => {
    this.setState({ loader: true });
    const detailId = event.target.dataset.detailid;
    const file = event.target.files[0];

    const fileData=new FormData();
    fileData.append("file", file);
    fileData.append("id", detailId);

    const fileData2=new FormData();
    fileData2.append("marker", file);
    fileData2.append("detail_id", detailId);

    const header = { headers: {
      "Authorization": "Token " + this.userToken,
      "Content-Type": "multipart/form-data"
    } }
    
    axios.post(
      process.env.REACT_APP_IP + "/costingDetail/parse_marker/",
      fileData2,
      header
    ).then( function() {
      uploadFileToS3(
        process.env.REACT_APP_IP + '/costingDetail/post_marker/',
        header,
        fileData,
        'file',
        ["xlsx", "xls", "XLSX", "XLS"]
      ).then( function() {
        this.setState({ loader: false });
        this.getCostingHeaders();
        this.notify("tr", "success", "Marker Loaded and parsed");
      }.bind(this)).catch(function(error) {
        this.handleError(error,"upload marker");
        this.setState({ loader: false });
      }.bind(this));
    }.bind(this)).catch(function(error) {
      this.handleError(error,"parse marker");
      this.setState({ loader: false });
    }.bind(this))
  }

  componentDidMount() {
    this._isMounted = true;
    this.getCostingHeaders();
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  checkPageMode = () => {
    let pageMode=document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    let modalClass;
    switch(pageMode){
      case true:
        bgclass="bg-ligh";
        headerclass="";
        modalClass="modal-white";
        break;
      default:
        bgclass="bg-dark";
        headerclass="text-primary";
        modalClass="modal-black";
        break;
    }
    return {bg: bgclass,hc: headerclass, mc: modalClass};
  }

  returnFlag = (header_id) => {
    let data = new FormData();
    data.append('id',header_id);
    data.append('delete_task',1);
    axios.post(
      process.env.REACT_APP_IP + '/administration/return/',
      data,
      {headers:{"Authorization":"Token " + this.userToken}}
    ).then(function(){
      this._isMounted && this.notify("tr", "success", "Task Removed!");
      this.componentDidMount();
    }.bind(this)).catch(function(error){
      this.handleError(error,"returnFlag");
    }.bind(this))
  }

  render() {
    const pageMode=this.checkPageMode();
    let inverted = pageMode.bg==="bg-ligh" ? 'inverted' : '';
    return (<>
      <div className="blackdiv" id="blackdiv" style={this.state.loading?{display:'flex'}:{display:'none'}}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{minHeight:400}}><p>&nbsp;</p></div>
        </div>
      </div>

      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert"/>
        </div>
        <Row>
          <Col md="12">
            <PatternTable
              dataTable = {this.state.costingHeaders}
              handleFileChangePatternUpload = {this.handleFileChangePatternUpload}
              handleFileChangePatternDownload = {this.handleFileChangePatternDownload}
              handleFileChangeMarkerUpload = {this.handleFileChangeMarkerUpload}
              handleRemoveTaks={this.returnFlag}
            />
          </Col>
        </Row>
      </div>
    </>);
  }
}

export default Pattern;
