import React from "react";
import axios from 'axios';
import NotificationAlert from "react-notification-alert";
import '../components/MaterialsCustomComponents/tabs.css';
import ReactTable from "react-table";
import { matchSorter } from 'match-sorter';
import {
  Button,
  Card,
  CardBody,
} from "reactstrap";

export default class PoHistory extends React.Component {
  userToken = localStorage.getItem('userToken') || '';
  costingURL = process.env.REACT_APP_IP;
  Role = localStorage.getItem('role') || '';
  styleId = 0
  defaultEmbellishmentLine = {
    id: 0,
    color: 0,
    name: '',
    embroidery_description: '',
    laser_description: '',
    washed_description: '',
    ironing_description: '',
    serigraphy_description: '',
    sublimate_description: '',
    materials: []
  }
  _isMounted = false;

  constructor() {
    super();
    this.state = {
      loading: false
    }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>" && this._isMounted) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  closePO = (id) => {
    this.toggleLoading();
    const formData = new FormData();
    formData.append("po_id", id);
    axios.post(
      process.env.REACT_APP_IP + `/po/close_po/`,
      formData,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function () {
      this.notify("tr", "success", "PO Closed");
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      this.componentDidMount();
      this.notify("tr", "warning", "Error Closing PO");
    }.bind(this))
  }

  _loadedPoIngested = false;
  getPoIngested = () => {
    this.toggleLoading();
    axios.get(
      process.env.REACT_APP_IP + `/po/get_all_my_po/`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({
        poIngested: response.data.map(
          posI => {
            return {
              id: posI.id,
              //created_at: posI.created_at.split(' ',1),
              delivery_place: posI.clients_delivery_place,
              po_number: posI.po_number,
              status: `${posI.status === "U" ? 'Uploaded' : posI.status === "I" ? 'Ingested' : 'Closed'}`,
              actions:
                <>
                  {posI.status === "I" &&
                    <Button
                      color="info"
                      size="sm"
                      onClick={() => this.closePO(posI.id)}>
                      Close <i class="fas fa-check"></i>
                    </Button>}
                </>
            }
          }
        )
      })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
    }.bind(this))
  }

  _loadedPoClosed = false;
  getPoClosed = () => {
    this.toggleLoading();
    axios.get(
      process.env.REACT_APP_IP + `/po/get_my_po/?flag=C`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({
        poClosed: response.data.map(
          posI => {
            return {
              id: posI.id,
              created_at: posI.created_at.split(' ', 1),
              delivery_place: posI.delivery_place.split(' ')[1],
              po_number: posI.po_number,
              clients_id: posI.clients_id,
            }
          }
        )
      })
      //this.toggleLoading();
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
    }.bind(this))
  }


  componentDidMount() {
    this._isMounted = true;
    this.getPoIngested();
    this.getPoClosed();
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      })
    }
  }

  render() {
    const {
      loading,
      poIngested,
    } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (<div className="content">
      <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
        </div>
      </div>

      <div className="react-notification-alert-container">
        <NotificationAlert ref="notificationAlert" />
      </div>
      <Card >
        <CardBody>
          <h1>POs On Progress</h1>
          <ReactTable
            responsive
            data={poIngested}
            rows={10}
            defaultPageSize={10}
            filterable
            defaultFilterMethod={(filter, row,) => {
              const id = filter.pivotId || filter.id
              //console.log(filter,row[id],id);
              const filterValue = filter.id === "codigo" ? parseFloat(filter.value) : filter.value.toLowerCase();
              const filterRow = id === "codigo" ? row[id] : row[id].toLowerCase();
              return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
            }}
            columns={[
              {
                Header: "PO Number",
                accessor: "po_number",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["po_number"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              },

              {
                Header: "Delivery Place",
                accessor: "delivery_place",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["delivery_place"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              },
              {
                Header: "Status",
                accessor: "status",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["status"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              },

              {
                Header: "ACTION",
                accessor: "actions",
                sortable: false,
                filterable: false,
                width: 150
              }
            ]}
            showPaginationTop
            showPaginationBottom={false}
            defaultSorted={[
              {
                id: "codigo",
                desc: true
              }
            ]}
            className="-striped -highlight primary-pagination"
          /></CardBody></Card>

      {/*<>
          <Nav tabs>
            <NavItem>
              <NavLink
                className={tab === '1' ? 'active' : ''}
                onClick={() => { this.toggle('1'); }}
                style={pageMode.bg === "bg-ligh" ? {
                  color: 'black',
                  borderColor: 'black',
                } : {}}
              >
                On Progress
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={tab === '2' ? 'active' : ''}
                onClick={() => { this.toggle('2'); }}
                style={pageMode.bg === "bg-ligh" ? {
                  color: 'black',
                  borderColor: 'black',
                } : {}}
              >
                Closed
              </NavLink>
            </NavItem>

          </Nav>
          this._loadedPoIngested &&

            <TabContent activeTab={tab}>

              <TabPane tabId="1">
                <h1>POs On Progress</h1>
                <ReactTable responsive
                  data={poIngested}
                  filterable
                  defaultFilterMethod={(filter, row,) => {
                    const id = filter.pivotId || filter.id
                    console.log(filter,row[id],id);
                    const filterValue = filter.id === "codigo" ? parseFloat(filter.value) : filter.value.toLowerCase();
                    const filterRow = id === "codigo" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
                  }}
                  columns={[
                    {
                      Header: "PO Number",
                      accessor: "po_number",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["po_number"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    
                    {
                      Header: "Delivery Place",
                      accessor: "delivery_place",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["delivery_place"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "Status",
                      accessor: "status",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["status"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    
                    {
                      Header: "ACTION",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 150
                    }
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true
                    }
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </TabPane></TabContent>}

          this._loadedPoClosed &&
            <TabContent activeTab={tab}>

              <TabPane tabId="2">

                <h1>POs Closed</h1>
                <ReactTable responsive
                  data={poClosed}
                  filterable
                  defaultFilterMethod={(filter, row,) => {
                    const id = filter.pivotId || filter.id
                    const filterValue = filter.id === "codigo" ? parseFloat(filter.value) : filter.value.toLowerCase();
                    const filterRow = id === "codigo" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
                  }}
                  columns={[
                    {
                      Header: "PO Number",
                      accessor: "po_number",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["po_number"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "Client",
                      accessor: "clients_id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["clients_id"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "Delivery Place",
                      accessor: "delivery_place",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["delivery_place"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "Created At",
                      accessor: "created_at",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["created_at"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true
                    }
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </TabPane></TabContent>}
                </>*/}



    </div>);
  }
}
