import React from "react";
import axios from 'axios';
import ReactWizard from "react-bootstrap-wizard";
import NotificationAlert from "react-notification-alert";
import { downloadFileFromS3 } from '../helpers/awsS3';

// reactstrap components
import {
  Col,
  Row,
  Input,
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from "reactstrap";

// wizard steps
import Step1 from "../components/FormWizard/Step1.jsx";
import Step2 from "../components/FormWizard/Step2.jsx";
import Step3 from "../components/FormWizard/Step3.jsx";


class Wizard extends React.Component {

  constructor() {
    super();
    this.state = {
      bom_id: 0,
      rejectModal: false,
      loading: false,
      rejectedColors: [],
      colorsToSend: [],
      tableHeaders: undefined,
      bomsHeaders: [],
      header: {
        client: "",
        client_id: 0,
        costing_id: 0,
        description: "",
        cutting_cost: 0,
        sewing_cost: 0,
        thread: 0,
        packing: 0,
        serigraphy: 0,
        sms_flag: false,
        sms_multiplier: 0,
        bom_style: "",
        return_flag: false,
        embellishment_description: []
      },
    }
  }

  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;

  steps = [
    {
      stepName: "Same Consumptions",
      stepIcon: "tim-icons icon-single-02",
      component: Step1,
    },
    {
      stepName: "Materials X Colors",
      stepIcon: "tim-icons icon-settings-gear-63",
      component: Step2,
    },
    {
      stepName: "Colors and Consumptions",
      stepIcon: "tim-icons icon-delivery-fast",
      component: Step3,
    },
  ];

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>" && this._isMounted) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  _techPackLoaded = false;
  downloadTechPack = (filekye) => {
    //console.log('download techpack', filekye);
    //event.preventDefault();
    let url = "";
    url = process.env.REACT_APP_IP + '/costingHeader/download/?key=' + filekye;
    //url = process.env.REACT_APP_IP + '/bom_header/download/?key=' + filekye;
    let config = {
      params: { "key": filekye },
      headers: {
        "Authorization": "Token " + this.userToken
      }
    }
    downloadFileFromS3(url, config).then(function (response) {
      const result = { file: response };
      window.open(result.file);
      this.notify("tr", "success", " downloaded!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'download file');
    }.bind(this));

  }

  showHeaderInfo = (headerId) => {
    this.toggleLoading();
    axios.get(
      this.costingURL + `/bom_header/${headerId}/`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this._selectedHeader = true;
      const {
        header
      } = this.state;
      const {
        data
      } = response;
      //Aaqui vamos a intentar llenar todo,, dije intentar
      this._isMounted && this.setState({
        //selectedCostSheet: response.data,
        header: {
          ...header,
          approvalStatus: data.approvalStatus,
          bom_style: data.bom_style,
          description: data.description,
          sms_flag: data.sms_flag,
          sms_multiplier: data.sms_multiplier,
          cutting_cost: data.cutting_cost.toFixed(4),
          sewing_cost: data.sewing_cost.toFixed(4),
          thread: data.thread.toFixed(4),
          packing: data.packing.toFixed(4),
          serigraphy: data.serigraphy.toFixed(4),
          return_flag: data.return_flag,
          production_techpack: data.production_techpack,
          embellishment_description: data.embellishment_description.map(embellishment_description => {
            return {
              id: embellishment_description.id,
              color: embellishment_description.color_id,
              value: `${embellishment_description.color}`,
              embroidery_description: embellishment_description.embroidery_description,
              ironing_description: embellishment_description.ironing_description,
              laser_description: embellishment_description.laser_description,
              serigraphy_description: embellishment_description.serigraphy_description,
              sublimate_description: embellishment_description.sublimate_description,
              washed_description: embellishment_description.washed_description,
              uid: embellishment_description.id,
              ingested_flag: embellishment_description.ingested_flag,
              returned_flag: embellishment_description.returned_flag,
              materials: embellishment_description.materials.map(material => {
                return {
                  bom_material_id: material.id,
                  material_id: material.material.id,
                  label: `${material.material.description} / ${material.material.size.name} / ${material.material.client.abbreviation}`,
                  consumption: material.material_consumption
                }
              })
            }
          })
        },
      });
      this.toggleLoading();
    }.bind(this)).catch(function (error) {
      this.toggleLoading();
      this.handleError(error, 'selected');
    }.bind(this));

  }

  _setLenght = true;
  setArrayLenght(tableHeaders) {
    this.totales = Array(tableHeaders.length).fill(0);
    this._setLenght = false;
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }
  //se cambio url
  handleReject = (header_id) => {
    let data = new FormData();
    data.append('flag', 'A');//N
    data.append('bom_id', header_id);
    axios.post(
      process.env.REACT_APP_IP + '/bom_header/change_status/',
      data,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this._isMounted && this.notify("tr", "success", "CostSheet Returned!");
      this.componentDidMount();
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleReject");
    }.bind(this));
  }

  toggleRejectModal = () => {
    this._isMounted && this.setState({
      rejectModal: !this.state.rejectModal
    });
  }
  _checkedMaterials = false;
  checkedStatus = (checkedList, bomsHeaders) => {
    if (checkedList.some(x => x === false || checkedList.length !== bomsHeaders.length)) {
      this._checkedMaterials = false;
    } else {
      this._checkedMaterials = true;
    }
  }
  //se cambio url
  rejectColors = () => {
    if (this.state.rejectedColors.length === 0) {
      this.notify("tr", "warning", "Please Check colors to reject");
      this.toggleRejectModal();
    }
    else {
      this.toggleLoading();
      this.toggleRejectModal()
      console.log('print del colors to reject', this.state.rejectedColors)
      var colors_rejected = this.state.rejectedColors.toString();
      let data = new FormData();
      data.append('bom_id', this.props.location.state.bomHeader);
      data.append('procces_returned', colors_rejected);
      axios.post(
        process.env.REACT_APP_IP + '/bom_header/return_bom_costing/',
        data,
        { headers: { "Authorization": "Token " + this.userToken } }
      ).then(function () {
        this._isMounted && this.notify("tr", "success", "Colors Returned!");
        this.componentDidMount();
        const path = "/admin/managments_boms"
        this.props.history.push({
          pathname: path
        })
      }.bind(this)).catch(function (error) {
        this.handleError(error, "handleReject");
      }.bind(this));
    }
  }
  //se cambio url
  handleApproveCosting = (header_id) => {
    this.toggleLoading()
    let data = new FormData();
    if (this.state.header.approvalStatus === 'A') {
      data.append('flag', 'D');
    } else {
      data.append('flag', 'D');
    }
    data.append('bom_id', header_id);
    axios.post(
      process.env.REACT_APP_IP + '/bom_header/change_status/',
      data,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.toggleLoading()
      this._isMounted && this.notify("tr", "success", "BOM Approved!");
      this.componentDidMount();
      const path = "/admin/managments_boms"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.notify("tr", "warning", error);
    }.bind(this))
  }




  getBomsHeaders = (headerId) => {
    //this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/bom_header/get_colors_for_bom/?bom=${headerId}`,
      {
        headers: {
          "Authorization": "Token " + this.userToken
        }
      }
    ).then(function (response) {
      this.setColors(response.data.colors)
      //this.toggleLoading()

      //this._loadedBomsHeaders = true;
      //status= response.data.approvalStatus;
      /*this.setState({
        tableHeaders: 'hola'
      });*/

    }.bind(this)).catch(function (error) {
    })
  }

  setColors(data) {
    this.setState({
      tableHeaders: data
    })
  }

  processToReject = (id) => {
    var inlist = this.state.rejectedColors.includes(id)
    //console.log(inlist)
    if (inlist) {
      this.state.rejectedColors.indexOf(id)
      this.state.rejectedColors.splice(this.state.rejectedColors.indexOf(id))
      // console.log('color',this.state.rejectedColors)
    }
    else {
      this.state.rejectedColors.push(id)
      console.log('colors: ',this.state.rejectedColors)
    }
    console.log('rejected colors: ',this.state.rejectedColors)
    this.setState({colorsToSend:this.state.rejectedColors})
    //console.log('Colors to Send: ',this.state.colorsToSend)
    return this.state.colorsToSend
  }

  componentDidMount() {
    this._isMounted = true;
    //if (this.props.location.state) {
    //console.log('colors en el didmount',this.props.location.state)
    /* if (this.props.location.state.allColors === true) {
       this.setState({
         rejectAllColors: true
       })
     }*/
    this.getBomsHeaders(
      this.props.location.state.bomHeader
      //location.state.bomHeader
    );
    this.showHeaderInfo(this.props.location.state.bomHeader);
    //console.log('AQUI IMPRIME ',this.props.location.state.bomHeader);
    // }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  render() {
    const {
      location
    } = this.props

    const {
      loading
    } = this.state;
    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (
      <>
        <div className="content" style={{ marginTop: '-50px' }}>
          <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
            <div className="ui segment">
              <div className={`ui active transition ${inverted} visible dimmer`}>
                <div className="content"><div className="ui text loader">Loading</div></div>
              </div>
              <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
            </div>
          </div>
          <div className="react-notification-alert-container">
            <NotificationAlert ref="notificationAlert" />
          </div>
          <Col className="mr-auto ml-auto" md="11">
            <Row className="justify-content-md-center">
              <Col md="auto">
                <div style={{ fontSize: '26px', marginBottom: '-27px' }}>
                  {this.state.header.bom_style}
                </div>
              </Col>
            </Row>
            <ReactWizard
              steps={[
                {
                  stepName: "Same Consumptions",
                  stepIcon: "tim-icons icon-single-02",
                  component: <Step1
                    id={location.state.bomHeader} />
                },
                {
                  stepName: "Materials x Colors",
                  stepIcon: "tim-icons icon-settings-gear-63",
                  component: <Step2
                    id={location.state.bomHeader} />
                },
                {
                  stepName: "Colors and Consumptions",
                  stepIcon: "tim-icons icon-delivery-fast",
                  component: <Step3
                    id={location.state.bomHeader} />
                },
              ]}
              navSteps
              title=""
              headerTextCenter
              finishButtonClasses="btn-wd btn-info"
              finishButtonClick={() => this.handleApproveCosting(location.state.bomHeader)}
              finishButtonText="Approve"
              nextButtonClasses="btn-wd btn-info"
              previousButtonClasses="btn-wd"
              progressbar
              color="blue"
            />
            <div style={{ marginTop: '-7.5%', marginLeft: '40%' }}>
              <Button color="warning"
                onClick={this.toggleRejectModal}
              >
                Reject
              </Button>
              <Button
                color="primary"
                onClick={() => this.downloadTechPack(this.state.header.production_techpack)}
              /*onClick={() => {
                console.log('path to techpack', this.state.header.production_techpack)
              }}*/
              >Download Techpack</Button>
            </div>
          </Col>
          <Modal isOpen={this.state.rejectModal}
            toggle={this.toggleRejecttModal}
            modalClassName={this.checkPageMode().mc}
          >
            <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
              Reject Colors
            </ModalHeader>
            <ModalBody>
              Select the colors for reject
              <Row>
                {//console.log('this sis ',this.state.rejectAllColors),
                  this.state.header.embellishment_description && this.state.tableHeaders &&
                  this.state.header.embellishment_description.map((color, idx) => (
                    <>
                      <>
                        <Col sm={6}>
                          <b >{color.value}</b>
                        </Col>
                        <Col sm={6}>
                          <Input
                            type={"checkbox"}

                            className="check"
                            value={this.state.header.embellishment_description.length !== 0 ? this.state.header.embellishment_description[idx].id : color}
                            onChange={e => {
                              console.log(this.state.header.embellishment_description[idx].id);
                              this.processToReject(this.state.header.embellishment_description[idx].id)
                            }}
                          />
                        </Col>
                      </>
                      {/*this.state.rejectAllColors === true ?
                      <>
                          <Col sm={6}>
                            <b >{color.value}</b>
                          </Col>
                          <Col sm={6}>
                            <Input
                              type={"checkbox"}
                              className="check"
                              value={this.state.header.embellishment_description.length !== 0 ? this.state.header.embellishment_description[idx].id : color}
                              onChange={e => {
                                this.processToReject(this.state.header.embellishment_description[idx].id)
                              }}
                            />
                          </Col>
                        </>:
                        <>
                        {color.returned_flag === true || color.ingested_flag === false ?
                        <>
                          <Col sm={6}>
                            <b >{color.value}</b>
                          </Col>
                          <Col sm={6}>
                            <Input
                              type={"checkbox"}
 
                              className="check"
                              value={this.state.header.embellishment_description.length !== 0 ? this.state.header.embellishment_description[idx].id : color}
                              onChange={e => {
                                this.processToReject(this.state.header.embellishment_description[idx].id)
                              }}
                            />
                          </Col>
                        </>
                        :
                        <></>
                      }
                        </>
                    */}

                    </>
                  ))}
              </Row>
            </ModalBody>
            <ModalFooter>
              <Button color="secondary" onClick={this.toggleRejectModal}>
                Close
              </Button>

              <Button color="primary" onClick={() =>
                this.rejectColors()
              }>
                Reject Colors
              </Button>
            </ModalFooter>
          </Modal>

        </div>
      </>
    );
  }
};

export default Wizard;
