import React from "react";
import {
  Table,
  Input
} from "reactstrap";

export default function CostSheetResume(props){
  let {detailCalculation} = props;
  return (<>
    <Table striped size={'sm'} style={{marginBottom: '0rem'}}>
      <tbody>
        <tr>
          <th style={{width: '20%'}}>&nbsp;</th>
          <th>White</th>
          <th>Medium</th>
          <th>Dark</th>
          <th>Special</th>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            Total Fabrics<br/>(<b>{detailCalculation.wasteFabrics*100}%</b>)
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.pricesFabrics.white.toFixed(2)}</td>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.pricesFabrics.medium.toFixed(2)}</td>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.pricesFabrics.dark.toFixed(2)}</td>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.pricesFabrics.special.toFixed(2)}</td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            <b>Total Materials</b>
            <br/>
            (<b>{detailCalculation.wasteMaterials*100}%</b>)
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.pricesMaterials.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.pricesMaterials.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.pricesMaterials.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.pricesMaterials.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            Manufacturing
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>            
            ${detailCalculation.manufacturing.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>            
            ${detailCalculation.manufacturing.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>            
            ${detailCalculation.manufacturing.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.manufacturing.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            Insurance
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.broker.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.broker.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.broker.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.broker.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            Freight Total
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>     
            ${detailCalculation.freightTotal.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>     
            ${detailCalculation.freightTotal.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>     
            ${detailCalculation.freightTotal.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>     
            ${detailCalculation.freightTotal.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">Profit (%)</th>
          <td style={{textAlign: 'center',width: '20%'}}>
            {console.log(props.resume)}
            {props.resume?(detailCalculation.profit.white*100).toFixed(2)+'%':<Input type={'number'} min={0.00} step={1.00}
              value={(detailCalculation.profit.white*100).toFixed(2)} 
              onChange={(e)=>props.helper({key:'profitWhite','value':e.target.value})} 
            />}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            {props.resume?(detailCalculation.profit.medium*100).toFixed(2)+'%':<Input type={'number'} min={0.00} step={1.00}
              value={(detailCalculation.profit.medium*100).toFixed(2)} 
              onChange={(e)=>props.helper({key:'profitMedium','value':e.target.value})} 
            />}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            {props.resume?(detailCalculation.profit.dark*100).toFixed(2)+'%':<Input type={'number'} min={0.00} step={1.00}
              value={(detailCalculation.profit.dark*100).toFixed(2)} 
              onChange={(e)=>props.helper({key:'profitDark','value':e.target.value})} 
            />}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            {props.resume?(detailCalculation.profit.special*100).toFixed(2)+'%':<Input type={'number'} min={0.00} step={1.00}
              value={(detailCalculation.profit.special*100).toFixed(2)} 
              onChange={(e)=>props.helper({key:'profitSpecial','value':e.target.value})} 
            />}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            Profit
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.profitValues.white.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.profitValues.medium.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.profitValues.dark.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.profitValues.special.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            Factoring
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>        
            {(detailCalculation.factoring*100).toFixed(2)}%
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>        
            {(detailCalculation.factoring*100).toFixed(2)}%
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>        
            {(detailCalculation.factoring*100).toFixed(2)}%
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>        
            {(detailCalculation.factoring*100).toFixed(2)}%
          </td>
        </tr>
        <tr>
          <th style={{width: '20%'}} scope="row">
            Sell Commision
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.sellComisionPrices.white.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.sellComisionPrices.medium.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.sellComisionPrices.dark.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.sellComisionPrices.special.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">
            <b>Embellishments Total</b>
          </th>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.totalEmbellishments.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.totalEmbellishments.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.totalEmbellishments.toFixed(2)}
          </td>
          <td style={{textAlign: 'center',width: '20%'}}>
            ${detailCalculation.totalEmbellishments.toFixed(2)}
          </td>
        </tr>

        <tr>
          <th style={{width: '20%'}} scope="row">Selling Prices</th>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.sellingPrices.white.toFixed(2)}</td>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.sellingPrices.medium.toFixed(2)}</td>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.sellingPrices.dark.toFixed(2)}</td>
          <td style={{textAlign: 'center',width: '20%'}}>${detailCalculation.sellingPrices.special.toFixed(2)}</td>
        </tr>

      </tbody>
    </Table>
  </>);
}