/*

*/
import React from "react";
import axios from "axios";
import NotificationAlert from "react-notification-alert";

import EngineeringTable from '../components/EngineeringCustomComponents/EngineeringTable';
import { uploadFileToS3 } from '../helpers/awsS3.js';
import '../components/QuotationElements/loader.min.css';

// reactstrap components
import {
  Row,
  Col
} from "reactstrap";

class Engineering extends React.Component {
  _isMounted = false;
  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';

  constructor (props) {
    super(props);
    this.state = {
      file: undefined,
      costingHeaders: [],
      selectedHeader: -1,
      loader: false
    };
    this.handleFileChange = this.handleFileChange.bind(this);
  }

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {
    let response = error.response?error.response:{};
    let message = error.message?error.message:''; //console.log(response,message,task)
    if (response.status!==undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      }else{
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document.getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  getCostingHeaders = () => {
    let jsonobj = [];
    axios.get(
      process.env.REACT_APP_IP + '/costingHeader/',
      { headers: {
        "Authorization":"Token "+this.userToken
      } }
    ).then( function(response) {
      response.data.map( (row) => {
        if (!row.samFlag) {
          let newRow={
            ...row,
            altCost: false
          }
          jsonobj.push(newRow);
        }
        return 1;
      });
      this._isMounted && this.setState({
        costingHeaders: jsonobj
      });
    }.bind(this)).catch( function(error) {
      this.handleError(error);
    }.bind(this));
  }

  componentDidMount() {
    this._isMounted = true;
    this.getCostingHeaders();
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  patchDetailPathToSAM = (detailId,pathToSam) => {
    axios.patch(
      process.env.REACT_APP_IP + '/costingDetail/' + detailId + '/', 
      {pathToSAM: pathToSam},
      { headers: {
        "Authorization": "Token " + this.userToken
      } }
    ).then(function(){return true;}).catch(function(error){
      this.handleError(error);
      return false;
    })
  }

  handleFileChange = (event) => {
    this.setState({ loading: true });
    const detailId = event.target.dataset.detailid;
    const altCost = event.target.dataset.altcost==="true"?1:0;
    //const headerId = event.target.dataset.headerid;
    const file = event.target.files[0];

    const fileData=new FormData();
    fileData.append('file', file);
    fileData.append('id', detailId);

    const fileDataParse=new FormData();
    fileDataParse.append('sam', file);
    fileDataParse.append('detail_id', detailId);
    fileDataParse.append('flag', 0);
    fileDataParse.append('operation_flag', altCost);

    const header = {
      headers: {
        "Authorization": "Token " + this.userToken,
        "Content-Type": "multipart/form-data"
      }
    }
    
    axios.post(
      process.env.REACT_APP_IP + "/costingDetail/parse_sams/",
      fileDataParse,
      header
    ).then( function() {
      uploadFileToS3(
        process.env.REACT_APP_IP + '/costingDetail/post_sams/',
        header,
        fileData,
        'file',
        ["xlsx", "xls", "XLSX", "XLS"]
      ).then( function() {
        this.setState({ 
          loading: false,
          file: file
        });
        this.notify("tr", "success", "SAM uploaded");
        this.getCostingHeaders();
      }.bind(this)).catch( function(error) {
        this.setState({ loading: false });
        this.handleError(error,'uploadFileToS3');
      }.bind(this));
    }.bind(this)).catch(function(error) {
      this.setState({ loading: false });
      this.handleError(error,'parse_sams');
    }.bind(this));
  }

  checkPageMode = () => {
    let pageMode=document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    let modalClass;
    switch(pageMode){
      case true:
        bgclass="bg-ligh";
        headerclass="";
        modalClass="modal-white";
        break;
      default:
        bgclass="bg-dark";
        headerclass="text-primary";
        modalClass="modal-black";
        break;
    }
    return {bg: bgclass,hc: headerclass, mc: modalClass};
  }

  altCostHandle=(value,index)=>{
    let {costingHeaders}=this.state
    costingHeaders[index].altCost = !costingHeaders[index].altCost
    this.setState({
      costingHeaders: costingHeaders
    })
  }

  render() {
    const pageMode=this.checkPageMode();
    let inverted = pageMode.bg==="bg-ligh" ? 'inverted' : '';
    return (<>
      <div className="blackdiv" id="blackdiv" style={this.state.loading?{display:'flex'}:{display:'none'}}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{minHeight:400}}><p>&nbsp;</p></div>
        </div>
      </div>
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert"/>
        </div>
        <Row>
          <Col md="12">
            <EngineeringTable
              dataTable = {this.state.costingHeaders}
              handleFileChange = {this.handleFileChange}
              altCostHandle = {this.altCostHandle}
            >
            </EngineeringTable>
          </Col>
        </Row>
      </div>
    </>);
  }
}

export default Engineering;
