import React from 'react';
import NotificationAlert from "react-notification-alert";
import axios from 'axios';
import {
  Form,
  FormGroup,
  Label,
  Input,
  FormText,
  Button,
  Card,
  CardBody,
  Row,
  Col,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane
} from "reactstrap";
import ClientList from '../components/AdminPanelComponents/ClientsList';
import Select from "react-select";
import ReactTable from "react-table";
import '../components/BOMS/styles.css'

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: '#e14eca'
  })
}

class Clients extends React.Component {
  Access = JSON.parse(localStorage.getItem('permissions') || '{}');
  _isMounted = false;
  _ClientsLoaded = false;
  _UserLoaded = false;
  userToken = localStorage.getItem('userToken') || '';

  companyOptions = [
    {value: 'T', label: 'TRECENTO'},
    {value: 'Z', label: 'ZUNTEX'},
    {value: 'J', label: 'JT'},
  ];

  defaultColorForm = {
    color_class: null,
    client: null,
    name: '',
  }

  constructor(props){
    super(props);
    this.state = {
      vTabs: "vt1",
      clients: [],
      client: {},
      users: [],
      user: {},
      selectedUser: null,
      selectedClients: [],
      selectCompany: {},
      colorsClasses: [],
      selectedClient: [],
      clientsOptions: [],
      ColorsForm: this.defaultColorForm,
      loadingColors: false,
      colors: [],
    }
    this.createService = React.createRef();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    if(pageMode !== true){
      return 'modal-black';
    }else{
      return 'modal-white';
    }
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };

    let notificationContainer = 
      document.getElementsByClassName("react-notification-alert-container");
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {    
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    if (
      response.status!==undefined && 
      response.status === 401
    ) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      }else{
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  getUsers = () => {
    axios.get(
      process.env.REACT_APP_IP+'/users/',      
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(response){
      this._UserLoaded=true;
      let users = response.data.map(user => {
        return {value: user.id, label: user.email, clients: user.clients}
      });
      this.setState({
        users: users
      });
    }.bind(this)).catch(function(error){
      this.handleError(error,'getUsers');
    }.bind(this))
  }

  updateUser = (user_id,clients) => {
    axios.patch(
      process.env.REACT_APP_IP + '/users/' + user_id + '/',
      clients,
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(){
      this.componentDidMount();
      this.notify("tr", "success", "Clients changed in user.");
    }.bind(this)).catch(function(error){
      this.handleError(error,'updateUser')
    }.bind(this))
  }

  _userClientsLoaded = false;
  getUserClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/',
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(response) {
      //console.log('clientesxPeople',response);
      this._userClientsLoaded=true;
      this._isMounted && this.setState({
        clientsOptions: response.data.map(client => {
          return {
            value: client.id,
            label: `${client.company} / ${client.name} / ${client.abbreviation}`
          }
        })
      })
    }.bind(this)).catch( function(error) {
      this.handleError(error,'getUserClients');
    }.bind(this));
  }

  //este devuelve todos
  getClients = () => {
    axios.get(
      process.env.REACT_APP_IP + '/clients/get_all_clients/',
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(response) {
      this._ClientsLoaded=true;
      this._isMounted && this.setState({
        clients: response.data,
      })
    }.bind(this)).catch( function(error) {
      this.handleError(error,'getClients');
    }.bind(this));
  }

  addClient = (data) => {
    axios.post(
      process.env.REACT_APP_IP + '/clients/',
      data,
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(){
      this.componentDidMount();
      this.notify("tr", "success", "Client added!");
    }.bind(this)).catch(function(error){
      this.handleError(error,'addClient');
    }.bind(this));
  }

  updateClient = (data) => {
    const newData = data;
    delete newData.created_at;
    delete newData.updated_at;
    axios.patch(
      process.env.REACT_APP_IP + '/clients/' + newData.id + '/',
      newData,
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(){
      this.componentDidMount();
      this.notify("tr", "success", "Client updated!");
    }.bind(this)).catch(function(error){
      this.handleError(error,'updateClient');
    }.bind(this));
  }

  removeClient = (data) => {
    axios.delete(
      process.env.REACT_APP_IP + '/clients/' + data.id + '/',
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(){
      this.componentDidMount();
      this.notify("tr", "success", "Client removed!");
    }.bind(this)).catch(function(error){
      this.handleError(error,'removeClient');
    }.bind(this));
  }

  handleChange = (parameter,value) => {
    this.setState({
      client: {...this.state.client, [parameter]:value}
    });
  }

  handleSubmit = (event,action) => {
    event.preventDefault();
    if(!action){
      this.addClient(this.state.client);
    }else{
      this.updateClient(this.state.client);
    }
  }
  
  clearClient = () => {
    this.setState({
      client: {}
    })
  }

  VisualizeElement = (action) => {
    if(action===false){
      return this.Access.clients.create;
    }
    if(action===true){
      return this.Access.clients.edit;
    }
  }

  ClientsForm = (data) => {
    const id = !data.id?0:data.id;
    const action = id>0?true:false;
    const name = !data.name?'':data.name;
    const abbreviation = !data.abbreviation?'':data.abbreviation;
    const company = data.company? data.company : null;
    const selectedCompany = this.companyOptions.find(c=>c.value===company);
    return (<Form onSubmit={event => this.handleSubmit(event,action)}>
      <FormGroup>
        <Label for="clientName">Client's name</Label>
        <Input
          type="text"
          name="clientName"
          id="clientName"
          placeholder="Enter clients name."
          style={{marginBottom: 0}}
          value={name}
          onChange={event => this.handleChange('name',event.target.value)}
        />
        <FormText color="muted">
          Enter client complete name.
        </FormText>
      </FormGroup>
      <FormGroup>
        <Label for="clientAbbreviation">Client's abbreviation</Label>
        <Input
          type="text"
          name="clientAbbreviation"
          id="clientAbbreviation"
          placeholder="Enter clients abbreviation"
          style={{marginBottom: 0}}
          value={abbreviation}
          onChange={event => this.handleChange('abbreviation',event.target.value)}
        />
        <FormText color="muted">
          3 Letter client's abbreviation.
        </FormText>
      </FormGroup>
      <FormGroup>
        <Label>Company</Label>
        <Select
          options={this.companyOptions}
          value={selectedCompany ? selectedCompany : null}
          onChange={o=>{
            this.handleChange('company',o.value)
          }}
        />
      </FormGroup>
      {action?
        <Button color="info" onClick={this.clearClient}>Clear</Button>
      :''}
      {this.VisualizeElement(action) && <Button color="primary" type="submit">
        {action?'Update':'Create'}
      </Button>}
    </Form>);
  }

  handleEditClient = (client) => {
    this.setState({client: client})
  }

  componentDidMount () {
    this.clearClient();
    this.getClients();
    this.getUserClients();
    this.getUsers();
    this.loadColorClass();
    this.loadColors();
    this._isMounted = true;
  }

  componentWillUnmount () {
    this._isMounted = false;
  }

  handleSelectChange = (event) => {
    let opts = [], opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
      }
    }
    this.setState({selectedClients: opts});
  };

  handleUserSubmit = (event) => {
    event.preventDefault();
    this.updateUser(this.state.selectedUser.value,{"clients":this.state.selectedClients})
  }

  _loadedColorClass = false
  loadColorClass = () => {
    axios.get(
      process.env.REACT_APP_IP + '/color_by_client/get_color_class/',
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(response){
      this._loadedColorClass = true;
      this.setState({
        colorsClasses: response.data.map(color=>({
          value: color.id, 
          label: color.name
        }))
      })
    }.bind(this))
  }

  loadFormColor = (color) => {
    this.setState({
      ColorsForm: color,
    })
  }

  createColor = (e) => {
    e.preventDefault();
    const {
      ColorsForm
    } = this.state;
    if(ColorsForm.name.length>25){
      this.notify("tr","warning","name cannot be more than 25 characters");
      return;
    }
    this.setState({
      loadingColors: true,
    })
    axios.post(
      process.env.REACT_APP_IP + '/color_by_client/',
      ColorsForm,
      {headers:{
        "Authorization": "Token " + this.userToken,
        "Content-Type":"application/json"
      }}
    ).then(function(){
      this.setState({
        loadingColors: false,
      })
      this.componentDidMount();
      this.loadFormColor(this.defaultColorForm);
      this.notify("tr", "success", "Color created!");
    }.bind(this)).catch(function(error){
      this.handleError(error,'createColor');
      this.setState({
        loadingColors: false,
      });
      this.loadFormColor(this.defaultColorForm);
      this.componentDidMount();
    }.bind(this))
  }

  editColor = (e) => {
    e.preventDefault();
    const {
      ColorsForm
    } = this.state;
    if(ColorsForm.name.length>25){
      this.notify("tr","warning","name cannot be more than 25 characters");
      return;
    }
    this.setState({
      loadingColors: true,
    });
    const {id, ...updateData} = ColorsForm;
    axios.patch(
      process.env.REACT_APP_IP + `/color_by_client/${ColorsForm.id}/`,
      updateData,
      {headers:{
        "Authorization": "Token " + this.userToken,
        "Content-Type":"application/json"
      }}
    ).then(function(){
      this.setState({
        loadingColors: false,
      })
      this.componentDidMount();
      this.loadFormColor(this.defaultColorForm);
      this.notify("tr", "success", "Color edited!");
    }.bind(this)).catch(function(error){
      this.handleError(error,'editColor');
      this.setState({
        loadingColors: false,
      });
      this.componentDidMount();
    }.bind(this))
  }

  _loadedColors = false
  loadColors = () => {
    axios.get(
      process.env.REACT_APP_IP + '/color_by_client/',
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then(function(response){
      this._loadedColors = true;
      this.setState({
        colors: response.data
      })
    }.bind(this));
  }

  RemoveColor = (id) => {
    this.setState({
      loadingColors: true,
    })
    axios.delete(
      process.env.REACT_APP_IP + `/color_by_client/${id}/`,
      {headers:{
        "Authorization": "Token " + this.userToken
      }}
    ).then(function(){
      this.setState({
        loadingColors: false,
      })
      this.componentDidMount();
      this.notify("tr", "success", "Color Removed!");
    }.bind(this)).catch(function(error){
      this.handleError(error,'RemoveColor');
      this.setState({
        loadingColors: false,
      });
      this.componentDidMount();
    }.bind(this))
  }

  render () {
    /*const selectedClass = this.state.colorsClasses.find(
      o=>o.value === this.state.ColorsForm.color_class
    )*/
    const selectedClient = this.state.clientsOptions.find(
      o=>o.value === this.state.ColorsForm.client
    )
    return (<div className="content" >
      <div className="react-notification-alert-container">
        <NotificationAlert ref="notificationAlert"/>
      </div>
      <Nav pills className="nav-pills-info nav-pills-icons">
        <NavItem>
          <NavLink
            className={this.state.vTabs === "vt1" ? "active":""}
            onClick={() => this.setState({vTabs: "vt1"})}
          >
            New Clients
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={this.state.vTabs === "vt2" ? "active":""}
            onClick={() => this.setState({vTabs: "vt2"})}
          >
            Clients per User
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={this.state.vTabs === "vt3" ? "active":""}
            onClick={() => this.setState({vTabs: "vt3"})}
          >
            Colors
          </NavLink>
        </NavItem>
      </Nav>
      <Card>
        <CardBody>
          <TabContent activeTab={this.state.vTabs}>
            <TabPane tabId="vt1">
              <Row>
                <Col xs={12} md={3}>
                  {this.ClientsForm(this.state.client)}
                </Col>
                <Col xs={12} md={9}>
                  {
                    this._ClientsLoaded && 
                    <ClientList 
                      dataTable={this.state.clients} 
                      itemsPages={5}
                      tableTitle={'Client\'s list'}
                      editClient={this.handleEditClient}
                      removeClient={this.removeClient}
                    />
                  }
                </Col>
              </Row>
            </TabPane>
            <TabPane tabId="vt2">
              <Form onSubmit={this.handleUserSubmit}>
                <div style={{width: "100%",maxWidth: "500px"}}>
                  {this._UserLoaded && <Select 
                    menuPortalTarget={document.body}
                    className="react-select"
                    classNamePrefix="react-select"
                    styles={customStyles}
                    name={'users'}
                    placeholder="Choose User"
                    options={this.state.users}
                    onChange={value =>{
                      this.setState({
                        selectedUser: value,
                        selectedClients: value.clients
                      })
                    }}
                    value={this.state.selectedUser}
                  />}
                </div>
                <FormGroup>
                  <Label for="exampleSelectMulti1">
                    <b>Hold control and click to check/uncheck options</b><br></br>
                    Clients assignated to this user:
                  </Label>
                  {this._ClientsLoaded && <Input 
                    type="select" 
                    name="selectMulti" 
                    id="exampleSelectMulti1" 
                    multiple
                    ref={this.createService}
                    onChange={this.handleSelectChange}
                    value={this.state.selectedClients}
                    style={{height: '200px'}}
                  >
                    {this.state.clients.map((client,idx) => 
                      <option key={idx} value={client.id}>
                        {client.name} / {client.abbreviation}
                      </option>
                    )}
                  </Input>}
                </FormGroup>
                <Button type={'submit'} color={'primary'}>
                  Save changes
                </Button>
              </Form>
            </TabPane>
            <TabPane tabId="vt3">
              <Row>
                <Col xs={4}>
                  <h3>Create Color</h3>
                  <Form onSubmit={e => {
                    if(this.state.ColorsForm.id){
                      this.editColor(e)
                    } else {
                      this.createColor(e)
                    }
                  }}>
                    {/*<FormGroup>
                      <Label>Color Class</Label>
                      {this._loadedColorClass && <Select 
                        options={this.state.colorsClasses}
                        value = {selectedClass ? selectedClass : null}
                        onChange={o=>{
                          this.loadFormColor({
                            ...this.state.ColorsForm,
                            color_class: o.value
                          })
                        }}
                      />}
                      </FormGroup>*/}
                    <FormGroup>
                      <Label>Cliente</Label>
                      {this._ClientsLoaded && <Select 
                        options={this.state.clientsOptions}
                        value = {selectedClient ? selectedClient : null}
                        onChange={o=>{
                          this.loadFormColor({
                            ...this.state.ColorsForm,
                            client: o.value
                          })
                        }}
                      />}
                    </FormGroup>
                    <FormGroup>
                      <Label>Color Name</Label>
                      <Input type="text"
                        value={this.state.ColorsForm.name}
                        onChange={e=>{
                          this.loadFormColor({
                            ...this.state.ColorsForm,
                            name: e.target.value,
                          })
                        }}
                      />
                    </FormGroup>
                    <div className="clearfix">                      
                      {this.state.ColorsForm.id && <Button type="button"
                        size="sm" 
                        color="secundary" 
                        disabled={this.state.loadingColors}
                        className="float-left"
                        onClick={()=>this.loadFormColor(this.defaultColorForm)}
                      >
                        Clear
                      </Button>}
                      <Button type="submit" color="primary" 
                        size="sm" 
                        disabled={this.state.loadingColors}
                        className="float-left"
                      >
                        {this.state.ColorsForm.id?'Update Color':'Create Color'}
                      </Button> 
                      {this.state.loadingColors && <div className={"BOMSloader float-left"}></div>}
                    </div>
                  </Form>
                </Col>
                <Col xs={8}>
                  <h3>Color List</h3>
                  {this._loadedColors && <ReactTable
                    data={this.state.colors.map(color => {
                      return {
                        ...color,
                        client: color.client.name,
                        actions: (<div className="clearfix">
                          <Button size="sm" 
                            color="danger" 
                            className="float-right"
                            onClick={()=>{
                              this.RemoveColor(color.id)
                            }}
                          >
                            <i className = "tim-icons icon-simple-remove" />
                          </Button>
                          <Button size="sm" 
                            color="info" 
                            className="float-right"
                            onClick={()=>{
                              //console.log(color)
                              this.loadFormColor({
                                id: color.id,
                                color_class: color.color_class.id,
                                client: color.client.id,
                                name: color.name,
                                client_code: color.client_code,
                              })
                            }}
                          >
                            <i className = "tim-icons icon-pencil" />
                          </Button>
                        </div>)
                      }
                    })}
                    defaultPageSize={5}
                    columns = {[
                      {
                        Header: "Client",
                        accessor: "client",
                      },
                      {
                        Header: "Name",
                        accessor: "name",
                      },
                     
                      {
                        Header: "Actions",
                        accessor: "actions",
                      }
                    ]}
                  />}
                </Col>
              </Row>
            </TabPane>
          </TabContent>
        </CardBody>
      </Card>
    </div>);
  }
}

export default Clients