import React from "react";

import {
  Button,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Nav,
  NavItem,
  NavLink,
  TabContent,
  TabPane,
  Row, Col,
  Input,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Label,
  Form
} from "reactstrap";
import axios from 'axios';
import NotificationAlert from "react-notification-alert";
import ImportMaterial from '../components/MaterialsCustomComponents/ImportMaterial';
import '../components/MaterialsCustomComponents/tabs.css';
import { ProductionMaterials } from '../components/MaterialsCustomComponents/ProductionMaterials';
import Axios from "axios";
import Select from "react-select";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: '#e14eca',
    Zindex: '100'
  })
};

class ProdMaterials extends React.Component {

  _isMounted = false;
  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  _loadingMaterials = false;
  costingURL = process.env.REACT_APP_IP;
  fenixURL = process.env.REACT_APP_IP;


  defaultMaterialForm = {
    description: '',
    client: null,
    type_material: null,
    measure: null,
    provider: null,
    min: 0,
    scale: null,
    size: {},
    factory: null
  }

  defaultEditMaterialForm = {
    description: '',
    client: null,
    type_material: null,
    measure: null,
    provider: null,
    min: 0,
    scale: null,
    size: {},
    factory: null
  }

  constructor(props) {
    super(props)
    this.state = {
      tab: '1',
      loading: false,
      importLoading: false,
      importId: null,
      importModal: false,
      materialInfo: {},
      materialForm: this.defaultMaterialForm,
      editmaterialForm: this.defaultEditMaterialForm,
      clients: null,
      materialTypes: null,
      companyName: null,
      selectedOption: '1',
      //companyName: [{name:'Texsun', code: '1'}, {name:'Texfor', code: '1'}],
      materialMeasures: null,
      providers: null,
      sizes: null,
      materials: [],
      scales: [],
      editMaterial: [],
      importScale: null,
      importSize: null,
      importMeasure: null,
    }
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      })
    }
  }

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    let modalClass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        modalClass = "modal-white";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        modalClass = "modal-black";
        break;
    }
    return { bg: bgclass, hc: headerclass, mc: modalClass };
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };

    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  importOnChange = (ID) => {
    this.setState({
      importId: ID
    })
  }

  importOnSubmit = (ID) => {
    if (ID === "" || ID === null) {
      return false;
    }
    this.setState({
      loading: true
    });
    let data = new FormData();
    data.append('id', ID);
    data.append('factory', this.state.selectedOption)
    axios.post(
      this.costingURL + '/materialp/bring_old_material/',
      data,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this.setState({
        loading: false,
        materialInfo: response.data
      });
      if (Object.keys(response.data).length > 0) {
        this.toggleImportModal();
      } else {
        this.notify("tr", "warning", "Material not found");
      }
    }.bind(this)).catch(function (error) {
      this.setState({
        loading: false
      });
      this.handleError(error, 'import material');
    }.bind(this))
  }

  toggleImportModal = () => {
    this._isMounted && this.setState({
      importModal: !this.state.importModal
    });
  }

  fnImportMaterial = (ID, scale, size) => {
    let materialMeasureId = 0;
    var i;
    const { materialInfo } = this.state;
    if (ID === "" || ID === null) {
      return false;
    }
    for (i = 0; i < this.state.materialMeasures.length; i++) {
      if (materialInfo.measure === this.state.materialMeasures[i].label) {
        materialMeasureId = this.state.materialMeasures[i].value
      }
    }
    this.setState({
      loading: true
    });
    let data = new FormData();
    data.append('old_code', ID);
    data.append('description', materialInfo.description);
    data.append('type_material', materialInfo.costing_type_material ? materialInfo.costing_type_material : materialInfo.type_material);
    data.append('measure', materialMeasureId);
    data.append('min', materialInfo.min);
    data.append('provider', materialInfo.costing_provider_id ? materialInfo.costing_provider_id : materialInfo.provider.trim());
    data.append('client', materialInfo.costing_client_id ? materialInfo.costing_client_id : materialInfo.client);
    data.append('factory', `${materialInfo.factory.id}`)
    data.append('size', size.value);
    axios.post(
      this.costingURL + '/materialp/create_new_material_from_old_material/',
      data,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.setState({
        loading: false,
        materialInfo: {},
        importId: null,
      });
      this.toggleImportModal();
      this.getProductionMaterials();
      this.notify("tr", "success", "Material Imported! Please review the provider");

    }.bind(this)).catch(function (error) {
      this.setState({
        loading: false
      });
      this.handleError(error, 'create material ');
    }.bind(this))
  }

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.setCompanyName();
    this.getMaterialType();
    this.getMaterialMeasures();
    this.getProviders();
    this.getScales();
    this.getProductionMaterials();
  }

  _loadingClients = false;
  getClients = () => {
    Axios.get(
      this.costingURL + '/client/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingClients = true;
      const { data } = response;
      this.setState({
        clients: data.map(client => {
          return {
            value: client.id,
            label: client.name,
            abbreviation: client.abbreviation,
          }
        })
      })
    }.bind(this))
  }

  _loadingCompanies = false;
  setCompanyName = () => {
    const data = [
      {
        name: 'Texsun',
        code: '01'
      }, {
        name: 'Texfor',
        code: '02'
      }];
    this._loadingCompanies = true;
    this.setState({
      companyName: data.map(company => {
        return {
          label: company.name,
          value: company.code
        }
      })
    })
  }

  _loadingMaterialType = false;
  getMaterialType = () => {
    Axios.get(
      this.costingURL + '/material_type/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingMaterialType = true;
      const { data } = response;
      this.setState({
        materialTypes: data.map(material => {
          return {
            value: material.id,
            label: material.name,
          }
        })
      })
    }.bind(this))
  }

  _loadingMaterialMeasures = false;
  getMaterialMeasures = () => {
    Axios.get(
      this.costingURL + '/material_measure/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingMaterialMeasures = true;
      const { data } = response;
      this.setState({
        materialMeasures: data.map(measure => {
          return {
            value: measure.id,
            label: measure.name,
          }
        })
      })
    }.bind(this))
  }

  _loadingProviders = false;
  getProviders = () => {
    Axios.get(
      this.costingURL + '/provider/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingProviders = true;
      const { data } = response;
      this.setState({
        providers: data.map(provider => {
          return {
            value: provider.id,
            label: provider.name,
          }
        })
      })
    }.bind(this))
  }

  _loadingSizes = false;
  getSizes = (client) => {
    Axios.get(
      `${this.costingURL}/material_size/?client=${client}`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingSizes = true;
      const { data } = response;
      this.setState({
        sizes: data
      })
    }.bind(this))
  }

  _loadingScales = false;
  getScales = () => {
    Axios.get(
      `${this.costingURL}/material_scale/`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingScales = true;
      const { data } = response;
      this.setState({
        scales: data
      })
    }.bind(this))
  }

  AddNewMaterial = ({
    client,
    description,
    measure,
    type_material,
    min,
    provider,
    size,
    factory
  }) => {
    if (
      client === null ||
      description === "" ||
      measure === null ||
      type_material === null ||
      provider === null ||
      size === {} ||
      factory === null
    ) {
      this.notify("tr", "warning", "Please fill all the fields of the material form");
    } else {
      this.setState({
        loading: true
      });
      let materialData = new FormData();
      let TextSizes = [];
      Object.keys(size).map(id => {
        if (size[id]) {
          TextSizes.push(id);
        }
        return id;
      });
      materialData.append('description', description.trim());
      materialData.append('type_material', type_material.value);
      materialData.append('measure', measure.value);
      materialData.append('min', min);
      materialData.append('provider', provider.value);
      materialData.append('client', client.value);
      materialData.append('size', TextSizes.join());
      materialData.append('factory', factory.value);

      
      //console.log('material data', TextSizes.join())
      axios.post(
        this.costingURL + '/materialp/create_new_material_from_old_material/',
        materialData,
        { headers: { "Authorization": "Token " + this.userToken } }
      ).then(function () {
        this.setState({
        loading: false,
        materialForm: {
          description: '',
          client: null,
          type_material: null,
          measure: null,
          provider: null,
          min: 0,
          scale: null,
          size: {},
          factory: null
        },
      });
        this.getProductionMaterials();
        this.notify("tr", "success", "Material Created!");
        this.setState({
        loading: false,
        materialForm: {
          description: '',
          client: null,
          type_material: null,
          measure: null,
          provider: null,
          min: 0,
          scale: null,
          size: {},
          factory: null
        },
      });
      }.bind(this)).catch(function (error) {
        this.setState({
          loading: false,
          materialForm: {
            description: '',
            client: null,
            type_material: null,
            measure: null,
            provider: null,
            min: 0,
            scale: null,
            size: {},
            factory: null
          },
        });
        this.handleError(error, 'Please try creating again');
      }.bind(this))
    }
  }

  _loadedMaterials = false;
  getProductionMaterials = () => {
    axios.get(
      this.costingURL + '/materialp/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadedMaterials = true;
      this.setState({
        materials: response.data
      })
    }.bind(this))
  }

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  _loadedMaterials = false;
  deleteProductionMaterial = (material_id) => {
    this.toggleLoading();
    axios.delete(
      `${this.fenixURL}/materialp/${material_id}/`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.notify("tr", "success", "Material deleted!");
      this.toggleLoading();
      this.getProductionMaterials();
    }.bind(this)).catch(function (error) {
      this.handleError(error, "delete  Material")
    }.bind(this));
  }

  _uploadMaterials = false;
  uploadProductionMaterial = (material_id) => {
    let materialData = new FormData();
    materialData.append('id', material_id);
    this.toggleLoading();
    axios.post(
      this.fenixURL + '/materialp/ingest_one_material/',
      materialData,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function () {
      this.notify("tr", "success", "Material added to Sistemas Integrados!");
      this.toggleLoading();
      this.getProductionMaterials();
    }.bind(this)).catch(function (error) {
      this.handleError(error, "Material not added")
    }.bind(this));
  }

  _updateMaterial = false;
  updateProductionMaterial = (material_id) => {
    axios.get(
      `${this.costingURL}/materialp/${material_id}/`,
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._updateMaterial = true;
      this.toggle('4');
      this.setState({
        editMaterial: {
          description: response.data.description,
          client: response.data.client.name,
          measure: response.data.measure.name,
          type: response.data.type.name,
          min: response.data.min,
          provider: undefined && response.data.provider.name,
          factory: response.data.factory[0].name,
          sizes: response.data.size
        },
        editmaterialForm: {
          id: response.data.id,
          description: response.data.description,
          client: response.data.client,
          measure: response.data.measure,
          type: response.data.type,
          min: response.data.min,
          provider: response.data.provider,
          size: response.data.size,
          factory: response.data.factory,
        }
      })
      this.setState({sizesMaterial: response.data.size});
      // this.state.sizesMaterial = response.data.size

    }.bind(this))
  }

  editMaterial = ({
    id,
    client,
    description,
    measure,
    type,
    min,
    provider,
    size,
    factory,
  }) => {
    if (
      client === null ||
      description === "" ||
      measure === null ||
      type === null ||
      provider === null ||
      size === {} ||
      factory === null
    ) {
      this.notify("tr", "warning", "Please fill all the fields of the material form");
    } else {
      this.setState({
        loading: true
      });
      let materialData = new FormData();
      materialData.append('id', id);
      materialData.append('description', description.trim());
      materialData.append('type', type.value === undefined ? this.state.editmaterialForm.type.id : type.value);
      materialData.append('measure', measure.value === undefined ? this.state.editmaterialForm.measure.id : measure.value);
      materialData.append('min', min);
      materialData.append('provider', provider.value === undefined ? this.state.editmaterialForm.provider.id : provider.value);
      materialData.append('client', client.value === undefined ? this.state.editmaterialForm.client.id : client.value);
      //materialData.append('size', size.id);
      materialData.append('factory', factory.value === undefined ? `0${this.state.editmaterialForm.factory[0].id}` : factory.value);
      axios.patch(
        `${this.costingURL}/materialp/${id}/`,
        materialData,
        { headers: { "Authorization": "Token " + this.userToken } }
      ).then(function () {
        this.setState({
          loading: false,
          materialForm: this.defaultMaterialForm,
          sizes: [],
        });
        this.getProductionMaterials();
        this.notify("tr", "success", "Material Changed!");
        this.toggle('1');

      }.bind(this)).catch(function (error) {
        this.setState({
          loading: false
        });
        this.handleError(error, 'edit material');
      }.bind(this))
    }
  }

  render() {
    const {
      tab,
      materialInfo,
      importId,
      materialForm,
      editmaterialForm,
      clients,
      materialTypes,
      companyName,
      materialMeasures,
      providers,
      materials,
      scales,
      editMaterial
    } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (<>
      <div className="blackdiv"
        id="blackdiv"
        style={this.state.loading ? { display: 'flex' } : { display: 'none' }}
      >
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content">
              <div className="ui text loader">Loading</div>
            </div>
          </div>
          <div style={{ minHeight: 400 }}>
            <p>&nbsp;</p>
          </div>
        </div>
      </div>
      <div className="react-notification-alert-container">
        <NotificationAlert ref="notificationAlert" />
      </div>
      <div className="content">

        <Modal isOpen={this.state.importModal}
          toggle={this.toggleImportModal}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
            Import Material.
          </ModalHeader>
          <ModalBody>
            <Row>
              <Col sm={4}>
                <b>Old Code:</b>
              </Col>
              <Col sm={8}>
                {importId}
              </Col>
              <Col sm={4}>
                <b>Description:</b>
              </Col>
              <Col sm={8}>
                {materialInfo.description}
              </Col>
              <Col sm={4}>
                <b>Type:</b>
              </Col>
              <Col sm={8}>
                {materialInfo.type_material}
              </Col>
              <Col sm={4}>
                <b>Measure:</b>
              </Col>
              <Col sm={8}>
                {materialInfo.measure}
              </Col>
              <Col sm={4}>
                <b>Minimum:</b>
              </Col>
              <Col sm={8}>
                {materialInfo.min}
              </Col>
              <Col sm={4}>
                <b>Provider:</b>
              </Col>
              <Col sm={8}>
                {materialInfo.provider}
              </Col>
              <Col sm={4}>
                <b>Client:</b>
              </Col>
              <Col sm={8}>
                {materialInfo.client}

              </Col>
              <Col sm={4}>
                <b>Scale:</b>
              </Col>
              <Col sm={8}>
                {this._loadingScales &&
                  <Select
                    styles={customStyles}
                    options={scales.map(scale => {
                      return {
                        value: scale.id,
                        label: scale.name,
                        sizes: scale.sizes,
                      }
                    })}
                    className="react-select"
                    classNamePrefix="react-select"
                    onChange={option => {
                      this.setState({
                        importScale: option,
                      })
                    }}
                    value={this.state.importScale}
                    placeholder="Select Scale"
                  />}<br />
              </Col>
              <Col sm={4}>
                <b>Size:</b>
              </Col>
              <Col sm={8}>
                {this._loadingScales && this.state.importScale &&
                  <Select
                    styles={customStyles}
                    options={this.state.importScale.sizes.map((size, idx) => {
                      return {
                        value: size.id,
                        label: size.name,
                      }
                    })}
                    className="react-select"
                    classNamePrefix="react-select"
                    onChange={option => {
                      this.setState({
                        importSize: option,

                      })
                    }}
                    value={this.state.importSize}
                    placeholder="Select Size"
                  />}
              </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleImportModal}>
              Close
            </Button>
            {this.state.importScale && this.state.importSize &&
              <Button color="primary" onClick={() =>
                this.fnImportMaterial(this.state.importId, this.state.importScale, this.state.importSize)
              }>
                Import
              </Button>}
          </ModalFooter>
        </Modal>
        <Nav tabs>
          <NavItem>
            <NavLink
              className={tab === '1' ? 'active' : ''}
              onClick={() => { this.toggle('1'); }}
              style={pageMode.bg === "bg-ligh" ? {
                color: 'black',
                borderColor: 'black',
              } : {}}
            >
              Materials
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={tab === '2' ? 'active' : ''}
              onClick={() => { this.toggle('2'); }}
              style={pageMode.bg === "bg-ligh" ? {
                color: 'black',
                borderColor: 'black',
              } : {}}
            >
              Create
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={tab === '3' ? 'active' : ''}
              onClick={() => { this.toggle('3'); }}
              style={pageMode.bg === "bg-ligh" ? {
                color: 'black',
                borderColor: 'black',
              } : null}
            >
              Import
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={tab}>
          <TabPane tabId="4">
            <Card>
              <CardHeader>
                Edit Material
                <hr></hr>
                <CardBody>
                  <Form onSubmit={e => {
                    e.preventDefault();
                    this.editMaterial(editmaterialForm);
                  }}>
                    <FormGroup>
                      <Row>
                        <Col sm={12} md={8}>
                          <Row>
                            <Col md={6}>
                              <Label>
                                Client
                              </Label>
                            </Col>
                            <Col md={6}>
                              {this._loadingClients && <Select
                                options={clients}
                                menuPortalTarget={document.body}
                                className="react-select"
                                classNamePrefix="react-select"
                                styles={customStyles}
                                onChange={option => {

                                  this.setState({
                                    editmaterialForm: {
                                      ...editmaterialForm,
                                      client: option,
                                    }
                                  })

                                }}
                                selectedValue={editmaterialForm.client}

                                placeholder={editMaterial.client}
                              ></Select>}<br />
                            </Col>
                            <Col md={6}>
                              <Label>Description</Label>
                            </Col>
                            <Col md={6}>
                              <Input type="text"
                              maxLength="150"
                                onChange={e => this.setState({
                                  editmaterialForm: {
                                    ...editmaterialForm,
                                    description: e.target.value
                                  }
                                })}
                                value={editmaterialForm.description}
                              /><br />
                            </Col>
                            <Col md={6}>
                              <Label>Factory</Label>
                            </Col>
                            <Col md={6}>
                              {this._loadingCompanies && <Select
                                options={companyName}
                                menuPortalTarget={document.body}
                                className="react-select"
                                classNamePrefix="react-select"
                                styles={customStyles}
                                onChange={option => {
                                  this.setState({
                                    editmaterialForm: {
                                      ...editmaterialForm,
                                      factory: option,
                                    }
                                  })
                                }}
                                placeholder={editMaterial.factory}
                              />}<br />
                            </Col>
                            <Col md={6}>
                              <Label>Type</Label>
                            </Col>
                            <Col md={6}>
                              {this._loadingMaterialType && <Select
                                options={materialTypes}
                                menuPortalTarget={document.body}
                                className="react-select"
                                classNamePrefix="react-select"
                                styles={customStyles}
                                onChange={option => {
                                  this.setState({
                                    editmaterialForm: {
                                      ...editmaterialForm,
                                      type: option,
                                    }
                                  })
                                }}
                                selectedValue={editmaterialForm.type}
                                //value={editmaterialForm.type}
                                placeholder={editMaterial.type}
                              />}<br />
                            </Col>
                            <Col md={6}>
                              <Label>Measure</Label>
                            </Col>
                            <Col md={6}>
                              {this._loadingMaterialMeasures && <Select
                                options={materialMeasures}
                                menuPortalTarget={document.body}
                                className="react-select"
                                classNamePrefix="react-select"
                                styles={customStyles}
                                onChange={option => {
                                  this.setState({
                                    editmaterialForm: {
                                      ...editmaterialForm,
                                      measure: option,
                                    }
                                  })
                                }}
                                selectedValue={editmaterialForm.measure}
                                placeholder={editMaterial.measure}
                              />}<br />
                            </Col>
                            <Col md={6}>
                              <Label>Min</Label>
                            </Col>
                            <Col md={6}>
                              <Input type="number"
                                onChange={e => this.setState({
                                  editmaterialForm: {
                                    ...editmaterialForm,
                                    min: e.target.value
                                  }
                                })}
                                value={editmaterialForm.min}
                                placeholder={editMaterial.min}
                              /><br />
                            </Col>
                            <Col md={6}>
                              <Label>Provider</Label>
                            </Col>
                            <Col md={6}>
                              {this._loadingProviders && <Select
                                options={providers}
                                menuPortalTarget={document.body}
                                className="react-select"
                                classNamePrefix="react-select"
                                styles={customStyles}
                                onChange={option => {
                                  this.setState({
                                    editmaterialForm: {
                                      ...editmaterialForm,
                                      provider: option,
                                    }
                                  })
                                }}
                                selectedValue={editmaterialForm.provider}

                              />}<br />
                            </Col>
                            <Col md={6}>
                              <Label>Sizes</Label>
                            </Col>
                            <Col md={6}>
                              {/*console.log(editMaterial)*/}
                              {this._updateMaterial && editMaterial.sizes ?
                                <>{/*editMaterial.sizes[1].name*/}
                                  {editMaterial.sizes.map(
                                    (size, idx) => (<>
                                      <Label>
                                        {size.name}, &nbsp;
                                      </Label>
                                    </>)
                                  )}
                                </> :
                                <>El material tiene una unica talla </>}

                            </Col>
                          </Row>
                        </Col>
                      </Row>
                      <Row>
                        <Col className="px-md-1" md="6">
                          <FormGroup>
                            <Button className="btn-fill" color="primary" type="submit">
                              Save Changes
                            </Button>
                          </FormGroup>
                        </Col>
                      </Row>
                    </FormGroup>
                  </Form>
                </CardBody>
              </CardHeader>
            </Card>
          </TabPane>
        </TabContent>
        <TabContent activeTab={tab}>
          <TabPane tabId="1">
            <ProductionMaterials
              materials={materials}
              onDelete={this.deleteProductionMaterial}
              onUpdate={this.updateProductionMaterial}
              onUpload={this.uploadProductionMaterial}
            />
          </TabPane>
          <TabPane tabId="2">
            <Card>
              <CardHeader>
                <h5 className="title">Create Material</h5>
              </CardHeader>
              <CardBody>
                <Form onSubmit={e => {
                  e.preventDefault();
                  this.AddNewMaterial(materialForm);
                }}>
                  <FormGroup>
                    <Row>
                      <Col sm={12} md={8}>
                        <Row>
                          <Col md={6}>
                            <Label>
                              Client
                            </Label>
                          </Col>
                          <Col md={6}>
                            {this._loadingClients && <Select
                              options={clients}
                              menuPortalTarget={document.body}
                              className="react-select"
                              classNamePrefix="react-select"
                              styles={customStyles}
                              onChange={option => {
                                this.setState({
                                  materialForm: {
                                    ...materialForm,
                                    client: option,
                                  }
                                })
                              }}
                              value={materialForm.client}
                              placeholder="Select client"
                            />}<br />
                          </Col>
                          <Col md={6}>
                            <Label>Description</Label>
                          </Col>
                          <Col md={6}>
                            <Input type="text"
                              maxLength="142"
                              onChange={e => this.setState({
                                materialForm: {
                                  ...materialForm,
                                  description: e.target.value
                                }
                              })}
                              value={materialForm.description}
                              placeholder="Enter description"
                            /><br />
                          </Col>
                          <Col md={6}>
                            <Label>Company</Label>
                          </Col>
                          <Col md={6}>
                            {this._loadingCompanies && <Select
                              options={companyName}
                              menuPortalTarget={document.body}
                              className="react-select"
                              classNamePrefix="react-select"
                              styles={customStyles}
                              onChange={option => {
                                this.setState({
                                  materialForm: {
                                    ...materialForm,
                                    factory: option,
                                  }
                                })
                              }}
                              value={materialForm.factory}
                              placeholder="Select company"
                            />}<br />
                          </Col>
                          <Col md={6}>
                            <Label>Type</Label>
                          </Col>
                          <Col md={6}>
                            {this._loadingMaterialType && <Select
                              options={materialTypes}
                              menuPortalTarget={document.body}
                              className="react-select"
                              classNamePrefix="react-select"
                              styles={customStyles}
                              onChange={option => {
                                this.setState({
                                  materialForm: {
                                    ...materialForm,
                                    type_material: option,
                                  }
                                })
                              }}
                              value={materialForm.type_material}
                              placeholder="Select type"
                            />}<br />
                          </Col>
                          <Col md={6}>
                            <Label>Measure</Label>
                          </Col>
                          <Col md={6}>
                            {this._loadingMaterialMeasures && <Select
                              options={materialMeasures}
                              menuPortalTarget={document.body}
                              className="react-select"
                              classNamePrefix="react-select"
                              styles={customStyles}
                              onChange={option => {
                                this.setState({
                                  materialForm: {
                                    ...materialForm,
                                    measure: option,
                                  }
                                })
                              }}
                              value={materialForm.measure}
                              placeholder="Select measure"
                            />}<br />
                          </Col>
                          <Col md={6}>
                            <Label>Min</Label>
                          </Col>
                          <Col md={6}>
                            <Input type="number"
                              onChange={e => this.setState({
                                materialForm: {
                                  ...materialForm,
                                  min: e.target.value
                                }
                              })}
                              value={materialForm.min}
                              placeholder="Enter minimum"
                            /><br />
                          </Col>
                          <Col md={6}>
                            <Label>Provider</Label>
                          </Col>
                          <Col md={6}>
                            {this._loadingProviders && <Select
                              options={providers}
                              menuPortalTarget={document.body}
                              className="react-select"
                              classNamePrefix="react-select"
                              styles={customStyles}
                              onChange={option => {
                                this.setState({
                                  materialForm: {
                                    ...materialForm,
                                    provider: option,
                                  }
                                })
                              }}
                              value={materialForm.provider}
                              placeholder="Select Provider"
                            />}<br />
                          </Col>
                          <Col md={6}>
                            <Label>Scale</Label>
                          </Col>
                          <Col md={6}>
                            {this._loadingScales && <Select
                              styles={customStyles}
                              options={scales.map(scale => {
                                return {
                                  value: scale.id,
                                  label: scale.name,
                                  sizes: scale.sizes,
                                }
                              })}
                              menuPortalTarget={document.body}
                              className="react-select"
                              classNamePrefix="react-select"

                              onChange={option => {
                                this.setState({
                                  materialForm: {
                                    ...materialForm,
                                    scale: option,
                                  }
                                })
                              }}
                              value={materialForm.scale}
                              placeholder="Select Scale"
                            />}<br />
                          </Col>
                          <Col md={6}>
                          </Col>
                          <Col md={6}>
                            {this._loadingScales && materialForm.scale && materialForm.scale.sizes.map(
                              (size, idx) => (<FormGroup check key={idx}>
                                <Label check>
                                  <Input
                                    type={"checkbox"}
                                    value={size.id}
                                    onChange={e => {
                                      let sizeArr = materialForm.size;
                                      sizeArr[size.id] = e.target.checked;
                                      this.setState({
                                        materialForm: {
                                          ...materialForm,
                                          size: sizeArr
                                        }
                                      })
                                    }}
                                    checked={materialForm.size[size.id] ? materialForm.size[size.id] : false}
                                  />
                                  {' '}{size.name}
                                  <span className="form-check-sign">
                                    <span className="check"></span>
                                  </span>
                                </Label>
                              </FormGroup>)
                            )}
                            <br />
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                    <Row>
                      <Col className="px-md-1" md="6">
                        <FormGroup>
                          <Button className="btn-fill" color="primary" type="submit">
                            Add
                          </Button>
                        </FormGroup>
                      </Col>
                    </Row>
                  </FormGroup>
                </Form>
              </CardBody>
            </Card>
          </TabPane>
          <TabPane tabId="3">
            <Card>
              <CardHeader>
                <h5 className="title">Import Material</h5>
              </CardHeader>
              <CardBody>
                <Row>
                  <Col sm={12} md={4}>
                    <div>
                      <label><input type="radio" value="1" name="company" checked={this.state.selectedOption === '1'} onChange={() => this.setState({ selectedOption: '1' })} /> Zuntex</label>
                      <br></br>
                      <label><input type="radio" value="2" name="company" onChange={() => this.setState({ selectedOption: '2' })} /> JYT </label>
                      <br></br>
                      <label><input type="radio" value="3" name="company" onChange={() => this.setState({ selectedOption: '3' })} /> Trechento </label>
                    </div>
                  </Col>
                </Row>
                <ImportMaterial
                  onChange={this.importOnChange}
                  onSubmit={this.importOnSubmit}
                  ID={this.state.importId}
                />
              </CardBody>
            </Card>
          </TabPane>
        </TabContent>

      </div>
    </>);
  }
}

export default ProdMaterials;