import React from 'react';
import ReactTable from "react-table";
import {
  Button,
  Row,
  Col,
} from "reactstrap";

let Data=[];

function CostSheetTable({...props}) {
  Data = props.dataTable.map((prop, key) => {
    const Calc=prop.Calc;
    return {
      row: key,
      header_id: prop.header.id,
      detail_id: prop.header.detail_id,
      client: prop.header.client.name,
      season: prop.header.season.name,
      style: prop.header.style,
      profit: (<>
        <Row>
          <Col>White:</Col>
          <Col><b>{`${(Calc.profit.white*100).toFixed(2)}%`}</b></Col>
        </Row>
        <Row>
          <Col>Medium:</Col>
          <Col><b>{`${(Calc.profit.medium*100).toFixed(2)}%`}</b></Col>
        </Row>
        <Row>
          <Col>Dark:</Col>
          <Col><b>{`${(Calc.profit.dark*100).toFixed(2)}%`}</b></Col>
        </Row>
        <Row>
          <Col>Special:</Col>
          <Col><b>{`${(Calc.profit.special*100).toFixed(2)}%`}</b></Col>
        </Row>
      </>),
      prices: (<>
        <Row>
          <Col>White:</Col>
          <Col><b>${Calc.sellingPrices.white.toFixed(2)}</b></Col>
        </Row>
        <Row>
          <Col>Medium:</Col>
          <Col><b>${Calc.sellingPrices.medium.toFixed(2)}</b></Col>
        </Row>
        <Row>
          <Col>Dark:</Col>
          <Col><b>${Calc.sellingPrices.dark.toFixed(2)}</b></Col>
        </Row>
        <Row>
          <Col>Special:</Col>
          <Col><b>${Calc.sellingPrices.special.toFixed(2)}</b></Col>
        </Row>
      </>),
      actions: (
        <div className="" >
          <Button
            onClick={()=>props.onApprove(key)}
            color = "info"
            size = "sm"
          >
            Approve <i className = "fa fa-edit" />
          </Button>
          <hr/>
          <Button
            onClick={()=>props.onReject(prop.header.id)}
            color = "danger"
            size = "sm"
          >
            Reject <i className={"fa fa-times"} />
          </Button>
        </div>
      )
    };
  });
  return (<>        
    <h4><b>{props.tableTitle}</b></h4>
    <ReactTable
      data = {Data}
      filterable
      columns = {[
        {
          Header: "Code",
          accessor: "header_id",
          width: 80
        },
        {
          Header: "Client",
          accessor: "client"
        },
        {
          Header: "Season",
          accessor: "season"
        },
        {
          Header: "Style",
          accessor: "style"
        },
        {
          Header: "Profits",
          accessor: "profit",
          sortable: false,
          filterable: false
        },
        {
          Header: "Selling Prices",
          accessor: "prices",
          sortable: false,
          filterable: false
        },
        {
          Header: "Aprove",
          accessor: "actions",
          sortable: false,
          filterable: false
        }
      ]}
      defaultPageSize={props.itemsPages}
      showPaginationTop
      showPaginationBottom={false}
      defaultSorted = {[
        {
          id: "header_id",
          desc: true
        }
      ]}
      className="-striped -highlight primary-pagination"
  />
  </>);
}

export default CostSheetTable;