import React from "react";

import NotificationAlert from "react-notification-alert";
import FabricsForm from '../components/FabricsCustomComponents/FabricsFormV2';
import FabricsHistory from '../components/FabricsCustomComponents/FabricsHistory';
import { uploadFileToS3 } from '../helpers/awsS3.js';
import '../components/QuotationElements/loader.min.css';
import fabricNames from '../components/FabricsCustomComponents/fabricNames.json';

import axios from 'axios';

import {
  Collapse, 
  Button, 
  CardBody, 
  Card,
  Modal, 
  ModalBody, 
  ModalFooter,
  ModalHeader
} from "reactstrap";

class Fabrics extends React.Component {

  _isMounted = false;
  userToken = localStorage.getItem('userToken') || '';
  valid_time = localStorage.getItem('valid_time') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  user_id = localStorage.getItem('user_id') || '0';
  _fabricStory = false;
  _fabricType = false;

  constructor(props) {
    super(props);
    this.state = {
      action: 'insert',
      actionText: 'Insert',
      itemsPages: 5,
      dataTable: [],
      formdata: {},
      openedCollapses: ["new"],
      SeasonsOps: [],
      StylesOps: [],
      clientOptions: [],
      deleteModal: false,
      toDeleteDM: {"dm":''},
      loading: true,
      certificate: null,
      fabricsTypes: {}
    };
  }

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    if (
      response.status!==undefined && 
      response.status === 401
    ) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  collapsesToggle = collapse => {
    if (this.state.openedCollapses.includes(collapse)) {
      this._isMounted && this.setState({
        openedCollapses: []
      });
    } else {
      this._isMounted && this.setState({
        openedCollapses: [collapse]
      });
    }
  };

  toggleDeleteModal = () => {
    this._isMounted && this.setState({
      deleteModal: !this.state.deleteModal
    });
  }

  checkPageMode = () => {
    let pageMode=document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    let modalClass;
    switch(pageMode){
      case true:
        bgclass="bg-ligh";
        headerclass="";
        modalClass="modal-white";
        break;
      default:
        bgclass="bg-dark";
        headerclass="text-primary";
        modalClass="modal-black";
        break;
    }
    return {bg: bgclass,hc: headerclass, mc: modalClass};
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };

    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  componentDidMount() {
    this._isMounted = true;
    this.seasonsHistory();
    this.fabricHistory();
    this.getClients();
    this.fabricTypes();
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  componentDidUpdate() {
    if(this._fabricStory === true && this.state.loading === true){
      this._isMounted && this.setState({loading: false});
    }
  }

  /**Fabrics API - editar tela */
  handleEditFabric = (data) => {
    this.collapsesToggle("new");
    //let {rowData} = data;
    let {formdata} = this.state;
    let selectedFabric_type = fabricNames.find(o => o.value === data.fabric_type_abbreviation);
    let selectedSeason = this.state.SeasonsOps.find(o => o.value === data.season.id);
    let selectedClient = this.state.clientOptions.find(o => o.value === data.client.id);
    for(var k in data) formdata[k]=data[k];
    formdata.fabric_type = selectedFabric_type;
    formdata.season = selectedSeason;
    formdata.client = selectedClient;
    formdata.pt = formdata.pt ? {value: true, label: "Plies"} : {value: false, label: "Tubular"}
    this._isMounted && this.setState({
      formdata: formdata,
      action: "update",
      actionText: "Update"
    });
  }

  /**Fabrics API - desactivar tela */
  handleRemoveFabric = (fabric) => {
    this._isMounted && this.setState({toDeleteDM: fabric});
    this.toggleDeleteModal();
  }

  removeSelectedFabric = () => {
    this._isMounted && this.setState({loading: true});
    axios.delete(
      process.env.REACT_APP_IP + 
        '/fabrics/' + this.state.toDeleteDM.id + '/',
      {headers: {"Authorization": "Token " + this.userToken}}
    ).then( function() {
      this._isMounted && this.setState({
        formdata: {},
        action: "insert",
        actionText: "Insert",
        toDeleteDM: {"dm": ''},
        loading: false
      });
      this.toggleDeleteModal();
      this.componentDidMount();
      this.notify("tr", "success", "Fabric disabled!");
    }.bind(this)).catch( function(error) {
      this.handleError(error,"delete fabrics");
      this._isMounted && this.setState({loading: false});
    }.bind(this));
  }

  /**Fabrics API - historico tela */
  fabricHistory = () => {
    axios.get(
      process.env.REACT_APP_IP + '/fabrics/', 
      { headers: {
        "Authorization": "Token " + this.userToken
      } }
    ).then(function (response) {
      this._fabricStory = true;
      this._isMounted && this.setState({dataTable: response.data});
    }.bind(this)).catch(function(error){
      this.handleError(error,"fabrics history");
    }.bind(this));
  }

  /**Fabrics types API - tipos tela */
  fabricTypes = () => {
    axios.get(
      process.env.REACT_APP_IP + '/fabric_types/', 
      { headers: {
        "Authorization": "Token " + this.userToken
      } }
    ).then(function (response) {
      this._fabricType = true;
      this._isMounted && this.setState({
        fabricsTypes: response.data.map(type => {
          return {
            "value": type.abbreviation,
            "label": type.name
          }
        })
      });
    }.bind(this)).catch(function(error){
      this.handleError(error,"fabrics types");
    }.bind(this));
  }

  /**Seasons API - historico temporadas */
  seasonsHistory = () => {
    axios.get(
      process.env.REACT_APP_IP + '/seasons/',
      { headers:{
        "Authorization": "Token " + this.userToken
      } }
    ).then(function (response) {
      let seasonsData=[];
      response.data.map( (season) => 
        seasonsData.push({
          value: season.id, 
          label: season.name
        }) 
      );
      this._isMounted && this.setState({SeasonsOps: seasonsData});
    }.bind(this)).catch(function(error){
      this.handleError(error,"season history");
    }.bind(this));
  }

  /**Clients API - proveedores */
  getClients = () => {
    const clients = [];
    axios.get(
      process.env.REACT_APP_IP + '/clients/',
      { headers:{
        "Authorization": "Token " + this.userToken
      } }
    )
    .then( function(response) {
      response.data.map( (client) => {
        clients.push(
          {value: client.id, label: client.name}
        );
        return client;
      });
      this._isMounted && this.setState({clientOptions: clients})
    }.bind(this)).catch( function(error) {
      this.handleError(error,"get clients");
    }.bind(this));
  }

  /**form helper {Object} data */
  formHelper = (data) => {
    if('certificate' in data){
      let certificate = data.certificate;
      this.setState({
        certificate: certificate
      });
    }
    if('formdata' in data){
      this.setState({
        formdata: {...this.state.formdata, [data.formdata.key]: data.formdata.value}
      });
    }
    if('submit' in data){
      this._isMounted && this.setState({
        loading: true
      });
      if(this.state.action==="update"){
        if(this.state.certificate!==null){
          //this.addcertificate(this.state.action);
          this.addCertificate(
            this.state.certificate,this.state.formdata.id
          ).then(function(){
            this.patchFabric(this.formToDB(this.state.formdata))
          }.bind(this))
        } else {
          this.patchFabric(this.formToDB(this.state.formdata))
        }
      } else if(this.state.action==="insert") {
        this.postFabric(this.formToDB(this.state.formdata))
      }
    }
    if('error' in data){
      this.notify("tr","warning",data.error)
    }
    if('resetForm' in data){
      this._isMounted && this.setState({
        formdata: {},
        action: "insert",
        actionText: "Insert",
        certificate: null
      });
    }
  }

  addCertificate = async (certificate,fabricId) => {
    let formData = new FormData();
    formData.append('file', certificate);
    formData.append('id', fabricId);
    return await uploadFileToS3(
      process.env.REACT_APP_IP + '/fabrics/post_certificate/',
      { headers: {
          "Authorization": "Token " + this.userToken,
          "Content-Type": "multipart/form-data"
      } },
      formData, "file",
      ["pdf", "doc", "docx"]
    )
  }

  postFabric = (data) => {
    data["created_by"] = this.user_id
    axios.post(
      process.env.REACT_APP_IP + '/fabrics/', 
      data, 
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then( function (response) {
      if(this.state.certificate === null) {
        this.notify("tr", "warning", "Dm certificate missing.");
      } else {
        this.addCertificate(this.state.certificate,response.data.id).then(function(){
          this._isMounted && this.setState({
            formdata: {},
            action: "insert",
            actionText: "Insert",
            loading: false,
            certificate: null
          });
          this.componentDidMount();
          this.notify("tr", "success", "Fabric added!");
        }.bind(this))
      }  
    }.bind(this)).catch( function(error) {
      this.handleError(error,"post fabric");
      this._isMounted && this.setState({loading: false,certificate: null});
    }.bind(this));
  }

  patchFabric = (data) => {
    let Id = data.id;
    delete data.id;
    delete data.created_at;
    delete data.updated_at;
    delete data.show_name;
    axios.patch(
      process.env.REACT_APP_IP + '/fabrics/' + Id + '/', 
      data, 
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then( function () {      
      this._isMounted && this.setState({
        formdata: {},
        action: "insert",
        actionText: "Insert",
        loading: false,
        certificate: null
      });
      this.componentDidMount();
      this.notify("tr", "success", "Fabric updated!"); 
      this.updateCostingSheet(Id) 
    }.bind(this)).catch( function(error) {
      this._isMounted && this.setState({
        loading: false,
        certificate: null
      });
      this.componentDidMount();
      this.handleError(error,"patch fabric");
    }.bind(this));
  }

  updateCostingSheet = (id) => {
    let data = new FormData()
    data.append('fabric', id)
    axios.post(
      process.env.REACT_APP_IP + '/fabrics/change_price_of_fabric/', 
      data, 
      {headers:{"Authorization": "Token " + this.userToken}}
    ).then( function () {      
      this.componentDidMount();
      this.notify("tr", "success", "Cost Sheets Updated!");  
    }.bind(this)).catch( function(error) {
      this.componentDidMount();
      this.handleError(error,"patch fabric");
    }.bind(this));

  }

  formToDB = (currentFormdata) => {
    let dbData = {
      ...currentFormdata,
      fabric_type: currentFormdata.fabric_type.value,
      pt: currentFormdata.pt.value,
      season: currentFormdata.season.value,
      client: currentFormdata.client.value
    }
    return dbData;
  }

  render() {
    const pageMode=this.checkPageMode();
    let inverted = pageMode.bg==="bg-ligh" ? 'inverted' : '';
    return (<>
      <div className="blackdiv" id="blackdiv" style={this.state.loading?{display:'flex'}:{display:'none'}}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{minHeight:400}}><p>&nbsp;</p></div>
        </div>
      </div>
      <div className="content" >
        <Modal isOpen={this.state.deleteModal} 
          toggle={this.toggleDeleteModal} 
          modalClassName={this.checkPageMode().mc}
        >
          <ModalHeader className="justify-content-center" 
            toggle={this.toggleDeleteModal}>
            Disable Fabric.
          </ModalHeader>
          <ModalBody>
            <h6 className="text-danger">Warning!</h6>
            <p>
              By disabling this fabric: <b className="text-warning">{this.state.toDeleteDM.dm}</b>&nbsp;
              you won't be able to use it in future costings.
              Contact the system administrator to recover a fabric.
            </p>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleDeleteModal}>
              Close
            </Button>
            <Button color="primary" onClick={this.removeSelectedFabric}>
              Disable fabric
            </Button>
          </ModalFooter>
        </Modal>

        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert"/>
        </div>
        <Button 
          color="primary" 
          onClick={(e) => {
            e.preventDefault(); 
            this.collapsesToggle("new")
          }}
        >
          Add Fabric
        </Button>
        <Button 
          color="primary" 
          onClick={(e) => {
            e.preventDefault();
            this.collapsesToggle("table");
          }}
        >
          Fabrics Table
        </Button>        
        <Button color="primary" 
          onClick={event=>{
            event.preventDefault();
            window.open(process.env.REACT_APP_IP+'/fabrics')
          }}
        >
          Download Fabrics
        </Button>
        <Collapse
          isOpen = {this.state.openedCollapses.includes("new")}
        >
          <Card>
            <CardBody>
              {this._fabricType && <FabricsForm 
                action={this.state.action}
                actionText={this.state.actionText}
                dataForm = {this.state.formdata}
                fabricNames={this.state.fabricsTypes}
                seasons = {this.state.SeasonsOps}
                clients = {this.state.clientOptions}
                helper = {data => this.formHelper(data)}
              />}
            </CardBody>
          </Card>
        </Collapse>
        <Collapse
          isOpen = {this.state.openedCollapses.includes("table")}
        >
          <Card>
            <CardBody>
              {this.Access.fabrics.view && this._fabricStory && <FabricsHistory 
                itemsPages = {this.state.itemsPages}
                dataTable = {this.state.dataTable}
                editFabric = {this.handleEditFabric}
                removeFabric = {this.handleRemoveFabric}
              />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
    </>);
  }
}

export default Fabrics;