import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";
import { downloadFileFromS3 } from "../helpers/awsS3";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  ModalHeader,
  Label,
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  TabPane,
  TabContent,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};

/* eslint-disable-next-line */
const nameComponent = {
  S: "SELF",
  C1: "CONTRAST1",
  C2: "CONTRAST2",
  C3: "CONTRAST3",
  C4: "CONTRAST4",
  C5: "CONTRAST5",
  R: "RIB",
  R1: "RIB1",
  R2: "RIB2",
  R3: "RIB3",
  R4: "RIB4",
  J: "JERSEY",
  J1: "JERSEY1",
  J2: "JERSEY2",
  J3: "JERSEY3",
  W: "WOVEN",
  W1: "WOVEN1",
  WA: "WATTA",
  WA1: "WATTA1",
  M: "MESH",
  M1: "MESH1",
  A: "APLIQUE",
  A1: "APLIQUE1",
  I1: "INTERLINING",
  I2: "INTERLINING2",
  I3: "INTERLINING3",
  WAF: "WAFFLE",
  TRI: "TRICOT",
  TAF: "TAFFETA",
  TA1: "TAFFETA1",
};


class PoClient extends React.Component {
  constructor() {
    super();
    this.state = {
      vTabs: "vt3",
      season: [],
      client: [],
      boms_styles: [],
      selectedPSeason: null,
      costing_styles: [],
      styleName: "",
      editModal: false,
      loading: false,
      styleOptions: [],
      styleOptionsCombo: [],
      style: {},
      styleComboSelected: null,
      stylesCombo: [],
      stylesStyle: [],
      styleSelected: null,
      description: [],
      styles_po: [],
      costingSketch: null,
      techpack: null,
      combos: [],
      colorFabric: null,
      component_name: null,
      selectedStyle: [],
      validate: false,
      styleWcombo: [],
      DMselect: null,
      ColorSelect: null,
      flag: "",
      clientSelect: null,
    };
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  addCard = (styles) => {
    return (
      <Card>
        <CardBody>
          <FormGroup row>
            <Label for="exampleEmail" className="text-info" sm={12}>
              DM:
              <br></br>
              <Label>
                <FormGroup>
                  <Select
                    // options={this.state.styles_po}
                    onChange={(e) => {
                      //combo[id][key].color_fabric = e.value
                    }}
                  />
                </FormGroup>
              </Label>
            </Label>
            <Label for="exampleEmail" className="text-info" sm={12}>
              {"Component Name: "}
              <Label>nameComponent[dm.component_name]</Label>
            </Label>
            <Label for="exampleEmail" className="text-info" sm={12}>
              {"Color Style: "}
              <Label>index.color</Label>
            </Label>
          </FormGroup>
          <FormGroup>
            <Select
              // options={this.state.colorFabric}
              onChange={(e) => {
                //   combo[id][key].color_fabric = e.value
              }}
            />
          </FormGroup>
          <FormGroup row>
            <Label for="exampleText" sm={12} className="text-primary">
              Description
            </Label>
            <Col sm={12}>
              <Input
                type="textarea"
                //   value={header.description}
                onChange={(e) => {
                  //    combo[id][key].description = e.target.value
                }}
              />
            </Col>
          </FormGroup>
        </CardBody>
      </Card>
    );
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  toggleEditModal = () => {
    this.setState({
      editModal: !this.state.editModal,
    });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  getStyle = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + `/style/styles_for_combos/`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            styleOptions: response.data.map((client) => {
              return {
                id: client.id,
                costing_id: client.costing,
                actions: (
                  <>
                    <div className="">
                      <Button
                        //onClick={() => this.handleReview(client)}
                        onClick={() => {
                          this.getStyleInfo(client.id);
                          console.log("mamadas")
                        }}
                        color="info"
                        size="sm"
                      >
                        EDIT <i className="fa fa-edit" />
                      </Button>
                      <br></br>
                      {/*<Button
                    onClick={() => this.handleReject(header.id)}
                    color="danger"
                    size="sm"
                  >
                    Reject <i className={"fa fa-times"} />
                  </Button>*/}
                    </div>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };
  getClients = () => {
    this.toggleLoading();
    let cliente = [];
    axios
      .get(
        process.env.REACT_APP_IP + `/clients/`,
        // data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          response.data.map((season) => {
            //console.log(season);
            cliente.push({ value: season.id, label: season.name });
          });
          console.log(cliente);
          this.setState({ client: cliente });
          console.log(this.setState.client)
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };
  getSeasons = () => {
    this.toggleLoading();
    let seasons = [];
    axios
      .get(
        process.env.REACT_APP_IP + `/seasons/`,
        // data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          response.data.map((season) => {
            //console.log(season);
            seasons.push({ value: season.id, label: season.name });
          });
          //console.log(response.data);
          this.setState({ season: seasons });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };
  get_costing_and_boms = (id, styleName) => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/combos/get_costings_and_boms/?base_style=${styleName}&season=${id}`,
        // data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          //response.data
          this.setState({
            boms_styles: response.data.boms_styles,
            costing_styles: response.data.costing_styles,
          });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };
  
  post_costing_and_boms = (id, styleName,id_cliente) => {
    console.log(id, styleName,id_cliente);
    //id = 4;
    const formData = new FormData();
    formData.append("season", id);
    formData.append("client",id_cliente);
    formData.append("base_style",styleName);
    axios
      .post(
        process.env.REACT_APP_IP +`/combos/`,
        formData,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      ).then(function () {
        //this.toggleLoading();
        this.componentDidMount();
        this.notify("tr", "success", "Created");
      }.bind(this)).catch(function (error) {
        //this.toggleLoading();
        this.componentDidMount();
        this.notify("tr", "warning", "Error");
      }.bind(this))
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
      this.setState({
        styleName: '',
        selectedPSeason: null,
        selectedClient: null,
        costing_styles: [],
        boms_styles:[],
        editModal: !this.state.editModal,
        vTabs: "vt3",
      });
  };

  getStyleCombo = () => {
    this.toggleLoading();
    let data = new FormData();
    data.append("flag", "N");
    axios
      .get(
        process.env.REACT_APP_IP + `/combos/?flag=N`,
        // data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            styleOptionsCombo: response.data.map((client) => {
              return {
                id: client.id,
                costing_id: client.styles,
                actions: (
                  <>
                    <div className="">
                      <Button
                        onClick={() => this.handleReview(client.id)}
                        color="info"
                        size="sm"
                      >
                        EDIT <i className="fa fa-edit" />
                      </Button>
                      <br></br>
                      {/*<Button
                    onClick={() => this.handleReject(header.id)}
                    color="danger"
                    size="sm"
                  >
                    Reject <i className={"fa fa-times"} />
                  </Button>*/}
                    </div>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  _clientsLoaded = false;
  getStyleInfo = (id) => {
    //this.toggleLoading();
    id = id.flatMap((x) => x.split(" ")[0]);
    console.log("ids", id);
    const formData = new FormData();
    formData.append("styles", id);
    axios
      .post(this.costingURL + "/combos/get_info_style/", formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              styles_po: response.data,
              clientSelect: response.data.style && response.data.style.client,
            });
          console.log("client", this.state.clientSelect);
          // this.getdataSelect(response.data)
          this.downloadSketch(
            this.state.styles_po.costing &&
              this.state.styles_po.costing.pathToSketch
          );

          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getStyleInfoCombo = (id) => {
    //this.toggleLoading();
    const formData = new FormData();
    formData.append("style", id);
    axios
      .post(this.costingURL + "/combos_components/get_resume/", formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              stylesCombo: response.data["combos"],
              stylesStyle: response.data["styles"],
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getdataSelect = (style) => {
    this.setState({
      DMselect: style.map((client) => {
        return {
          value: client.id,
          label: `${client.dm} `,
        };
      }),
    });
  };

  getColorFabric = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/colors_fabrics/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              colorFabric: response.data.map((client) => {
                return {
                  value: client.id,
                  label: `${client.color} `,
                };
              }),
            });

          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getInfoStyle = (id, data) => {
    const formData = new FormData();
    formData.append("styles", id);
    axios
      .post(this.costingURL + "/combos/get_info_style", formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              styleSelected: response.data,
            });
          // this.getdataSelect(response.data)
          this.downloadSketch(
            this.state.styles_po.costing &&
              this.state.styles_po.costing.pathToSketch
          );
          this.changepage(data, response.data);
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  changepage = (data, data2) => {
    const path = "/admin/account_components";
    this.props.history.push({
      pathname: path,
      state: {
        styleSelected: data2,
        stylesCombo: data,
        styles_po: this.state.styles_po,
        flag: "N",
        costingSketch: this.state.costingSketch,
        colorFabric: this.state.colorFabric,
      },
    });
  };

  handleReview = (header_id) => {
    const formData = new FormData();
    this.toggleLoading();
    let id = header_id;
    formData.append("style", id.id);
    axios
    .get(
      this.costingURL + `/combos/${header_id}/`,
      // formData,
      {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      }
      )
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
          this.setState({
            stylesCombo: response.data,
          });
          // this.getInfoStyle(id.id, response.data["combos"])
          //this.toggleLoading();
          this.downloadSketch(
            this.state.styles_po.costing &&
            this.state.styles_po.costing.pathToSketch
            );
            setTimeout(() => {
            this.toggleLoading();
            this.changepage(response.data, response.data);
          }, 4000);
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getStyleWCombo = (id) => {
    axios
      .get(this.costingURL + "/combos/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._isMounted &&
            this.setState({
              styleWcombo: response.data.map((client) => {
                return {
                  id: client.id,
                  costing_id: client.styles,
                  actions: (
                    <>
                      <div className="">
                        <Button
                          onClick={() => this.handleReview(client.id)}
                          color="info"
                          size="sm"
                        >
                          Review <i className="fa fa-edit" />
                        </Button>
                        <br></br>
                        {/*<Button
                      onClick={() => this.handleReject(header.id)}
                      color="danger"
                      size="sm"
                    >
                      Reject <i className={"fa fa-times"} />
                    </Button>*/}
                      </div>
                    </>
                  ),
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  };

  _sketchLoaded = false;
  downloadSketch = (file) => {
    const url = this.costingURL + "/costingHeader/download/?key=" + file;
    const headers = {
      params: { key: file },
      headers: { Authorization: "Token " + this.userToken },
    };
    axios.get(url, headers).then(
      function(response) {
        this._sketchLoaded = true;
        this.setState({
          costingSketch: response.data.url,
        });
      }.bind(this)
    );
  };

  //_sketchLoaded = false;
  downloadTechPack = (filekye) => {
    //console.log('download techpack', filekye);
    //event.preventDefault();
    let url = "";
    url = process.env.REACT_APP_IP + "/costingHeader/download/?key=" + filekye;
    //url = process.env.REACT_APP_IP + '/bom_header/download/?key=' + filekye;
    let config = {
      params: { key: filekye },
      headers: {
        Authorization: "Token " + this.userToken,
      },
    };
    downloadFileFromS3(url, config)
      .then(
        function(response) {
          const result = { file: response };
          window.open(result.file);
          this.notify("tr", "success", " downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "download file");
        }.bind(this)
      );
  };

  handleSelectChange = (event) => {
    let opts = [],
      opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
        console.log("opt", opt.value);
      }
    }

    this.setState({ selectedStyle: opts });
  };

  changeFlag = (id) => {
    const formData = new FormData();
    formData.append("style", id[0]);
    formData.append("flag", "A");
    axios
      .post(
        this.costingURL + "/combos_components/change_cc_status/",
        formData,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function() {
          this._isMounted && this.notify("tr", "success", "APPROVED!");
          this.componentDidMount();
          const path = "/admin/create_combocomponent";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  };

  approveCombo = (combo, id) => {
    console.log(id);
    let order = { combos: null };
    let orde;
    let ids;
    ids = id.flatMap((x) => parseInt(x.split(" ")[0]));
    orde = combo.flatMap((x) => x.flatMap((y) => [y]));
    order.combos = orde;
    console.log(order);
    axios
      .patch(
        this.costingURL + `/combos/${id}`,
        {
          components: orde,
        },
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function() {
          this._isMounted && this.notify("tr", "success", "SAVED!");
          this.componentDidMount();
          const path = "/admin/create_combocomponent";
          this.props.history.push({
            pathname: path,
          });
          this.changeFlag(ids);
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  };

  orderCombos = (combo, id) => {
    let order = { combos: null };
    let orde;
    let ids;
    ids = id.flatMap((x) => parseInt(x.split(" ")[0]));
    orde = combo.flatMap((x) => x.flatMap((y) => [y]));
    order.combos = orde;
    order = order + ids;
    console.log("order", order);
    axios
      .post(
        this.costingURL + "/combos_components/",
        (order = {
          styles: ids,
          combos: orde,
        }),
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function() {
          this._isMounted && this.notify("tr", "success", "SAVED!");
          // this.componentDidMount();
          const path = "/admin/create_combocomponent";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  };

  validateCombos = (combo) => {
    let order = { combos: null };
    let orde;
    orde = combo.flatMap((x) => x.flatMap((y) => [y]));
    order.combos = orde;

    this.approveCombo(combo, this.state.selectedStyle);
  };

  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    //this.getStyle()
    this.getColorFabric();
    this.getStyleCombo();
    this.getStyleWCombo();
    this.getSeasons();
    //this.getClients();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const {
      loading,
      styles_po,
    } = this.state;

    // const selectedStyle = this.state.styleOptions.find(
    //  o=>o.value === this.state.ColorsForm.client
    // )

    const pageMode = this.checkPageMode();
      styles_po.colors &&
      styles_po.colors.map(
        (index, ids) =>
          styles_po.dms &&
          styles_po.dms.map((dm, key) => ({
            dm: dm.id,
            color: index.id,
            description: "",
            color_fabric: "",
          }))
      );
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Nav pills className="nav-pills-info nav-pills-icons">
          <NavItem>
            <NavLink
              className={this.state.vTabs === "vt2" ? "active" : ""}
              onClick={() => this.setState({ vTabs: "vt2" })}
            >
              Combos History
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={this.state.vTabs === "vt3" ? "active" : ""}
              onClick={() => this.setState({ vTabs: "vt3" })}
            >
              Edit Combos
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={this.state.vTabs === "vt4" ? "active" : ""}
              onClick={() =>
                this.setState({
                  vTabs: "vt4",
                  editModal: !this.state.editModal,
                })
              }
            >
              Create Combos
            </NavLink>
          </NavItem>
        </Nav>

        <TabContent activeTab={this.state.vTabs}>
          <TabPane tabId="vt2">
            <Row>
              <Col>
                <ReactTable
                  responsive
                  data={this.state.styleWcombo}
                  filterable
                  defaultFilterMethod={(filter, row) => {
                    const id = filter.pivotId || filter.id;
                    //console.log(filter,row[id],id);
                    const filterValue =
                      filter.id === "codigo"
                        ? parseFloat(filter.value)
                        : filter.value.toLowerCase();
                    const filterRow =
                      id === "codigo" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined
                      ? String(filterRow).startsWith(filterValue)
                      : true;
                  }}
                  columns={[
                    {
                      Header: "ID",
                      accessor: "id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["id"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "Costing",
                      accessor: "costing_id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, {
                          keys: ["costing_id"],
                        }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "ACTION",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 250,
                    },
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true,
                    },
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="vt3">
            <Row>
              <Col>
                <ReactTable
                  responsive
                  data={this.state.styleOptionsCombo}
                  filterable
                  defaultFilterMethod={(filter, row) => {
                    const id = filter.pivotId || filter.id;
                    //console.log(filter,row[id],id);
                    const filterValue =
                      filter.id === "codigo"
                        ? parseFloat(filter.value)
                        : filter.value.toLowerCase();
                    const filterRow =
                      id === "codigo" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined
                      ? String(filterRow).startsWith(filterValue)
                      : true;
                  }}
                  columns={[
                    {
                      Header: "ID",
                      accessor: "id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["id"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "style list",
                      accessor: "costing_id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, {
                          keys: ["costing_id"],
                        }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "ACTION",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 250,
                    },
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true,
                    },
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="vt4">
            <Row>
              <Col>
                <Modal
                  isOpen={this.state.editModal}
                  toggle={this.toggleEditModal}
                  modalClassName={this.checkPageMode().mc}
                >
                  <ModalHeader
                    className="justify-content-center"
                    toggle={this.toggleImportModal}
                  >
                    Get Combos by Style and Season
                  </ModalHeader>
                  <ModalBody>
                    <Row>
                      <Col sm={12}>
                        <Label>Style </Label>
                        <Input
                          type="text"
                          placeholder={this.state.styleName}
                          name="name"
                          //defaultValue='{this.state.editForm.editName}'
                          onChange={(e) => {
                            this.setState({
                              styleName: e.target.value,
                            });
                          }}
                        />
                      </Col>
                      <Col sm={12}>
                        <Label>Select Season</Label>
                        <Row>
                          <Col sm={12}>
                            <Select
                              placeholder="Select Season"
                              styles={customStyles}
                              options={this.state.season}
                              onChange={(e) => {
                                this.setState({ selectedPSeason: e });
                              }}
                            />
                          </Col>
                        </Row>
                      </Col>
                      <Col sm={12}>
                        <Label>Select Client</Label>
                        <Row>
                          <Col sm={12}>
                            <Select
                              placeholder="Select Client"
                              styles={customStyles}
                              options={this.state.client}
                              onChange={(e) => {
                                this.setState({ selectedClient: e });
                              }}
                            />
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={12}>
                        <Row>
                          <Col sm={6}>
                            <Label>Style </Label>
                          </Col>
                          <Col sm={6}>
                            <Label>Costing</Label>
                          </Col>
                        </Row>
                        
                        {this.state.boms_styles.map((bom,idx) => {
                          return (
                            <Row>
                              <Col sm={6}>
                                <Label>{bom}</Label>
                              </Col>
                              <Col sm={6}>
                                <Label>{this.state.costing_styles[idx]}</Label>
                              </Col>
                            </Row>
                          );
                        })}
                        
                        
                      </Col>
                    </Row>
                  </ModalBody>
                  <ModalFooter>
                    <Button
                      color="secondary"
                      onClick={() => {
                        this.toggleEditModal();
                        this.setState({
                          styleName: '',
                          selectedPSeason: null,
                          selectedClient: null,
                          costing_styles: [],
                          boms_styles:[],
                        });
                      }}
                    >
                      Close
                    </Button>
                    <Button
                      color="primary"
                      onClick={() =>
                        this.get_costing_and_boms(
                          this.state.selectedPSeason.value,
                          this.state.styleName
                        )
                      }
                    >
                      Get
                    </Button>
                    <Button
                      color="primary"
                      onClick={() => 
                        //console.log(this..selectedClient)
                        this.post_costing_and_boms(
                          this.state.selectedPSeason.value,
                          this.state.styleName,
                          this.state.selectedClient.value,
                        )
                      }
                    >
                      Create
                    </Button>
                  </ModalFooter>
                </Modal>
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </div>
    );
  }
}
export default PoClient;
