import React from "react";
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import axios from 'axios';

import {
  Form,
  Button,
  TabContent,
  TabPane,
  Col,
  FormGroup,
  Label,
  Card,
  CardBody,
  Input,
  CardHeader,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import { matchSorter } from 'match-sorter';

class Suppliers extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      loading: false,
      loadingColors: false,
      providers: [],
      tab: "1"
    }
  }

  toggle = (tab) => {
    if (this.state.tab !== tab) {
      this.setState({
        tab: tab,
      });
    }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  _loadingProviders = false;
  getProductionProvider = () => {
    axios.get(
      this.costingURL + '/provider/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function (response) {
      this._loadingProviders = true;
      const { data } = response;
      this.setState({
        providers: data.map(provider => {
          return {
            value: provider.id,
            label: provider.name,
            abbreviation: provider.abbreviation,
            address: provider.address,
            phone: provider.phone,
            contact: provider.contact,
            time: provider.time,
            currency: provider.currency,
            company: provider.company.name,
            status: provider.status
          }
        })
      })
    }.bind(this))
  }
  deleteProvider = (id) => {
    this.toggleLoading();
    axios
      .delete(this.costingURL + `/provider/${id}/`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function() {
          this.notify("tr", "success", "Provider Deleted!");
          this.toggleLoading();
          this.componentDidMount();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.handleError(error, "Provider not Deleted");
          this.toggleLoading();
        }.bind(this)
      );
  };

  componentDidMount() {
    this.getProductionProvider();
    this._isMounted = true;
  }

  render() {
    const { tab, loading } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content" >
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <Card>
          <CardHeader>
            <CardBody>
              <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>
              <Nav tabs>
                <NavItem>
                  <NavLink
                    className={tab === "1" ? "active" : ""} 
                    onClick={() => {
                      this.toggle("1");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                            color: "black",
                            borderColor: "black",
                          }
                        : {}
                    }
                  >
                    Catalogo de provedores
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={tab === "2" ? "active" : ""}
                    onClick={() => {
                      this.toggle("2");
                    }}
                    style={
                      pageMode.bg === "bg-ligh"
                        ? {
                          color: "black",
                          borderColor: "black",
                        }
                        : {}
                    }
                  >
                    Crear provedores
                  </NavLink>
                </NavItem>
              </Nav>
              <br></br>
              <TabContent activeTab={tab}>
                <TabPane tabId="2">
                  <Col xs={4}>
                    <h3>Informacion de Provedores</h3>
                    <Form>
                      <FormGroup>
                        <Label>Codigo</Label>
                        <Input type="text"
                          maxLength="25"
                          onChange={e => {

                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Nombre</Label>
                        <Input type="text"
                          onChange={e => {

                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Direccion</Label>
                        <Input type="text"
                          onChange={e => {

                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Telefono</Label>
                        <Input type="text"
                          onChange={e => {

                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Contacto</Label>
                        <Input type="text"
                          onChange={e => {

                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Tiempo</Label>
                        <Input type="text"
                          onChange={e => {

                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Pagado en</Label>
                        <Input type="text"
                          onChange={e => {
                          }}
                        />
                      </FormGroup>
                      <FormGroup>
                        <Label>Moneda</Label>
                        <Input type="text"
                          onChange={e => {

                          }}
                        />
                        <Button type="button"
                          size="sm"
                          color="secundary"
                          className="float-left"
                          onChange={e => {
                          }}
                        >
                          Submit
                        </Button>
                      </FormGroup>
                    </Form>
                  </Col>
                </TabPane>
              </TabContent>
              <TabContent activeTab={tab}>
                <TabPane tabId="1">
                  <Col>
                    <h3>Catalogo de provedores</h3>
                    {this.getProductionProvider && <ReactTable
                      data={this.state.providers.map(provider => {
                        return {
                          ...provider,
                          id: provider.value,
                          actions: (<div className="clearfix">
                            <Button size="sm"
                              color="danger"
                              className="float-right"
                              onClick={() => {
                                this.deleteProvider(provider.value)
                                //console.log(provider.value)
                              }}
                            >
                              <i className="tim-icons icon-simple-remove" />
                            </Button>
                            <Button size="sm"
                              color="info"
                              className="float-right"
                              onClick={() => {
                                console.log(provider)
                              }}
                            >
                              <i className="tim-icons icon-pencil" />
                            </Button>
                          </div>)
                        }
                      })}
                      filterable
                      defaultPageSize={5}
                      columns={[
                        {
                          Header: "Codigo",
                          accessor: "value",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["value"] }),
                          filterAll: true,
                          disabled: false,
                          width: 80,
                        },
                        {
                          Header: "Nombre",
                          accessor: "label",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["label"] }),
                          filterAll: true,
                        },
                        {
                          Header: "Direccion",
                          accessor: "address",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["address"] }),
                          filterAll: true,
                          disabled: false,
                        },
                        {
                          Header: "Telefono",
                          accessor: "phone",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["phone"] }),
                          filterAll: true,
                          disabled: false,
                          width: 130,
                        },
                        {
                          Header: "Contacto",
                          accessor: "contact",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["contact"] }),
                          filterAll: true,
                          disabled: false,
                          width: 170,
                        },
                        {
                          Header: "Tiempo",
                          accessor: "time",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["time"] }),
                          filterAll: true,
                          disabled: false,
                          width: 80,
                        },
                        {
                          Header: "Pagado",
                          accessor: "company",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["company"] }),//cambiar key
                          filterAll: true,
                          width: 150,
                        },
                        {
                          Header: "Moneda",
                          accessor: "currency",
                          sortable: true,
                          filterMethod: (filter, row) =>
                            matchSorter(row, filter.value, { keys: ["currency"] }),//cambiar key
                          filterAll: true,
                          width: 80,
                        },
                        {
                          Header: "Actions",
                          accessor: "actions",
                          sortable: true,
                          filterable: true,
                          width: 140,
                        },
                      ]}
                    />}
                  </Col>
                </TabPane>
              </TabContent>
            </CardBody>
          </CardHeader>
        </Card>

      </div>
    )
  }
}
export default Suppliers;