/*eslint-disable no-unused-vars*/
import React from "react";
import {
  Card,
  CardBody,
  Row,
  Col,
  Label,
  Input,
  Button,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  FormGroup,
} from "reactstrap";
import axios from "axios";
import NotificationAlert from "react-notification-alert";
import Select from "react-select";
import ReactTable from "react-table";
import { matchSorter } from "match-sorter";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};

export default class Style extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  _isMounted = false;
  fenixURL = "http://testing.zuntexapparel.com/api/v1"
  //"http://testing.zuntexapparel.com/api/v1";
  costingURL = process.env.REACT_APP_IP;
  constructor() {
    super();
    this.state = {
      editModal: false,
      editModalSeason: false,
      loading: false,
      styleName: undefined,
      costingId: 0,
      costsheets: [],
      valueClientCostsheets: null,
      selectedCostSheet: {},
      selectedCostSheetOptions: {},
      StylesList: [],
      materials: [],
      seasons: [],
      selectedColors: [],
      selectedPlaces: [],
      seasonSelectNew: null,
      seasonSelectOld: null,
      material_list: [],
      Allmaterials: [],
      material_consumption: [],
      editedStyle: undefined,
      header: {
        embellishment_description: [],
      },
      editForm: {
        idStyle: undefined,
        editName: undefined,
        editCosting: undefined,
        editPlaces: undefined,
        editColors: undefined,
      },
      modalMaterials: {
        isOpen: false,
        index: null,
        edit: false,
        matIndex: null,
      },
      modalSeason: {
        isOpen: false,
        index: null,
        edit: false,
        matIndex: null,
      },
      materialEditForm: {
        material: null,
        consumption: "",
      },
      modalEditMat: false,
      materialForm: {
        material: null,
        consumption: "",
      },
    };
  }

  _seasons = false;
  getSeason = () => {
    this.toggleLoading();
    axios.get(this.costingURL + `/seasons/`, {
      headers: {
        Authorization: "Token " + this.userToken,
      },
    })
      .then(
        function (response) {
          this._seasons = true;
          this.setState({
            seasons: response.data.map((season) => {
              //console.log(material)
              return {
                value: season.id,
                label: `${season.name}`
              }
            })
          })
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "Error Loading Seasons");
        }.bind(this)
      );
  }

  toggleEditModal = () => {
    this._isMounted &&
      this.setState({
        editModal: !this.state.editModal,
      });
  };

  _editStyle = false;
  editStyle = (costingid, id, name) => {
    //console.log(id, name)
    this._editStyle = true;
    this.toggleEditModal();
    this.setState({
      editedStyle: id,
      editForm: {
        idStyle: id,
        editName: name,
        editCosting: costingid,
      },
    });
  };

  toggleAddMaterial = (index = null, edit, matIndex = null) => {
    const { modalMaterials } = this.state;
    this.setState({
      modalMaterials: {
        ...modalMaterials,
        isOpen: !modalMaterials.isOpen,
        index: index,
        edit: edit,
        matIndex: matIndex,
      },
    });
    if (index && edit === false && matIndex === null) {
      this.setState({
        materialForm: {
          material: null,
          consumption: null,
        },
      });
    }
  };

  toggleAddSeason = (index = null, edit, matIndex = null) => {
    const { modalSeason } = this.state;
    this.setState({
      modalSeason: {
        ...modalSeason,
        isOpen: !modalSeason.isOpen,
        index: index,
        edit: edit,
        matIndex: matIndex,
      },
    });
  }
  
  toggleEditMaterial = (index = null, edit, matIndex = null) => {
    const { modalMaterials } = this.state;
    this.setState({
      modalEditMat: !this.state.modalEditMat,
    });
    if (index && edit === false && matIndex === null) {
      this.setState({
        materialForm: {
          material: null,
          consumption: null,
        },
      });
    }
  };

  _loadedMaterials = false;
  getProductionMaterials = () => {
    this.toggleLoading();
    axios
      .get(this.costingURL + `/materialp/`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function (response) {
          this._loadedMaterials = true;
          //console.log(response.data)
          this.setState({
            Allmaterials: response.data.map((material) => {
              //console.log(material)
              return {
                bom_material_id: 0,
                old_costing_id: material.old_costing_id,
                value: material.id,
                label: `${material.description}/ ${material.old_costing_id}`,
                name: material.description,
                actions: (
                  <>
                    <Button
                      size="sm"
                      color="info"
                      className="btn-icon btn-link"
                      onClick={() => {
                        //console.log(color)
                        this.loadFormMaterial({
                          id: material.id,
                          label: `${material.description}/ ${material.old_costing_id}`,
                        });
                      }}
                    >
                      <i className="tim-icons icon-pencil" />
                    </Button>
                    <Button
                      size="sm"
                      color="danger"
                      className="btn-icon btn-link"
                      onClick={() => {
                        //console.log(color.id)
                        this.RemoveMaterial(material.id);
                      }}
                    >
                      <i className="tim-icons icon-simple-remove" />
                    </Button>
                  </>
                ),
                //label: `${material.description} / ${material.size.name} / ${material.old_costing_id} / ${material.client.abbreviation}`,
                //old_costing_id: material.old_costing_id,
                //measure: material.measure.name,
              };
            }),
          });
        }.bind(this)
      );
  };

  editMaterialEmbellishment = () => {
    const consumption_list = [...this.state.material_consumption];
    const material_list = [...this.state.materials];
    consumption_list[this.state.materialEditForm.index] = this.state.materialEditForm.consumption;
    material_list[this.state.materialEditForm.index].consumption = this.state.materialEditForm.consumption;
    this.setState({
      material_consumption: consumption_list,
      materials: material_list,
      materialEditForm: {
        material: null,
        consumption: null,
      },
    });
    this.toggleEditMaterial();
  };

  addMaterialToEmbellishment = () => {
    //console.log('addMaterialToEmbellishment');
    let {
      header,
      header: { embellishment_description },
      materialForm,
      modalMaterials,
    } = this.state;
    if (
      !materialForm.consumption ||
      materialForm.consumption < 0 ||
      materialForm.material === null
    ) {
      this.notify("tr", "warning", "Material inputs required");
      return false;
    }
    const list = [...this.state.material_list];
    const consumption_list = [...this.state.material_consumption];
    const material_list = [...this.state.materials];
    if (list.find((material) => material === materialForm.material.value)) {
      this.notify("tr", "warning", "Material already exists in this style");
      return false;
    } else {
      material_list.push(materialForm);
      list.push(materialForm.material.value);
      consumption_list.push(materialForm.consumption);
      console.log("llego hasta aqui");
      /*embellishment_description[modalMaterials.index].materials = [
        ...list,
        {
          material_id: materialForm.material.value,
          consumption: materialForm.consumption,
          bom_material_id: 0,
          old_costing_id: materialForm.material.old_costing_id,
          measure: materialForm.material.measure,
        },
      ];*/
      this.setState({
        material_list: list,
        material_consumption: consumption_list,
        materials: material_list,
        materialForm: {
          material: null,
          consumption: null,
          bom_material_id: 0,
          old_costing_id: null,
          measure: null,
        },
      });
      this.toggleAddMaterial();
    }
  };

  loadFormMaterial = (id) => {
    let element = [...this.state.material_list];
    const material = element.find((item) => item === id.id);
    const consumption = element.findIndex((item) => item === id.id);
    this.setState({
      materialEditForm: {
        material: id.label,
        consumption: this.state.material_consumption[consumption],
        index: consumption,
      },
      modalMaterials: {
        edit: true,
      },
    });
    this.toggleEditMaterial();
    //console.log(this.state.material_list, this.state.material_consumption, element.find(item => item === id.id))
  };

  RemoveMaterial = (material_id) => {

    let element = [...this.state.material_list];
    const material = element.findIndex((item) => item === material_id);
    const list = [...this.state.material_list];
    const consumption_list = [...this.state.material_consumption];
    const material_list = [...this.state.materials];
    list.splice(material, 1)
    consumption_list.splice(material, 1)
    material_list.splice(material, 1)
    this.setState({
      material_list: list,
      material_consumption: consumption_list,
      materials: material_list,
    });
  };

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };
  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }
  componentWillUnmount() {
    this._isMounted = false;
  }

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  _selectedCostsheet = false;
  getSelectedCostSheet = (id) => {
    this.toggleLoading();
    axios
      .get(this.costingURL + `/costingHeader/new_BOM/?costing_id=${id}`, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._selectedCostsheet = true;
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "selected");
        }.bind(this)
      );
  };

  costingValue = (value) => {
   // console.log('value', value)
    this.setState({
      valueClientCostsheets: value
    })
  }

  getColors = (id) => {
    this.toggleLoading();
    axios
      .get(this.costingURL + `/color_by_client/get_color_style/?costing=${id}`, 
      {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              colors: response.data.map((color) => {
                return {
                  value: color.id,
                  label: `${color.name}`,
                  name: color.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Failed Load Colors");
        }.bind(this)
      );
  };

  getDestinations = () => {
    this.toggleLoading();
    axios
      .get(this.costingURL + "/destionation/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              places: response.data.map((place) => {
                return {
                  value: place.id,
                  label: `${place.name} / ${place.clients}`,
                  name: place.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Failed Load Sizes Scales");
        }.bind(this)
      );
  };

  duplicateSeason(newS, oldS) {
    console.log(newS, oldS);
    this.toggleLoading();
    //this.componentDidMount();

    let data = new FormData();
    data.append("new", newS); //J
    data.append("old", oldS);
    axios
      .post(process.env.REACT_APP_IP + "/style/",
        data,
        {
          headers: { Authorization: "Token " + this.userToken, },
        })
      .then(
        function () {
          //this.toggleLoading();
          this._isMounted && this.notify("tr", "success", "Season Duplicated!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Network Error");
        }.bind(this)
      );
    this.componentDidMount();

  }

  CreateStyle(costingId, styleName, value) {
    //console.log(costingId, styleName);
    this.toggleLoading();
    //this.componentDidMount();
    if (styleName.length > 20) {
      this.notify("tr", "warning", "name cannot be more than 20 characters");
      return;
    }
    let data = new FormData();
    data.append("name", styleName); //J
    //data.append("season", this.state.selectedSeason);
    data.append("costing_id", costingId);
    value === 0 && data.append("colors", this.state.selectedColors);
    value === 0 && data.append("destinations", this.state.selectedPlaces);
    value === 0 && data.append("materials", this.state.material_list.join())
    value === 0 && data.append("consumptions",this.state.material_consumption.join())
    axios
      .post(process.env.REACT_APP_IP + "/style/", data, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function () {
          this.toggleLoading();
          this.setState({
            styleName: undefined,
            //selectedCostSheetOptions: undefined,
            //selectedSeason: undefined,
            selectedColors: [],
            selectedPlaces: [],
            materials: [],
            material_consumption: []
          });
          this._isMounted && this.notify("tr", "success", "Style Created!");
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Network Error");
        }.bind(this)
      );
    this.componentDidMount();

  }

  componentDidMount() {
    this._isMounted = true;
    this.costingHistory();
    this.getStyles();
    //this.getColors();
    this.getDestinations();
    this.getProductionMaterials();
    this.getSeason();
  }

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  _loadedStyles = false;
  getStyles = () => {
    this.toggleLoading();
    axios
      .get(
        //this.costingURL + '/bom_header/',
        this.costingURL + "/style/",

        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function (response) {
          this._loadedStyles = true;
          //console.log(response.data)
          this.setState({
            StylesList: response.data.map((header) => {
              return {
                ...header,
                actions: (
                  <>
                    <Button
                      color="info"
                      size="sm"
                      className="btn-icon btn-link"
                      disabled={false}
                      //aqui el header me regresa toda la info del BOM clickeado
                      //onClick={()=>console.log(header, header.costing_id.split(' ')[0])}
                      onClick={(e) => {
                        //console.log(header.costing_id, header.id, header.name)
                        this.editStyle(
                          header.costing_id,
                          header.id,
                          header.name
                        );
                      }}
                    //onClick={this.OpenSheet(header.costing_id.split(' ')[0])}
                    >
                      <i className="fa fa-edit" />
                    </Button>
                    <Button
                      color="danger"
                      size="sm"
                      className="btn-icon btn-link"
                      disabled={false}
                      onClick={() => this.deleteStyle(header.id)}
                    //onClick={()=>console.s(header)}
                    >
                      <i className="fa fa-times" />
                    </Button>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
          //console.log(this.state.StylesList)
        }.bind(this)
      )
      .catch(function (error) {
        // this.handleError(error, "boms headers");
      });
  };

  _loadingCostSheets = false;
  costingHistory = () => {
    axios
      .get(this.costingURL + "/costingHeader/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingCostSheets = true;
          this._isMounted &&
            this.setState({
              costsheets: response.data.map((costsheet) => {
                return {
                  value: costsheet.id,
                  label: `${costsheet.id} / ${costsheet.client.name} / ${costsheet.season.name} / ${costsheet.style}`,
                  data: costsheet,
                  client: costsheet.client.name
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "costing History");
        }.bind(this)
      );
  };

  handleSelectChangePlaces = (event) => {
    let opts = [],
      opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
      }
    }
    this.setState({ selectedPlaces: opts });
  };

  handleSelectChange = (event) => {
    let opts = [],
      opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
      }
    }
    this.setState({ selectedColors: opts });
  };

  deleteStyle(id) {
    //console.log(id);
    this.toggleLoading();
    axios
      .delete(this.costingURL + "/style/" + id + "/", {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function () {
          this.notify("tr", "success", "Style deleted!");
          this.toggleLoading();
          this.componentDidMount();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "delete  style");
        }.bind(this)
      );
  }


  updateStyle() {
    //console.log(this.state.editForm.editName, this.state.editForm.editCosting.value);
    this.toggleLoading();
    this.toggleEditModal();
    let data = new FormData();
    //console.log(this.state.editForm)
    data.append("style", this.state.editedStyle); //J
    data.append("name", this.state.editForm.editName);
    data.append("colors", this.state.editForm.editName);
    data.append("destinations", this.state.editForm.editName);
    axios
      .post(process.env.REACT_APP_IP + "/style/update_style/", data, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function () {
          this.toggleLoading();
          this._isMounted && this.notify("tr", "success", "Style Updated!");
          this.componentDidMount();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Network Error");
        }.bind(this)
      );
  }

  render() {
    const {
      loading,
      costsheets,
      StylesList,
      materialEditForm,
      materialForm,
      modalMaterials,
      selectedCostSheetOptions,
    } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';

    return (
      <div className="content">

        <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
              </div>

                <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
                  <div className="ui segment">
                    <div className={`ui active transition ${inverted} visible dimmer`}>
                      <div className="content"><div className="ui text loader">Loading</div></div>
                    </div>
                    <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
                  </div>
                </div>
                <Modal isOpen={this.state.editModal}
                toggle={this.toggleEditModal}
                modalClassName={this.checkPageMode().mc}
              ><ModalHeader className="justify-content-center" toggle={this.toggleImportModal}>
              Edit Style
              </ModalHeader>
            <ModalBody>
            
                    <Row>
                    <Col sm={12}>
                      
                        <Label>Style Name</Label>
                        <Input
                          type='text'
                          
                          placeholder='Style Name'
                          name='name'
                          defaultValue={this.state.editForm.editName}
                          
                          onChange={e => {
                            this.setState({
                              editForm: {
                                editName: e.target.value
                              }
                            })
                          }}
                        />
                        </Col>
                  </Row>
                </ModalBody>
                <ModalFooter>
                  <Button color="secondary" onClick={this.toggleEditModal}>
                    Close
                    </Button>

                  <Button color="primary" onClick={() =>
                    //console.log(this.state.editForm.editName)
                    this.updateStyle()
                  }>
                    Save Changes
                    </Button>
                </ModalFooter>


              </Modal>
        <Modal
          isOpen={this.state.modalMaterials.isOpen}
          toggle={this.toggleAddMaterial}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <FormGroup row>
              <Label sm={3}>Material</Label>
              <Col sm={9}>
                {this.state.Allmaterials && (
                  <Select
                    options={this.state.Allmaterials}
                    value={this.state.materialForm.material}
                    onChange={(o) => {
                      console.log(o);
                      this.setState({
                        materialForm: {
                          ...materialForm,
                          material: o,
                        },
                      });
                    }}
                  />
                )}
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label sm={3}>Consumption</Label>
              <Col sm={9}>
                <Input
                  type="number"
                  value={materialForm.consumption}
                  onChange={(e) => {
                    this.setState({
                      materialForm: {
                        ...materialForm,
                        consumption: e.target.value,
                        actions: materialForm.material.actions,
                        material_name:
                          materialForm.material.name ||
                          materialForm.material.description,
                        old_costing_id:
                          materialForm.old_costing_id ||
                          materialForm.material.old_costing_id,
                      },
                    });
                  }}
                />
              </Col>
            </FormGroup>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleAddMaterial}>
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                if (modalMaterials.edit) {
                  this.editMaterialEmbellishment();
                } else {
                  this.addMaterialToEmbellishment();
                }
              }}
            >
              ADD
            </Button>
          </ModalFooter>
        </Modal>
        
        <Modal
          isOpen={this.state.modalEditMat}
          toggle={this.toggleEditMaterial}
          modalClassName={this.checkPageMode().mc}
        >
          <ModalBody>
            <FormGroup row>
              <Label sm={3}>Material</Label>
              <Col sm={9}>
                {this.state.materialEditForm &&
                  `${this.state.materialEditForm.material}`}
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label sm={3}>Consumption</Label>
              <Col sm={9}>
                <Input
                  type="number"
                  //value={materialEditForm.consumption}
                  placeholder={materialEditForm.consumption}
                  onChange={(e) => {
                    this.setState({
                      materialEditForm: {
                        ...materialEditForm,
                        consumption: e.target.value,
                        index: materialEditForm.index,
                      },
                    });
                  }}
                />
              </Col>
            </FormGroup>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleAddMaterial}>
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                this.editMaterialEmbellishment();
              }}
            >
              ADD
            </Button>
          </ModalFooter>
        </Modal>
                <Card>
                  <CardBody>
                    <Row>
                      <Col sm={5}>
                        <h3>
                          Style Creation
                        </h3>
                        <Label>Style Name</Label>
                        <Input
                          type='text'
                          maxLength={20}
                          placeholder='Style Name'
                          name='name'
                          //value={this.state.editForm.editName}
                          onChange={e => {
                            this.setState({
                              styleName: e.target.value
                            })
                          }}
                        />

                        <Label>CostSheet</Label>
                        {this._loadingCostSheets ? <Select
                          placeholder="Search CostSheet"
                          styles={customStyles}
                          options={costsheets}
                          onChange={option => {
                            this.getSelectedCostSheet(option.value)
                            this.getColors(option.value);
                            this.setState({
                              selectedCostSheetOptions: option
                            })
                            console.log(option,'option')
                          }}
                          value={selectedCostSheetOptions}
                        /> : <div className={"BOMSloader"}></div>}

                        {this.state.selectedCostSheetOptions.data && this.state.selectedCostSheetOptions.data.client.name === 'THE NORTH FACE'?
                        <>

                          <Label>Select Colors for Style</Label>
                          <Input
                            id="colors"
                            type="select"
                            name="selectMulti"
                            multiple
                            onChange={this.handleSelectChange}
                            value={this.state.selectedColors}
                            style={{ height: "100px" }}
                          >
                            {this.state.colors &&
                              this.state.colors.map((client, idx) => (
                                <option key={idx} value={client.value}>
                                  {client.name}
                                </option>
                              ))}
                          </Input>

                          <Label>Select Destinations</Label>
                          <Input
                            id="destinations"
                            type="select"
                            name="selectMulti"
                            multiple
                            onChange={this.handleSelectChangePlaces}
                            value={this.state.selectedPlaces}
                            style={{ height: "100px" }}
                          >
                            {this.state.places &&
                              this.state.places.map((client, idx) => (
                                <option key={idx} value={client.value}>
                                  {client.name}
                                </option>
                              ))}
                          </Input>
                    {    /*   <Button
                            type="submit"
                            color="info"
                            size="sm"
                            onClick={() => {
                              this.toggleAddSeason();
                            }}
                          >
                            DUPLICATE SEASON
                          </Button>
                          */}
                        </>
                        :
                        <></>
                      }


                  
                          <Button
                            type="submit"
                            color="primary"
                            disabled={
                              selectedCostSheetOptions.value && this.state.styleName
                                ? false
                                : true
                            }
                            onClick={() => {
                              this.CreateStyle(
                                selectedCostSheetOptions.value,
                                this.state.styleName,
                                0
                              );
                              
                            }}
                          >
                            Create Style
                          </Button>
                      </Col>
                      
                      {this.state.selectedCostSheetOptions.data && this.state.selectedCostSheetOptions.data.client.name === 'THE NORTH FACE' ?//condicion para mostrar solo tabla materiales y colores cuando sea TNF
                                 
                        <Col sm={7}>
                          <h3>
                            Materials &nbsp;
                            <Button
                              type="submit"
                              color="info"
                              size="sm"
                              onClick={() => {
                                
                                this.toggleAddMaterial();
                                
                                
                              }}
                            >
                              +
                            </Button>
                          </h3>
                          {
                            <ReactTable
                            responsive
                            data={this.state.materials}
                            filterable
                            columns={[
                              {
                                Header: "Material",
                                accessor: "material_name",
                                sortable: false,
                                filterable: false,
                              },
                              {
                                Header: "Consumo",
                                accessor: "consumption",
                                sortable: false,
                                filterable: false,
                                width: 100,
                              },
                              {
                                Header: "Code",
                                accessor: "old_costing_id",
                                sortable: false,
                                filterable: false,
                                width: 200,
                              },
                              {
                                Header: "ACTION",
                                accessor: "actions",
                                sortable: false,
                                filterable: false,
                                width: 100,
                              },
                            ]}
                            defaultPageSize={5}
                          />
                          }
                          <br />

                      </Col>
                      :
                      <Col sm={7}>
                        
                          <h3>Styles</h3>
                          
                            <ReactTable
                              responsive
                              data={StylesList}
                              filterable
                              defaultFilterMethod={(filter, row,) => {
                                const id = filter.pivotId || filter.id
                                const filterValue = filter.id === "codigo" ? parseFloat(filter.value) : filter.value.toLowerCase();
                                const filterRow = id === "codigo" ? row[id] : row[id].toLowerCase();
                                return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
                              }}
                              columns={[
                                {
                                  Header: "Style",
                                  accessor: "name",
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, { keys: ["name"] }),
                                  filterAll: true,
                                  sortable: true,
                                  filterable: true,
                                  width: 250
                                },
                              
                                {
                                  Header: "Costing",
                                  accessor: "costing",
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, { keys: ["costing"] }),
                                  filterAll: true,
                                  //sortable: true,
                                  //filterable: true,
                                },
                                {
                                  Header: "ACTIONS",
                                  accessor: "actions",
                                  sortable: false,
                                  filterable: false,
                                  width: 150
                                }
                              ]}
                              defaultPageSize={5}
                              showPaginationTop
                              showPaginationBottom={false}
                              defaultSorted={[
                                {
                                  id: "codigo",
                                  desc: true
                                }
                              ]}
                              className="-striped -highlight primary-pagination"
                            />      
                      
                        </Col>
                      }
                    </Row>
                  </CardBody>
                </Card>
              </div>
    );
  };
}