import React from "react";
import {
  Card,
  CardBody,
  CardTitle,
  CardImg,
  Row,
  Col,
  FormGroup,
  Label,
  Input,
  ModalHeader,
  Button,
  Modal,
  ModalBody,
  ModalFooter,
} from "reactstrap";
import axios from "axios";
import NotificationAlert from "react-notification-alert";
import Select from "react-select";
//import { Redirect,Switch, useParams } from "react-router-dom";
import { uploadFileToS3 } from "../helpers/awsS3.js";
import "../components/BOMS/styles.css";
import { ProcessColorCard } from "../components/BOMS/BOMS_PROCESS.jsx";
import { CostSheetData } from "../components/BOMS/BOMS_COLORS.jsx";

const customStyles = {
  input: (provided) => ({
    ...provided,
    color: "#e14eca",
  }),
};

export default class BOMS extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  _isMounted = false;
  costingURL = process.env.REACT_APP_IP;
  styleId = 0;
  defaultEmbellishmentLine = {
    id: 0,
    color: 0,
    name: "",
    embroidery_description: "",
    laser_description: "",
    washed_description: "",
    ironing_description: "",
    serigraphy_description: "",
    sublimate_description: "",
    materials: [],
  };

  constructor() {
    super();
    this.state = {
      loading: false,
      costsheets: [],
      styleList: [],
      formdata: {},
      techpack: undefined,
      selectedCostSheet: {},
      selectedCostSheetOptions: {},
      selectedStyle: {},
      colors: [],
      processModal: false,
      costingSketch: null,
      processes: [],
      path_to_techpack: undefined,
      header: {
        client: "",
        client_id: 0,
        costing_id: 0,
        description: "",
        cutting_cost: 0,
        sewing_cost: 0,
        thread: 0,
        packing: 0,
        serigraphy: 0,
        sms_flag: false,
        sms_multiplier: 0,
        bom_style: "",
        return_flag: false,
        embellishment_description: [],
      },
      modalMaterials: {
        isOpen: false,
        index: null,
        edit: false,
        matIndex: null,
      },
      materials: [],
      materialForm: {
        material: null,
        consumption: "",
        bom_material_id: 0,
      },
    };
  }

  handleFileChangeTechPack = async (techpack, id) => {
    //this.setState({ loader: true });
    const detailId = id;
    const file = techpack;
    //console.log('uploading file', detailId, file)

    let fileData = new FormData();
    fileData.append("file", file);
    fileData.append("id", detailId);
    return await uploadFileToS3(
      process.env.REACT_APP_IP + "/bom_header/upload_production_techpacks/",
      {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      },
      fileData,
      "file",
      ["pdf", "img", "png", "jpg"]
    )
      .then(
        function () {
          //this.setState({ loader: false });
          this.notify("tr", "success", "Techpack uploaded");
          //this.getCostingHeaders();
        }.bind(this)
      )
      .catch(
        function (error) {
          //this.setState({ loader: false });
          //console.log(error);
          this.handleError(error);
          this.notify("tr", "error", "Techpack uploaded fail");
        }.bind(this)
      );
  };

  getBomHeader(costingName, bomHeader) {
    //console.log(costingName, 'BOM HEADER', bomHeader)
    axios
      .get(
        //this.costingURL + '/bom_header/'+ bomHeader + '/',
        this.costingURL + `/costingHeader/new_BOM/?costing_id=${bomHeader}`,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function (response) {
          //console.log('embellishment ',response.data.embellishment_description)
          //this._selectedCostsheet = true;
          const { header } = this.state;
          const { data } = response;
          this._isMounted &&
            this.setState({
              selectedCostSheet: response.data,
              header: {
                ...header,
                //this values are the same in getSelectedCostSheet
                return_flag: data.return_flag,
                client: data.client,
                thread: data.thread,
                costing_id: data.costing_id,
                costing_style: data.style,
                cutting_cost: data.cutting_cost.toFixed(4),
                sewing_cost: data.sewing_cost.toFixed(4),
                //here we fill the fields that we dont get on  getSelectedCostSheet
                bom_style: data.bom_style,
                sms_flag: data.sms_flag,
                description: data.description,
                /**/
              },
            });
          //console.log('header', header, materials, defaultEmbellishmentLine)
          //this._loadingCostSheets=true;
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Not Found BOM");
        }.bind(this)
      );
  }

  getBomProcess(costingName, bomHeader) {
    //console.log(costingName, bomHeader);
    axios
      .get(this.costingURL + "/bom_header/" + bomHeader + "/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(function (response) {
        //console.log('respuesta del get bom procesos', response)
        this._isMounted &&
          this.setState({
            //console.log(response)
            //selectedCostSheet
          });
      });
  }

  componentDidMount() {
    this._isMounted = true;
    this.costingHistory();
    this.getStyles();
    //this.getProcessClient();
    //console.log(this.props.location.state.costingName)
    if (this.props.location.state) {
      let option = {
        value: this.props.location.state.costingId,
        label: this.props.location.state.costingName,
        header: this.props.location.state.bomHeader,
      };
      this.getSelectedCostSheet(this.props.location.state.costingId);
      this.setState({
        selectedCostSheetOptions: option,
        selectedStyle: option.label,
        editProcess: this.props.location.state.editProcess,
      });
      //oTRA FUNC que llene la data del bom
      //this.getBomHeader(this.props.location.state.costingName, this.props.location.state.bomHeader)
      //this.getBomProcess(this.props.location.state.costingName, this.props.location.state.bomHeader)
      this.showHeaderInfo(this.props.location.state.bomHeader);
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (
      notificationContainer[0].innerHTML === "<div></div>" &&
      this._isMounted
    ) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };

  _sketchLoaded = false;
  downloadSketch = (file) => {
    const url = this.costingURL + "/costingHeader/download/?key=" + file;
    const headers = {
      params: { key: file },
      headers: { Authorization: "Token " + this.userToken },
    };
    axios.get(url, headers).then(
      function (response) {
        this._sketchLoaded = true;
        this.setState({
          costingSketch: response.data.url,
        });
      }.bind(this)
    );
  };

  _loadingCostSheets = false;
  costingHistory = () => {
    axios
      .get(this.costingURL + "/costingHeader/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingCostSheets = true;
          this._isMounted &&
            this.setState({
              costsheets: response.data.map((costsheet) => {
                return {
                  value: costsheet.id,
                  label: `${costsheet.id} / ${costsheet.client.name} / ${costsheet.season.name} / ${costsheet.style}`,
                  data: costsheet,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "costing History");
        }.bind(this)
      );
  };

  _loadingStyles = false;
  getStyles = () => {
    axios
      .get(this.costingURL + "/style/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingStyles = true;
          this._isMounted &&
            this.setState({
              styleList: response.data.map((costsheet) => {
                return {
                  value: costsheet.id,
                  label: `${costsheet.name} / ${costsheet.costing}`,
                  costing_id: costsheet.costing_id,
                  name: costsheet.name,
                };
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "costing History");
        }.bind(this)
      );
  };

  _selectedCostsheet = false;
  getSelectedCostSheet = (id) => {
    this.toggleLoading();
    axios
      .get(this.costingURL + `/costingHeader/new_BOM/?costing_id=${id}`, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._selectedCostsheet = true;
          const { header } = this.state;
          const { data } = response;
          //algo como esto es lo que debo hacer para llenar la data del BOM
          //console.log('selected cost sheet function', response)
          this._isMounted &&
            this.setState({
              selectedCostSheet: response.data,
              header: {
                ...header,
                client: data.client,
                costing_id: data.costing_id,
                costing_style: data.style,
                cutting_cost: data.cutting_cost.toFixed(4),
                sewing_cost: data.sewing_cost.toFixed(4),
                //bom_style: data.bom_style,
              },
            });
          this.getColors(data.client);
          this.toggleLoading();
          this.getProductionMaterials(data.client);
          this.downloadSketch(response.data.picture);
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "selected");
        }.bind(this)
      );
  };

  _selectedHeader = false;
  showHeaderInfo = (headerId) => {
    //console.log('HEADER ID ', headerId)
    this.toggleLoading();
    axios
      .get(`${this.costingURL}/bom_header/${headerId}/`, 
      {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._selectedHeader = true;
          const { header } = this.state;
          const { data } = response;
          //console.log('data pa buscar id', response)
          //Aaqui vamos a intentar llenar todo,, dije intentar
          this._isMounted &&
            this.setState({
              techpack: data.production_techpack,
              //selectedCostSheet: response.data,
              header: {
                ...header,
                client_id: data.client,
                return_flag: data.return_flag,
                bom_style: data.bom_style.name,
                description: data.description,
                sms_flag: data.sms_flag,
                sms_multiplier: data.sms_multiplier,
                cutting_cost: data.cutting_cost.toFixed(4),
                sewing_cost: data.sewing_cost.toFixed(4),
                thread: data.thread.toFixed(4),
                packing: data.packing.toFixed(4),
                serigraphy: data.serigraphy.toFixed(4),
                embellishment_description: data.embellishment_description.map(
                  (embellishment_description) => {
                    return {
                      id: embellishment_description.id,
                      color: embellishment_description.color_id,
                      value: `${embellishment_description.color}`,
                      embroidery_description:
                        embellishment_description.embroidery_description,
                      ironing_description:
                        embellishment_description.ironing_description,
                      laser_description:
                        embellishment_description.laser_description,
                      serigraphy_description:
                        embellishment_description.serigraphy_description,
                      sublimate_description:
                        embellishment_description.sublimate_description,
                      washed_description:
                        embellishment_description.washed_description,
                      uid: embellishment_description.id,
                      //editable: true,
                      returned_flag: embellishment_description.returned_flag,
                      materials: embellishment_description.materials.map(
                        (material) => {
                          //console.log('materiales que llenan el header', material)
                          return {
                            bom_material_id: material.id,
                            material_id: material.material.id,
                            label: `${material.material.description} / ${material.material.size.name} / ${material.old_costing_id} / ${material.material.client.abbreviation}`,
                            consumption: material.material_consumption,
                            measure: material.material.measure.name,
                            old_costing_id: material.material.old_costing_id,
                          };
                        }
                      ),
                    };
                  }
                ),
              },
            });

          this.getColors(header.client);
          //this.toggleLoading();
          this.getProductionMaterials(header.client);
          //this.downloadSketch(response.data.picture)
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "selected");
        }.bind(this)
      );
  };

  _colorsLoaded = false;
  getColors = (client) => {
    if (!client) {
      //console.log('waiting')
      client = 1;
      //this.toggleLoading();
    }
    this.toggleLoading();
    axios
      .get(
        process.env.REACT_APP_IP +
        `/color_by_client/get_colors_by_client/?client=${client}`,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function (response) {
          this._colorsLoaded = true;
          this._isMounted &&
            this.setState({
              colors: response.data.map((color) => {
                return {
                  value: color.id,
                  label: `${color.name}`,
                  //label: `${color.name} / ${color.client.name}`,
                  data: color,
                };
              }),
            });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "selected");
        }.bind(this)
      );
  };

  addColorProcessCopy = () => {
    let {
      //colors,
      header: { embellishment_description },
    } = this.state;
    /*const uid = Date.now();
    const colorObject = {
      ...this.defaultEmbellishmentLine,
      uid,
    }
    embellishment_description = [
      ...embellishment_description,
      colorObject
    ]*/
    /*const cIndex = colors.findIndex(item => item.id===color.id);
    const currentColor = colors[cIndex];
    colors[cIndex] = {
      ...currentColor,
      blocked: true,
    }*/
    this.setState({
      //colors: colors,
      header: {
        ...this.state.header,
        embellishment_description: embellishment_description,
      },
    });
    console.log(this.state)
  };

  _loadingComponentStyles = false;
  getProcessClient = () => {
    axios
    .get(this.costingURL + `/bom_header/get_bom_colors/?bom_id=${this.props.location.state.bomHeader}`, {
      headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._loadingComponentStyles = true;
          //console.log(response.data);
          this._isMounted &&
            this.setState({
              StylesWithProcess: response.data.map((process) => {
          
                    return {
                      value: process.id,
                      label: `${this.props.location.state.costingName} / ${process.color__name}`,
                      //costing_id: style.costing_id,
                      name: process.style_name,
                    };
               
              }),
            });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Loading Components");
        }.bind(this)
      );
  };

  setAllProces = () => {
    let all_components = []
    /*eslint-disable-next-line*/
    this.state.StylesWithProcess.map((process, idx) => {
      process.colors_Style.map((list_p, idx2) => {
        console.log(list_p)
        all_components.push(list_p)
        return {
          label: idx2
        }
      })
    })
    this.setState({ all_components: all_components })
  }

  _loadingSelectedComponentStyles = false;
  getOneProcess = (process_id) => {
    let process = new FormData();
    process.append("process_id", process_id);
    axios
      .post(this.costingURL + "/bom_header/get_one_process/", process, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function (response) {
          this._loadingSelectedComponentStyles = true;
          this._isMounted &&
            this.setState({ newProcessSelected: response.data });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "Error Loading Components");
        }.bind(this)
      );
  };

  poolProcessModal = () => {
    this.setState({
      processModal: !this.state.processModal,
    });
  };

  addOldColorProcess = () => {
    let color = this.state.newProcessSelected;
    const today = new Date();
    const data = {
      //...color,
      id: 0,
      uid: today.getDate() + today.getHours() + today.getSeconds(),
      value: color.color,
      color: color.id,
      editable: false,
      embroidery_description: color.embroidery_description,
      ironing_description: color.ironing_description,
      laser_description: color.laser_description,
      serigraphy_description: color.serigraphy_description,
      sublimate_description: color.sublimate_description,
      washed_description: color.washed_description,
      materials: color.materials.map((material) => {
        return {
          bom_material_id: 0,
          material_id: material.material.id,
          label: material.material.description,
          consumption: material.material_consumption,
          old_costing_id: material.material.old_costing_id,
          measure: material.material.measure.name,
        };
      }),
    };
    /*eslint-disable no-unused-vars*/
    let {
      //colors,
      header: { embellishment_description },
    } = this.state;
    const uid = `${today.getDate() + today.getHours() + today.getSeconds()}`;
    const editable = false;
    const colorObject = {
      ...this.defaultEmbellishmentLine,
      uid,
      editable,
    };
    embellishment_description = [...embellishment_description, colorObject];
    //console.log(data);
    this.setState({
      //colors: colors,
      header: {
        ...this.state.header,
        embellishment_description: this.state.header.embellishment_description.push(
          data
        ),
      },
    });
    //console.log('HEADER', this.state.header.embellishment_description.push(data));
    //console.log('HEADER', this.state.header);
    this.addColorProcessCopy();
  };

  addColorProcess = () => {
    //console.log('adding color',this.state )
    let {
      //colors,
      header: { embellishment_description },
    } = this.state;
    const uid = Date.now();
    const editable = false;
    const colorObject = {
      ...this.defaultEmbellishmentLine,
      uid,
      editable,
    };
    embellishment_description = [...embellishment_description, colorObject];
    /*const cIndex = colors.findIndex(item => item.id===color.id);
    const currentColor = colors[cIndex];
    colors[cIndex] = {
      ...currentColor,
      blocked: true,
    }*/
    this.setState({
      //colors: colors,
      header: {
        ...this.state.header,
        embellishment_description: embellishment_description,
      },
    });
    //console.log(this.state)
  };

  duplicateColorProcess = (color) => {
    //console.log('color objeto', color);
    const today = new Date();
    const data = {
      //...color,
      id: 0,
      uid: today.getDate() + today.getHours() + today.getSeconds(),
      value: undefined,
      color: undefined,
      editable: false,
      embroidery_description: color.embroidery_description,
      ironing_description: color.ironing_description,
      laser_description: color.laser_description,
      serigraphy_description: color.serigraphy_description,
      sublimate_description: color.sublimate_description,
      washed_description: color.washed_description,
      materials: color.materials.map((material) => {
        //console.log('materiales que llenan el header', material)
        return {
          bom_material_id: 0,
          material_id: material.material_id,
          label: material.label,
          consumption: material.consumption,
          old_costing_id: material.old_costing_id,
          measure: material.measure,
        };
      }),
    };
    let {
      //colors,
      header: { embellishment_description },
    } = this.state;
    const uid = `${today.getDate() + today.getHours() + today.getSeconds()}`;
    const editable = false;
    const colorObject = {
      ...this.defaultEmbellishmentLine,
      uid,
      editable,
    };
    embellishment_description = [...embellishment_description, colorObject];
    //console.log(data);
    this.setState({
      //colors: colors,
      header: {
        ...this.state.header,
        embellishment_description: this.state.header.embellishment_description.push(
          data
        ),
      },
    });
    //console.log('HEADER', this.state.header.embellishment_description.push(data));
    //console.log('HEADER', this.state.header);
    this.addColorProcessCopy();
  };

  removeColorProcess = (uid) => {
    let {
      //colors,
      header: { embellishment_description },
    } = this.state;
    //console.log('embellisment for delete', embellishment_description, 'iud', uid)
    /*const cIndex = colors.findIndex(item => item.id===color.color);
    const currentColor = colors[cIndex];
    colors[cIndex] = {
      ...currentColor,
      blocked: false,
    }*/
    const newEmbLines = embellishment_description.filter(
      (desc) => desc.uid !== uid
    );
    this.setState({
      header: {
        ...this.state.header,
        embellishment_description: newEmbLines,
      },
    });
  };

  handleProcessColorChange = (index, key, value) => {
    //console.log(index,key,value);
    let {
      header: { embellishment_description },
    } = this.state;
    embellishment_description[index][key] = value;
    this.setState({
      header: {
        ...this.state.header,
        embellishment_description: embellishment_description,
      },
    });
  };

  toggleAddMaterial = (index = null, edit, matIndex = null) => {
    const { modalMaterials } = this.state;
    this.setState({
      modalMaterials: {
        ...modalMaterials,
        isOpen: !modalMaterials.isOpen,
        index: index,
        edit: edit,
        matIndex: matIndex,
      },
    });
    if (index && edit === false && matIndex === null) {
      this.setState({
        materialForm: {
          material: null,
          consumption: null,
        },
      });
    }
  };

  _loadedMaterials = false;
  getProductionMaterials = (clientId) => {
    this.toggleLoading();
    axios
      .get(
        process.env.REACT_APP_IP +
        `/materialp/get_materialp_by_client/?client=${clientId}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function (response) {
          this._loadedMaterials = true;
          //console.log(response.data)
          this.setState({
            materials: response.data.map((material) => {
              //console.log(material)
              return {
                bom_material_id: 0,
                old_costing_id: material.old_costing_id,
                value: material.material_id,
                label: `${material.description} / ${material.old_costing_id} / ${material.client_abbreviation}`,
                //old_costing_id: material.old_costing_id,
                measure: material.measure_name,
              };
            }),
          });
        }.bind(this)
      );
  };

  addMaterialToEmbellishment = () => {
    //console.log('addMaterialToEmbellishment');
    let {
      header,
      header: { embellishment_description },
      materialForm,
      modalMaterials,
    } = this.state;
    if (
      !materialForm.consumption ||
      materialForm.consumption < 0 ||
      materialForm.material === null
    ) {
      this.notify("tr", "warning", "Material inputs required");
      return false;
    }
    const embLine = embellishment_description[modalMaterials.index].materials;
    //console.log('embline', materialForm)
    if (
      embLine.find(
        (material) => material.material_id === materialForm.material.value
      )
    ) {
      this.notify("tr", "warning", "Material already exists in this process");
      return false;
    } else {
      embellishment_description[modalMaterials.index].materials = [
        ...embLine,
        {
          material_id: materialForm.material.value,
          consumption: materialForm.consumption,
          bom_material_id: 0,
          old_costing_id: materialForm.material.old_costing_id,
          measure: materialForm.material.measure,
        },
      ];
      this.setState({
        header: {
          ...header,
          embellishment_description: embellishment_description,
        },
        materialForm: {
          material: null,
          consumption: null,
          bom_material_id: 0,
          old_costing_id: null,
          measure: null,
        },
      });
      this.toggleAddMaterial();
    }
  };

  removeMaterialEmbellishment = (index, material_id) => {
    let {
      header,
      header: { embellishment_description },
    } = this.state;
    const embLine = embellishment_description[index].materials;
    embellishment_description[index].materials = embLine.filter(
      (material) => material.material_id !== material_id
    );
    this.setState({
      header: {
        ...header,
        embellishment_description: embellishment_description,
      },
    });
  };

  editMaterialToggle = (index, form, matIndex) => {
    //console.log('edit material Toggle', form.material.label, index)
    this.setState({
      materialForm: {
        material: form.material,
        consumption: form.consumption,
        bom_material_id: 0,
        old_costing_id: form.material.old_costing_id,
        measure: form.material.measure,
      },
    });
    this.toggleAddMaterial(index, true, matIndex);
  };

  editMaterialEmbellishment = () => {
    //console.log('editMaterialEmbellishment');
    let {
      header,
      header: { embellishment_description },
      materialForm,
      modalMaterials: { index, matIndex },
    } = this.state;
    embellishment_description[index].materials[matIndex] = {
      material_id: materialForm.material.value,
      consumption: materialForm.consumption,
      bom_material_id: 0,
      old_costing_id: materialForm.old_costing_id,
      measure: materialForm.measure,
    };
    this.setState({
      header: {
        ...header,
        embellishment_description: embellishment_description,
      },
      materialForm: {
        material: null,
        consumption: null,
        bom_material_id: 0,
        old_costing_id: null,
        measure: null,
      },
    });
    this.toggleAddMaterial();
  };

  formHelper = (data) => {
    //console.log('data upload helper', data)
    if ("techpack" in data) {
      let techpack = data.techpack;
      this.setState({
        techpack: techpack,
      });
    }
    if ("formdata" in data) {
      this.setState({
        formdata: {
          ...this.state.formdata,
          [data.formdata.key]: data.formdata.value,
        },
      });
    }
    this.setState({
      path_to_techpack: data,
    });
  };

  updateHeader = (bomHeader, event) => {
    //console.log('UPDATE HEADER ID', bomHeader)
    const {
      header,
      header: { embellishment_description },
    } = this.state;
    //console.log(embellishment_description);
    /*if (header.bom_style === "") {
      this.notify("tr", "warning", "BOM style required");
      return;
    }*/
    if (embellishment_description.length === 0) {
      this.notify("tr", "warning", "BOM processes required");
      return;
    } else {
      let validLines = true;
      embellishment_description.map((line) => {
        if (line.color === 0) {
          validLines = false;
        }
        if (line.materials.length === 0) {
          validLines = false;
          line.materials.length.map((mat) => {
            if (mat.hasOwnProperty("bom_material_id")) {
              //console.log(mat);
              //mat.bom_material_id = 0
            }
          });
        }
        return line;
      });
      if (!validLines) {
        this.notify(
          "tr",
          "warning",
          "Invalid Process data, missing color and/or materials"
        );
        return;
      }
    }
    //this.handleFileChangeTechPack();
    //this.toggleLoading();
    //console.log(this.state.selectedStyle.value)
    const finalHeader = {
      ...header,
      client: undefined,
      style: this.state.selectedStyle.value,
      id: bomHeader,
      sms_flag:
        header.sms_flag === true
          ? (header.sms_flag = 1)
          : (header.sms_flag = 0),
      sms_multiplier:
        header.sms_multiplier || header.sms_multiplier === ""
          ? header.sms_multiplier
          : 0.0,
    };
    //console.log('UPDATE HEADER', finalHeader)

    if (this.state.techpack === undefined) {
      this.notify("tr", "warning", "Techpack missing.");
    } 
    else{    
      if (finalHeader.description.length === 0) {
        this.notify("tr", "warning", "Description missing.");
        return
        
      } else {
        axios
          .post(process.env.REACT_APP_IP + `/bom_header/update_bom_header/`, finalHeader, {
            headers: {
              Authorization: "Token " + this.userToken,
            },
          })
          .then(
            function (response) {
              if (this.state.techpack === undefined) {
                this.notify("tr", "warning", "SHOUDNT BE.");
              } else {
                this.handleFileChangeTechPack(
                  this.state.techpack,
                  response.data.id
                );
                this.toggleLoading();
                this.notify("tr", "success", "BOM saved!");
              }
            }.bind(this)
          )
          .catch(
            function (error) {
              this.toggleLoading();
              this.handleError(error, "save bom");
              //return;
            }.bind(this)
          );
      }
      this.toggleLoading();
    }
  };

  duplicateHeader = (id) => {
    this.toggleLoading();
    let data = new FormData();
    data.append("bom_id", id);
    axios
      .post(process.env.REACT_APP_IP + "/bom_header/clone_bom/", data, {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function () {
          this.toggleLoading();
          this.notify("tr", "success", "BOM duplicated!");
          const path = "/admin/detail_boms";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.toggleLoading();
          this.handleError(error, "save bom");
        }.bind(this)
      );
  };

  createHeader = () => {
    const {
      header,
      header: { embellishment_description },
    } = this.state;
    /*if (header.bom_style === "") {
      this.notify("tr", "warning", "BOM style required");
      return;
    }*/
    if (embellishment_description.length === 0) {
      this.notify("tr", "warning", "BOM processes required");
      return;
    } else {
      let validLines = true;
      embellishment_description.map((line) => {
        if (line.color === 0) {
          validLines = false;
        }
        if (line.materials.length === 0) {
          validLines = false;
        }
        return line;
      });
      if (!validLines) {
        this.notify(
          "tr",
          "warning",
          "Invalid Process data, missing color and/or materials"
        );
        return;
      }
    }
    //this.toggleLoading();
    const finalHeader = {
      ...header,
      bom_style: undefined,
      style: this.state.selectedStyle.value,
      client: undefined,
      client_id: undefined,
      costing_id: undefined,
      sms_flag:
        header.sms_flag === true
          ? (header.sms_flag = 1)
          : (header.sms_flag = 0),
      sms_multiplier:
        header.sms_multiplier || header.sms_multiplier === ""
          ? header.sms_multiplier
          : 0.0,
    };
    if (this.state.techpack === undefined) {
      this.notify("tr", "warning", "Techpack missing.");
    } else {
      //console.log('nuevo header', finalHeader)
      axios
        .post(process.env.REACT_APP_IP + "/bom_header/", finalHeader, {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        })
        .then(
          function (response) {
            if (this.state.techpack === undefined) {
              this.notify("tr", "warning", "SHOUDNT BE.");
            } else {
              //console.log('ya posteo ahorita va a probar subir')
              this.handleFileChangeTechPack(
                this.state.techpack,
                response.data.id
              );
              //this.notify("tr", "success", "Fabric added!");
              //this.toggleLoading();
              this.notify("tr", "success", "BOM saved!");
              const path = "/admin/detail_boms";
              this.props.history.push({
                pathname: path,
              });
            }
          }.bind(this)
        )
        .catch(
          function (error) {
            //this.toggleLoading();
            this.handleError(error, "save bom");
            //this.notify("tr", "Error", "BOM saved!");
            //return;
          }.bind(this)
        );
    }
  };

  sendForApproval(header) {
    //console.log('enviando a aprove by accoun manager', header);
    this.toggleLoading();
    let data = new FormData();
    data.append("flag", "A"); //Sent for account manager approv
    data.append("bom_id", header);
    axios
      .post(process.env.REACT_APP_IP + "/bom_header/change_status/", data, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function () {
          this._isMounted &&
            this.notify("tr", "success", "CostSheet sent for Approve!");
          this.componentDidMount();
          const path = "/admin/detail_boms";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function (error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  }

  render() {
    /*if(this.state.Reload!==false) {
      return (<Switch>
        <Redirect push to={{pathname: "/admin/detail_boms"}} />
      </Switch>);
    }*/
    //console.log(this.props.location.state.bomHeader, this.props.location.state.costingId )
    const {
      costsheets,
      selectedCostSheetOptions,
      selectedCostSheet,
      colors,
      styleList,
      selectedStyle,
      processModal,
      loading,
      ProcessColor,
      costingSketch,
      header,
      modalMaterials,
      materials,
      materialForm,
      editProcess,
    } = this.state;
    const pageMode = this.checkPageMode();
    //console.log(selectedStyle);
    //let idStyle = useParams();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    const sms_multiplier = header.sms_multiplier;
    //aqui se declaran los valores simples
    const cutting_cost = parseFloat(header.cutting_cost);
    const sewing_cost = parseFloat(header.sewing_cost);
    const thread = parseFloat(header.thread);
    const packing = parseFloat(header.packing);
    const serigraphy = parseFloat(header.serigraphy);

    //Aqui se muestran los valores multiplicados
    const cutting_cost_multiplied = header.sms_flag
      ? parseFloat(header.cutting_cost) *
      parseFloat(sms_multiplier ? sms_multiplier : 0)
      : parseFloat(header.cutting_cost);

    const sewing_cost_multiplied = header.sms_flag
      ? parseFloat(header.sewing_cost) *
      parseFloat(sms_multiplier ? sms_multiplier : 0)
      : parseFloat(header.sewing_cost);

    const thread_multiplied = header.sms_flag
      ? parseFloat(header.thread) *
      parseFloat(sms_multiplier ? sms_multiplier : 0)
      : parseFloat(header.thread);

    const packing_multiplied = header.sms_flag
      ? parseFloat(header.packing) *
      parseFloat(sms_multiplier ? sms_multiplier : 0)
      : parseFloat(header.packing);

    const serigraphy_multiplied = header.sms_flag
      ? parseFloat(header.serigraphy) *
      parseFloat(sms_multiplier ? sms_multiplier : 0)
      : parseFloat(header.serigraphy);

    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>

        <Modal isOpen={modalMaterials.isOpen} toggle={this.toggleAddMaterial}>
          <div className="modal-header">
            <button
              type="button"
              className="close"
              data-dismiss="modal"
              aria-label="Close"
              onClick={this.toggleAddMaterial}
            >
              <i className="tim-icons icon-simple-remove"></i>
            </button>
            <h5 className="modal-title">Add Material</h5>
          </div>
          <ModalBody>
            <FormGroup row>
              <Label sm={3}>Material</Label>
              <Col sm={9}>
                {this._loadedMaterials && (
                  <Select
                    options={materials}
                    value={materialForm.material}
                    onChange={(o) => {
                      this.setState({
                        materialForm: {
                          ...materialForm,
                          material: o,
                        },
                      });
                    }}
                  />
                )}
              </Col>
            </FormGroup>
            <FormGroup row>
              <Label sm={3}>Consumption</Label>
              <Col sm={9}>
                <Input
                  type="number"
                  value={materialForm.consumption}
                  onChange={(e) => {
                    this.setState({
                      materialForm: {
                        ...materialForm,
                        consumption: e.target.value,
                        bom_material_id: 0,
                        old_costing_id:
                          materialForm.old_costing_id ||
                          materialForm.material.old_costing_id,
                        measure:
                          materialForm.measure || materialForm.material.measure,
                      },
                    });
                  }}
                />
              </Col>
            </FormGroup>
          </ModalBody>
          <ModalFooter>
            <Button color="secondary" onClick={this.toggleAddMaterial}>
              Close
            </Button>
            <Button
              color="primary"
              onClick={() => {
                if (modalMaterials.edit) {
                  this.editMaterialEmbellishment();
                } else {
                  this.addMaterialToEmbellishment();
                }
              }}
            >
              ADD
            </Button>
          </ModalFooter>
        </Modal>

        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <h1>BOMs</h1>

        <Row>
          <Col sm={3}>CostSheet</Col>
          <Col sm={9}>
            {/*this._loadingCostSheets ? <Select
            placeholder="Search CostSheet"
            styles={customStyles}
            options={costsheets}
            onChange={option => {
              this.getSelectedCostSheet(option.value)
              this.setState({
                selectedCostSheetOptions: option,
                header: {
                  ...header,
                  embellishment_description: []
                }
              })
            }}
            value={selectedCostSheetOptions}
          /> : <div className={"BOMSloader"}></div>*/}
            {this._loadingStyles ? (
              <Select
                placeholder="Search Style"
                styles={customStyles}
                options={styleList}
                onChange={(option) => {
                  //console.log(this.state)
                  this.getSelectedCostSheet(option.costing_id);
                  this.setState({
                    ...this.state,
                    selectedStyle: option,
                    //selectedCostSheetOptions: option,
                    header: {
                      ...header,
                      //embellishment_description: []
                    },
                  });
                  //console.log(this.state)
                }}
              //value={selectedCostSheetOptions}
              />
            ) : (
              <div className={"BOMSloader"}></div>
            )}
          </Col>
        </Row>

        <br />

        {this._selectedCostsheet && (
          <Row>
            <Col sm={6}>
              <CostSheetData
                selectedCostSheet={selectedCostSheet}
                selectedStyle={selectedStyle}
                //handleFileChangeTechPack = {this.handleFileChangeTechPack}
                helper={(data) => this.formHelper(data)}
                nameFile={this.state.techpack}
                header={header}
                onChange={(field, value) =>
                  this.setState({
                    header: {
                      ...header,
                      [field]: value,
                    },
                  })
                }
              />
            </Col>

            <Col sm={6}>
              {this._sketchLoaded && (
                <Card body outline color="primary">
                  <CardImg
                    top
                    src={costingSketch}
                    alt={selectedCostSheet.picture}
                    style={{
                      maxWidth: "300px",
                      maxHeight: "300px",
                    }}
                  />
                </Card>
              )}

              <Card>
                <CardBody>
                  <CardTitle>Precios</CardTitle>
                  <Row>
                    <Col sm={6}>
                      <FormGroup row>
                        <Label sm={4}>Corte</Label>
                        <Col sm={8}>
                          <Input
                            type="number"
                            min="0"
                            value={cutting_cost}
                            onChange={(e) => {
                              this.setState({
                                header: {
                                  ...header,
                                  cutting_cost: parseFloat(e.target.value),
                                },
                              });
                            }}
                          />
                          <Label sm={12}>
                            x SMS flag: {cutting_cost_multiplied.toFixed(4)}
                          </Label>
                        </Col>
                      </FormGroup>
                    </Col>

                    <Col sm={6}>
                      <FormGroup row>
                        <Label sm={4}>Costura</Label>
                        <Col sm={8}>
                          <Input
                            type="number"
                            min="0"
                            value={sewing_cost}
                            onChange={(e) => {
                              this.setState({
                                header: {
                                  ...header,
                                  sewing_cost: e.target.value,
                                },
                              });
                            }}
                          />
                          <Label sm={12}>
                            x SMS flag: {sewing_cost_multiplied.toFixed(4)}
                          </Label>
                        </Col>
                      </FormGroup>
                    </Col>

                    <Col sm={6}>
                      <FormGroup row>
                        <Label sm={4}>Hilo</Label>
                        <Col sm={8}>
                          <Input
                            type="number"
                            placeholder="0"
                            min="0"
                            onChange={(e) => {
                              this.setState({
                                header: {
                                  ...header,
                                  thread: e.target.value,
                                },
                              });
                            }}
                            value={thread}
                          />
                          <Label sm={12}>
                            {" "}
                            x SMS flag: {thread_multiplied.toFixed(4)}
                          </Label>
                        </Col>
                      </FormGroup>
                    </Col>

                    <Col sm={6}>
                      <FormGroup row>
                        <Label sm={4}>Empaque</Label>
                        <Col sm={8}>
                          <Input
                            type="number"
                            placeholder="0"
                            min="0"
                            onChange={(e) => {
                              this.setState({
                                header: {
                                  ...header,
                                  packing: e.target.value,
                                },
                              });
                            }}
                            value={packing}
                          />
                          <Label sm={12}>
                            x SMS flag: {packing_multiplied.toFixed(4)}
                          </Label>
                        </Col>
                      </FormGroup>
                    </Col>

                    <Col sm={6}>
                      <FormGroup row>
                        <Label sm={4}>Serigrafia</Label>
                        <Col sm={8}>
                          <Input
                            type="number"
                            placeholder="0"
                            min="0"
                            onChange={(e) => {
                              this.setState({
                                header: {
                                  ...header,
                                  serigraphy: e.target.value,
                                },
                              });
                            }}
                            value={serigraphy}
                          />
                          <Label sm={12}>
                            x SMS flag: {serigraphy_multiplied.toFixed(4)}
                          </Label>
                        </Col>
                      </FormGroup>
                    </Col>
                  </Row>
                </CardBody>
              </Card>
            </Col>
          </Row>
        )}

        {this._selectedCostsheet && (
          <Card body outline color="primary">
            <CardTitle>
              <div className="clearfix"></div>
              Procesos/Colores
              <Button
                color="primary"
                size="sm"
                onClick={() => this.addColorProcess()}
                className="float-right"
              >
                Add Color
              </Button>

              <Modal
                isOpen={processModal}
                toggle={this.poolProcessModal}
                modalClassName={this.checkPageMode().mc}
              >
                <ModalHeader
                  className="justify-content-center"
                  toggle={this.toggleImportModal}
                >
                  Select the process
                </ModalHeader>
                <ModalBody>
                  <Row>
                    <Col sm={12}>
                      <Label>Style / Color</Label>
                      <Row>
                        <Col sm={12}>
                          {this.state.StylesWithProcess &&
                            <Select
                              placeholder="Select Process"
                              styles={customStyles}
                              options={
                                this.state.all_components
                                /*this.state.StylesWithProcess.map((process,idx)=> {
                                process.colors_Style.map((list_p,idx2) => {
                                  console.log(list_p)
                                  return {
                                    label:idx2
                                  }
                                })
                                
                                
                              })*/
                                //this.state.StylesWithProcess
                              }
                              onChange={(e) => {
                                this.setState({ selectedPro: e })
                                this.getOneProcess(e.value);

                              }}
                            />}
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </ModalBody>
                <ModalFooter>
                  <Button
                    color="secondary"
                    onClick={() => this.poolProcessModal()}
                  >
                    Close
                  </Button>
                  <Button
                    color="primary"
                    onClick={() => {
                      this.poolProcessModal();
                      this.addOldColorProcess();

                    }}
                  >
                    Save Changes
                  </Button>
                </ModalFooter>
              </Modal>
            </CardTitle>
            <CardBody>
              {header.embellishment_description.map((pcolor, i) => {
                return (
                  <>
                    {!header.return_flag && !pcolor.returned_flag ? ( //caso si edita
                      <ProcessColorCard
                        key={i}
                        color={pcolor}
                        index={i}
                        onChange={this.handleProcessColorChange}
                        onRemove={this.removeColorProcess}
                        onDuplicate={this.duplicateColorProcess}
                        colorsList={colors}
                        addMaterial={this.toggleAddMaterial}
                        materialsList={materials}
                        removeMaterial={this.removeMaterialEmbellishment}
                        editMaterial={this.editMaterialToggle}
                        materialDisabled={editProcess}
                      />
                    ) : (
                      <>
                        {header.return_flag && pcolor.returned_flag ? ( //caso si retornaron colores
                         <ProcessColorCard
                            key={i}
                            color={pcolor}
                            index={i}
                            onChange={this.handleProcessColorChange}
                            onRemove={this.removeColorProcess}
                            onDuplicate={this.duplicateColorProcess}
                            colorsList={colors}
                            addMaterial={this.toggleAddMaterial}
                            materialsList={materials}
                            removeMaterial={this.removeMaterialEmbellishment}
                            editMaterial={this.editMaterialToggle}
                            materialDisabled={false}
                          />
                        ) : (
                          <>
                            {pcolor.editable === false ? (
                              <>
                              <ProcessColorCard
                                key={i}
                                color={pcolor}
                                index={i}
                                onChange={this.handleProcessColorChange}
                                onRemove={this.removeColorProcess}
                                onDuplicate={this.duplicateColorProcess}
                                colorsList={colors}
                                addMaterial={this.toggleAddMaterial}
                                materialsList={materials}
                                removeMaterial={
                                  this.removeMaterialEmbellishment
                                }
                                editMaterial={this.editMaterialToggle}
                                materialDisabled={false}
                              />
                              </>
                            ) : (
                              <> </>
                            )}
                          </>
                        )}
                      </>
                    )}
                  </>
                );
              })}
            </CardBody>
          </Card>
        )}

        {this._selectedCostsheet && this._selectedHeader === false ? (
          <Button color="primary" onClick={() => this.createHeader()}>
            Save as New
          </Button>
        ) : (
          <p></p>
        )}

        {this._selectedCostsheet && this._selectedHeader && (
          <>
            <Button
              onClick={() =>
                this.updateHeader(this.props.location.state.bomHeader)
              }
            >
              Save Changes
            </Button>
            <Button
              color="danger"
              onClick={() =>
                this.sendForApproval(this.props.location.state.bomHeader)
              }
            >
              Send for Approve
            </Button>
          </>
        )}
      </div>
    );
  }
}
