import React from "react";
import axios from 'axios';
import NotificationAlert from "react-notification-alert";
import ReactTable from "react-table";
import {matchSorter} from 'match-sorter';
import {
  Button,
} from "reactstrap";

export default class BOMSLIST extends React.Component {
  userToken = localStorage.getItem('userToken') || '';
  costingURL = process.env.REACT_APP_IP;
  _isMounted = false;
  constructor(){
    super();
    this.state = {
      loading: false,
      bomsHeaders: undefined,
      clients: [],
      user: {},
    }
  }

  checkPageMode = () => {
    let pageMode=document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch(pageMode){
      case true:
        bgclass="bg-ligh";
        headerclass="";
        break;
      default:
        bgclass="bg-dark";
        headerclass="text-primary";
        break;
    }
    return {bg: bgclass,hc: headerclass};
  }

  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };    
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>" && this._isMounted) {
      this._isMounted && this.refs.notificationAlert.notificationAlert(options);
    }
  }

  logOut = () => {
    this._isMounted=false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }

  handleError = (error,task='unknown') => {
    let response = error.response?error.response:{};
    let message = error.message?error.message:'';
    if (response.status!==undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors=message;        
      }
      this.notify("tr", "warning", errors);
    }
  }

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }

  deleteHeaders = (id) => {
    this.toggleLoading();
    axios.delete(
      process.env.REACT_APP_IP + '/bom_header/' + id + '/',
      { headers: { "Authorization": "Token " + this.userToken } }
    ).then(function(){
      this.notify("tr", "success", "BOM deleted!");
      this.toggleLoading();
      this.getBomsHeaders();
      this.getProviders();
    }.bind(this)).catch(function(error){
      this.handleError(error,"delete  BOM")
    }.bind(this));
  }

  OpenSheet = (costingId, headerId, costingNames ) => {
    console.log(costingId, headerId, costingNames)
    const path = "/admin/boms"
    this.props.history.push({
      pathname: path,
      state: { 
        bomHeader: headerId, 
        costingId: costingId,
        costingName: costingNames,
        editProcess: false
       }
    })
  }

  goToCreate = () => {
    const path = "/admin/boms"
    this.props.history.push({
      pathname: path,
    })
  }

  loadrender= (data) => {
    this.setState({
      bomsHeader: data
      //data.map(header => {
        
          //props.header
          //...header,
          //header: header.id,
          //bom_style: header.bom_style.name,
          //client: header.client,
          //costing: header.bom_style.costing,
          /*actions: (<>
            <Button
              color="info"
              size="sm"
              className="btn-icon btn-link"
              //aqui el header me regresa toda la info del BOM clickeado
              //onClick={()=>console.log(header, header.costing_id.split(' ')[0])}
              onClick={(e) => {
                    //console.log(prop)
                    this.OpenSheet(header.bom_style.costing_id, header.id, header.bom_style.name);
               }}
              //onClick={this.OpenSheet(header.costing_id.split(' ')[0])}
            ><i className="fa fa-edit" /></Button>
            <Button
              color="danger"
              size="sm"
              className="btn-icon btn-link"
              onClick={()=>this.deleteHeaders(header.id)}
              //onClick={()=>console.s(header)}
            > 
            <i className="fa fa-times" />
            </Button>
          </>)*/
        
      //})
    });
    //console.log('guarado en el estado', this.state.bomsHeader)
  }

  _loadedBomsHeaders = false;
  getBomsHeaders = () => {
    this.toggleLoading()
    axios.get(
      //this.costingURL + '/bom_header/',
      this.costingURL + '/bom_header/get_account_approve_list/?flag=N', 

      { headers: {
        "Authorization": "Token " + this.userToken
      } }
    ).then(function(response){
      /*this.setState({
        bomHeaders: {
          name:response.data[1]}
      })*/
      this._loadedBomsHeaders = true;
      //console.log(response.data)
     // const boms = response.data
      //this.loadrender(boms);
      
      //console.log('getbomsheader',this.state.bomHeaders)
      this.setState({
        bomsHeaders: response.data.map(header => {
          return {
            header: header.id,
            bom_style: header.bom_style.name,
            client: header.client,
            costing: header.bom_style.costing,
            actions: (<>
              <Button
                color="info"
                size="sm"
                className="btn-icon btn-link"
                //aqui el header me regresa toda la info del BOM clickeado
                //onClick={()=>console.log(header, header.costing_id.split(' ')[0])}
                onClick={(e) => {
                      //console.log(prop)
                      this.OpenSheet(header.bom_style.costing_id, header.id, header.bom_style.name);
                 }}
                //onClick={this.OpenSheet(header.costing_id.split(' ')[0])}
              ><i className="fa fa-edit" /></Button>
              <Button
                color="danger"
                size="sm"
                className="btn-icon btn-link"
                onClick={()=>this.deleteHeaders(header.id)}
                //onClick={()=>console.s(header)}
              > 
              <i className="fa fa-times" />
              </Button>
            </>)
          }
        })
      })
      this.toggleLoading();
    }.bind(this)).catch(function(error){
      //console.log(error)
      this.handleError(error,'boms headers');
    })
  }

  componentDidMount(){
    this._isMounted=true;
    this.getBomsHeaders();
  }

  render(){
    const {
      loading,
      //bomsHeaders,
      //data
    } = this.state;
    const pageMode=this.checkPageMode();
    //console.log('dentro de render', this.state.bomsHeaders)
    let inverted = pageMode.bg==="bg-ligh" ? 'inverted' : '';
    return(
    <div className="content">
      <div className="blackdiv" id="blackdiv" style={loading?{display:''}:{display:'none'}}>
        <div className="ui segment">
          <div className={`ui active transition ${inverted} visible dimmer`}>
            <div className="content"><div className="ui text loader">Loading</div></div>
          </div>
          <div style={{minHeight:400}}><p>&nbsp;</p></div>
        </div>
      </div>

      <div className="react-notification-alert-container">
        <NotificationAlert ref="notificationAlert" />
      </div>

      <h1>BOMS</h1>
      <Button 
        onClick={this.goToCreate}
      >+ New BOM
      </Button>
      {/*this._loadedBomsHeaders && 
      /*<Table>
         <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">First</th>
      <th scope="col">Last</th>
      <th scope="col">Handle</th>
    </tr>
  </thead>
  <tbody>
  <tr>
    
    
      <th scope="row">1</th>
      <td>Mark</td>
      <td>Otto</td>
      <td>@mdo</td>
    </tr>
    </tbody>
        </Table>*/}

      {this._loadedBomsHeaders && 
      <ReactTable 
      responsive
        data = {this.state.bomsHeaders}
        filterable
        defaultFilterMethod = {(filter, row, ) => {
          const id = filter.pivotId || filter.id
          //console.log(filter,row[id],id);
          const filterValue = filter.id==="codigo" ? parseFloat(filter.value) : filter.value.toLowerCase();
          const filterRow = id==="codigo" ? row[id] : row[id].toLowerCase();
          return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
      }}
        columns={[
          {
            Header: "Style",
            accessor: "bom_style",
            filterMethod: (filter, row) =>
                                matchSorter(row, filter.value, { keys: ["bom_style"] }),
                                filterAll: true,
            sortable: true,
            filterable: true,
            
          },
          {
            Header: "Client",
            accessor: "client",
            sortable: true,
            filterable: true,
          },
          {
            Header: "Costing",
            accessor: "costing",
            filterMethod: (filter, row) =>
                                matchSorter(row, filter.value, { keys: ["costing_name"] }),
                                filterAll: true,
            //sortable: true,
            //filterable: true,
          },
          {
            Header: "ACTION",
            accessor: "actions",
            sortable: false,
            filterable: false,
            width: 150
          }
        ]}
        defaultPageSize = {5}
        showPaginationTop
        showPaginationBottom = {false}
        defaultSorted = {[
          {
              id: "codigo",
              desc: true
          }
      ]}
        className="-striped -highlight primary-pagination"
    />}
    </div>);
  }
}