import React from "react";
import classnames from "classnames";
import { Redirect, Link } from "react-router-dom";
import axios from "axios";
import NotificationAlert from "react-notification-alert";
import './login.css'
// reactstrap components
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    CardTitle,
    Form,
    Container,
    Col,
    Row,
    Input,
    InputGroup,
    InputGroupAddon,
    InputGroupText
} from "reactstrap";

class Login extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            Email: "",
            focusEmail: false,
            Password: "",
            focusPassword: false,
            _LoggedIn: false,
            _RecoverPass: false,
        }
    }

    notify = (place, type, msg) => {
        let options = {};
        options = {
            place: place,
            message: (<div>
                <div>{msg}</div>
            </div>),
            type: type,
            icon: "tim-icons icon-bell-55",
            autoDismiss: 7
        };

        let notificationContainer = document
            .getElementsByClassName("react-notification-alert-container");
        if (notificationContainer[0].innerHTML === "<div></div>") {
            this.refs.notificationAlert.notificationAlert(options);
        }
    };

    componentDidMount() {
        document.body.classList.toggle("login-page");
    }
    componentWillUnmount() {
        document.body.classList.toggle("login-page");
    }

    handleEmailChange = (event) => {
        const value = event.target.value;
        this.setState({ Email: value });
    }

    handlePasswordChange = (event) => {
        const value = event.target.value;
        this.setState({ Password: value });
    }

    SignInUser = async (username, password) => {
        return await axios.post(
            process.env.REACT_APP_IP + '/signin',
            {
                "username": username,
                "password": password
            },
            { "Content-Type": "application/json" }
        );
    }

    recover = () => {
        const path = "/recover"
        this.props.history.push({
            pathname: path
        })
        // views.push('/managments_boms')
        //    views.push('/approve_boms')
    }

    handleAuthentication = (event) => {
        event.preventDefault();
        this.SignInUser(this.state.Email, this.state.Password).then(function (response) {
            const views = [
                ...response.data.views,
            ]
            /*views.push('/managments_boms')
            views.push('/approve_boms')*/
            //console.log(response.data)
            localStorage.setItem('userToken', response.data.token);
            localStorage.setItem('valid_time', response.data.valid_time);
            localStorage.setItem('role', response.data.role);
            localStorage.setItem('user_id', response.data.user_id);
            localStorage.setItem('user_name', response.data.user_name);
            localStorage.setItem('user_email', response.data.user_email);
            localStorage.setItem('permissions', JSON.stringify(response.data.permissions));
            localStorage.setItem('views', views);
            this.setState({ _LoggedIn: true });
        }.bind(this)).catch(function (error) {
            let errorMessage = '';
            if (error.response !== undefined) {
                for (let [key, value] of Object.entries(error.response.data)) {
                    errorMessage += `${key}: ${value}\t`;
                }
            } else {
                errorMessage = error.message;
            }
            this.notify("tr", "warning", errorMessage);
        }.bind(this));
    }

    _homeRedirect = <Redirect to={{ pathname: "/admin/home" }} />

    _passRedirect = <Redirect to={{ pathname: "/admin/recover" }} />

    render() {
        return (<>
            <div className="react-notification-alert-container">
                <NotificationAlert ref="notificationAlert" />
            </div>
            {this.state._LoggedIn ? this._homeRedirect : ''}
            {this.state._RecoverPass ? this._passRedirect : ''}
            <div className="content">
                <Container>
                    <Col className="ml-auto mr-auto" lg="4" md="6">
                        <Form className="form" onSubmit={e => this.handleAuthentication(e)}>
                            <Card className="card-login">
                                <CardHeader className="card-header-name">
                                    <CardTitle tag="h1"  className="card-title-name">&nbsp;VULCANO </CardTitle>
                                </CardHeader>
                                <CardBody>
                                    <InputGroup className={classnames({
                                        "input-group-focus": this.state.Emailfocus
                                    })}>
                                        <InputGroupAddon addonType="prepend" className="inputadd">
                                            <InputGroupText className="input-group-text">
                                                <i className="tim-icons icon-single-02"></i>
                                            </InputGroupText>
                                        </InputGroupAddon>
                                        <Input
                                            type="text"
                                            placeholder="Email"
                                            onFocus={e => this.setState({ Emailfocus: true })}
                                            onBlur={e => this.setState({ Emailfocus: false })}
                                            value={this.state.Email}
                                            onChange={e => this.handleEmailChange(e)}
                                            className="input"
                                        />
                                    </InputGroup>
                                    <InputGroup className={classnames({
                                        "input-group-focus": this.state.Passwordfocus
                                    })}>
                                        <InputGroupAddon addonType="prepend" className="inputadd">
                                            <InputGroupText className="input-group-text"><i id='iconl' className="tim-icons icon-key-25"></i></InputGroupText>
                                        </InputGroupAddon>
                                        <Input
                                            type="password"
                                            placeholder="Password"
                                            onFocus={e => this.setState({ Passwordfocus: true })}
                                            onBlur={e => this.setState({ Passwordfocus: false })}
                                            value={this.state.Password}
                                            onChange={e => this.handlePasswordChange(e)}
                                            className="input"
                                        />
                                    </InputGroup>
                                    <Row>
                                        <Col sm="6"></Col>
                                        <Col sm="6">
                                           <Link to='/auth/recover' className="forgot">Forgot Password?</Link>
                                        </Col>
                                    </Row>
                                    <Button type="submit">
                                        Log in
                                    </Button>
                                </CardBody>
                            </Card>
                        </Form>
                    </Col>
                </Container>
            </div>
        </>);
    }
}

export default Login;