import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  Button,
  CardHeader,
  Label
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

class cpoBalance extends React.Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      productOrder: [],
      selectedPO: null,
      styles_po: null,
      filesForm: ["1"],
      selectedFile: null,
      uploadPO: false,
      uploadAllPO: false,
      resumePO: false,
      clientList: [],
      fileList: [],
      flagBuy: false,
      tableMaterial: []
    };
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  uploadChangeFilePO = (event) => {
    let selectedClientPo = this.state.selectedClientPo;
    this.toggleLoading();
    for (const i in this.state.fileList) {
      const formData = new FormData();
      formData.append("file", this.state.fileList[0]);
      formData.append(
        "clients",
        this.state.flagBuy ? "-1" : this.state.clientList[i]
      );
      axios
        .post(process.env.REACT_APP_IP + `/po/create_po/`, formData, {
          headers: {
            Authorization: "Token " + this.userToken,
            "Content-Type": "multipart/form-data",
          },
        })
        .then(
          function() {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "success", "Upload File PO ");
          }.bind(this)
        )
        .catch(
          function(error) {
            this.toggleLoading();
            this.componentDidMount();
            this.notify("tr", "warning", "Bad document format ");
          }.bind(this)
        );
    }
    this.toggleUploadPOModal();
    selectedClientPo.label = "";
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  openPo = (po_id) => {
    const path = "/admin/resume_po";
    this.props.history.push({
      pathname: path,
      state: {
        poId: po_id,
      },
    });
  };

  getPOS = () => {
    this.toggleLoading();
    axios
      .get(process.env.REACT_APP_IP + `/po/get_my_po/?flag=I`, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            productOrder: response.data.map((pos) => {
              return {
                label:  pos.po_number,
                value: pos.id,
                consumption: pos.consumption,
                consumption_ot: pos.consumption_ot,
                description: pos.description,
                diference: pos.diference,
                inventory: pos.inventory,
                material_costing_id: pos.material_costing_id,
                pedidos: pos.orders,
                type: pos.type,
                //po_number: pos.po_number,
                //po_id: pos.id,
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  getPoResume = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`,
        { headers: { Authorization: "Token " + this.userToken } }
      )
      .then(
        function() {
          this.componentDidMount();
          window.open(
            process.env.REACT_APP_IP +
              `/admin/get_resume_pos/?client=${this.state.selectedClientPo}`
          );
          this.notify("tr", "success", "Resume downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "Get Resume failed");
        }.bind(this)
      );
  };

  deletePO = (id) => {
    this.toggleLoading();
    let poF = new FormData();
    poF.append("po_id", id);
    axios
      .post(this.costingURL + "/po/clients_delete_po/", poF, {
        headers: { Authorization: "Token " + this.userToken },
      })
      .then(
        function() {
          this.notify("tr", "success", "Po Deleted!");
          this.toggleLoading();
          this.componentDidMount();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Deleted");
          this.toggleLoading();
        }.bind(this)
      );
  };

  _clientsLoaded = false;
  getClients = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/clients/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              clients: response.data.map((client) => {
                return {
                  value: client.id,
                  label: client.name,
                  abbreviation: client.abbreviation,
                };
              }),
            });
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  approveAllPos = () => {
    axios
      .get(
        process.env.REACT_APP_IP +
          `/po/approve_all_pos/?client=${this.state.selectedClientPo.value}`,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function() {
          this.notify("tr", "success", "Pos Approved!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "PO not Approved");
        }.bind(this)
      );
  };

  toggleUploadAllPOsModal = () => {
    this.setState({
      uploadAllPO: !this.state.uploadAllPO,
    });
  };

  toggleUploadPOModal = () => {
    this.setState({
      uploadPO: !this.state.uploadPO,
    });
  };

  toggleGetResumeModal = () => {
    this.setState({
      resumePO: !this.state.resumePO,
    });
  };

  addClientToList = (clientId) => {
    const list = [...this.state.clientList];
    list.push(clientId.value);
    this.setState({
      clientList: list,
    });
  };

  addFileToList = (event) => {
    this.setState({ selectedFile: event.target.files[0] });
    const list = [...this.state.fileList];
    list.push(event.target.files[0]);
    this.setState({
      fileList: list,
    });
  };

  addEvent = () => {
    const listForm = [...this.state.filesForm];
    listForm.push("1");
    this.setState({
      filesForm: listForm,
    });
  };

  resetModal = () => {
    const listForm = ["1"];
    this.setState({
      filesForm: listForm,
    });
  };
  
  searchPO = () => {
    this.toggleLoading();
    axios
    .get(
      process.env.REACT_APP_IP +
        `/materialp/balance_of_materials_in_PO/?po=${this.state.selectedPO}`,
      {
        headers: { Authorization: "Token " + this.userToken },
      }
    )
    .then(
      function(response) {
        this.notify("tr", "success", "OK!");
        this._loadedBomsHeaders = true;
        this.setState({
          tableMaterial: response.data.map((pos) => {
            return {
              label:  pos.po_number,
              value: pos.id,
              consumption: pos.consumption,
              consumption_ot: pos.consumption_ot,
              description: pos.description,
              diference: pos.diference,
              inventory: pos.inventory,
              material_costing_id: pos.material_costing_id,
              pedidos: pos.orders,
              type: pos.type,
              //po_number: pos.po_number,
              //po_id: pos.id,
            };
          }),
        });
        this.toggleLoading();
      }.bind(this)
    )
    .catch(
      function(error) {
        this.handleError(error, "not OK");
      }.bind(this)
    );
  }
  componentDidMount() {
    this._isMounted = true;
    this.getClients();
    this.getPOS();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const { loading, productOrder } = this.state;
    const pageMode = this.checkPageMode();

    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Row className="justify-content-center mt-5">
          <Col md={{ size: 12, order: 2 }}>
            <Card>
              <CardBody>
                <CardHeader>
                  <Row>
                    <Col sm="9">
                      <h1>CPO Balance</h1>
                    </Col>
                    <Col sm="3"></Col>
                    <CardBody>
                      <Row>
                        <Col>
                          <Label>CPO</Label>
                          <Select
                            options={this.state.productOrder }
                            //value={this.state.productOrder ? this.state.productOrder : null}
                            onChange={(o) => {
                              this.setState({
                                selectedPO: o.label,
                              });
                            }}
                          />
                        </Col>
                      {/*  <Col>
                          <Label>TIPO DE MATERIAL</Label>
                          <Select
                            options={this.state.clientsOptions}
                            //value={selectedClient ? selectedClient : null}
                            onChange={(o) => {
                              this.loadFormColor({
                                ...this.state.ColorsForm,
                                client: o.value,
                              });
                            }}
                          />
                          </Col> */}
                        <Col>
                          <br></br>
                          <Button
                            type="submit"
                            color="primary"
                            size="sm"
                            disabled={this.state.loadingColors}
                            className="float-left"
                            onClick = {() => this.searchPO()}
                          >
                            Search
                          </Button>
                        </Col>
                        <Col>
                          <FormGroup
                            id="formGroup_pathToTechpack"
                            className="text-center"
                          >
                            <h4>&nbsp; &nbsp;Actions</h4>
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                              onClick={() => {
                                this.getPoResume();
                                console.log(this.state.selectedClientPo);
                              }}
                            >
                              <i className="tim-icons icon-cloud-download-93" />
                              &nbsp;Convert to Excel
                            </Button>
                            <Button
                              className="btn-simple"
                              color="info"
                              size="sm"
                              onClick={() => {
                                this.getPoResume();
                                console.log(this.state.selectedClientPo);
                              }}
                            >
                              <i className="tim-icons icon-cloud-download-93" />
                              &nbsp;Download Report
                            </Button>
                          </FormGroup>
                        </Col>
                      </Row>
                    </CardBody>
                    <br></br>
                    {this.state.tableMaterial && (
                      <>
                        <Card className={`${pageMode.bg} border`}>
                          <CardBody>
                            <ReactTable
                              responsive
                              defaultPageSize={5}
                              data={this.state.tableMaterial}
                              filterable
                              showPaginationBottom={true}
                              className="-striped -highlight primary-pagination"
                              columns={[
                                {
                                  Header: "CODIGO",
                                  accessor: "material_costing_id",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["material_costing_id"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "DESCRIPTION",
                                  accessor: "description",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["description"],
                                    }),
                                  filterAll: true,
                                },
                                {
                                  Header: "CONSUMO",
                                  accessor: "consumption",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["consumption"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "CONSUMO OT",
                                  accessor: "consumption_ot",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["consumption_ot"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "INVENTARIO",
                                  accessor: "inventory",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["inventory"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "DIFERENCIA",
                                  accessor: "diference",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["diference"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "EXISTENCIA",
                                  accessor: "delivery_place",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["delivery_place"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "PEDIDOS",
                                  accessor: "pedidos",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["pedidos"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                                {
                                  Header: "TIPO",
                                  accessor: "type",
                                  sortable: true,
                                  filterMethod: (filter, row) =>
                                    matchSorter(row, filter.value, {
                                      keys: ["type"],
                                    }),
                                  filterAll: true,
                                  width: 125
                                },
                              ]}
                            />
                          </CardBody>
                        </Card>
                      </>
                    )}
                  </Row>
                </CardHeader>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}
export default cpoBalance;
