/*eslint-disable array-callback-return*/
import React from "react";
import axios from 'axios';
import { downloadFileFromS3 } from '../helpers/awsS3';

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  CardHeader,
  CardImg,
  Label,
  Button,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

class PoClient extends React.Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      styleOptions: [],
      style: {},
      description: [],
      styles_po: [],
      costingSketch: null,
      techpack: null,
      combos: [],
      colorFabric: null,
      styleSelected: null,
      stylesCombo: []

    }
  }
  userToken = localStorage.getItem('userToken') || '';
  Access = JSON.parse(localStorage.getItem('Access') || '{}');
  Role = localStorage.getItem('role') || '';
  costingURL = process.env.REACT_APP_IP;

  toggleLoading = () => {
    this._isMounted && this.setState({
      loading: !this.state.loading
    })
  }
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>
            {msg}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7
    };
    let notificationContainer = document
      .getElementsByClassName("react-notification-alert-container");
    // If the container child element is not clear then don't 
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };


  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  }


  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = 'unknown') => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : '';
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  }



  getStyle = () => {
    this.toggleLoading()
    axios.get(
      process.env.REACT_APP_IP + `/bom_header/get_account_approve_list/?flag=D`,
      {
        headers: { "Authorization": "Token " + this.userToken }
      }
    ).then(function (response) {
      this._loadedBomsHeaders = true;
      this.setState({
        styleOptions: response.data.map(client => {
          return {
            value: client.id,
            idBom: client.bom_style.id,
            label: `${client.bom_style.costing} `
          }
        })
      })
      this.toggleLoading()
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'error Loading');
    }.bind(this))

  }


  _clientsLoaded = false;
  getStyleInfo = (id) => {
    //this.toggleLoading();
    const formData = new FormData();
    formData.append("styles", id);
    axios.post(
      this.costingURL + '/combos_components/get_info_style/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
          "Content-Type": "multipart/form-data"
        }
      }
    ).then(function (response) {
      this._clientsLoaded = true;
      this._isMounted &&
        this.setState({
          styles_po: response.data
        })
      this.downloadSketch(this.state.styles_po.costing && this.state.styles_po.costing.pathToSketch)

      //this.toggleLoading();
    }.bind(this)).catch(function (error) {
      //this.toggleLoading();
      this.handleError(error, 'clients');
    }.bind(this));
  }

  getColorFabric = () => {
    //this.toggleLoading();
    axios.get(
      this.costingURL + '/colors_fabrics/',
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function (response) {
      this._clientsLoaded = true;
      this._isMounted &&
        this.setState({
          colorFabric: response.data.map(client => {
            return {
              value: client.id,
              label: `${client.color} `
            }
          })
        })

      //this.toggleLoading();
    }.bind(this)).catch(function (error) {
      //this.toggleLoading();
      this.handleError(error, 'clients');
    }.bind(this));
  }





  addEvent = () => {
    const listForm = [...this.state.filesForm]
    listForm.push('1')
    this.setState({
      filesForm: listForm
    })
    console.log(this.state.filesForm)
  }

  handleChange = (parameter, value) => {
    this.setState({
      style: { ...this.state.style, [parameter]: value }
    });
  }

  _sketchLoaded = false;
  downloadSketch = (file) => {
    const url = this.costingURL + '/costingHeader/download/?key=' + file;
    const headers = {
      params: { "key": file },
      headers: { "Authorization": "Token " + this.userToken }
    }
    axios.get(
      url,
      headers
    ).then(function (response) {
      this._sketchLoaded = true
      this.setState({
        costingSketch: response.data.url
      })
    }.bind(this));
  }

  //_sketchLoaded = false;
  downloadTechPack = (filekye) => {
    //console.log('download techpack', filekye);
    //event.preventDefault();
    let url = "";
    url = process.env.REACT_APP_IP + '/costingHeader/download/?key=' + filekye;
    //url = process.env.REACT_APP_IP + '/bom_header/download/?key=' + filekye;
    let config = {
      params: { "key": filekye },
      headers: {
        "Authorization": "Token " + this.userToken
      }
    }
    downloadFileFromS3(url, config).then(function (response) {
      const result = { file: response };
      window.open(result.file);
      this.notify("tr", "success", " downloaded!");
    }.bind(this)).catch(function (error) {
      this.handleError(error, 'download file');
    }.bind(this));

  }
  rejectCombo = (id) => {
    const formData = new FormData();
    formData.append("style", id);
    formData.append("flag", 'N');
    axios.post(
      this.costingURL + '/combos_components/change_cc_status/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this._isMounted && this.notify("tr", "success", "APPROVED!");
      this.componentDidMount();
      const path = "/admin/review_component"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleApprove");
    }.bind(this))
  }
  approveCombo = (id) => {
    console.log(id)
    const formData = new FormData();
    formData.append("style", id);
    formData.append("flag", 'F');
    axios.post(
      this.costingURL + '/combos_components/change_cc_status/',
      formData,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this._isMounted && this.notify("tr", "success", "APPROVED!");
      this.componentDidMount();
      const path = "/admin/review_component"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  orderCombos = (combo) => {
    let order = { 'combos': null };
    let orde;
    orde = combo.flatMap(x => x.flatMap(y => [y]));
    order.combos = orde
    console.log(order)
    axios.post(
      this.costingURL + '/combos_components/',
      order,
      {
        headers: {
          "Authorization": "Token " + this.userToken,
        }
      }
    ).then(function () {
      this._isMounted && this.notify("tr", "success", "SAVED!");
      this.componentDidMount();
      const path = "/admin/create_combocomponent"
      this.props.history.push({
        pathname: path
      })
    }.bind(this)).catch(function (error) {
      this.handleError(error, "handleApprove");
    }.bind(this))
  }

  componentDidMount() {
    this._isMounted = true;
    // this.getClients();
    //  this.getStyle()
    this.getColorFabric()
    this.downloadSketch(this.props.location.state.styleSelected.costing && this.props.location.state.styleSelected.costing.pathToSketch)
    // this.getStyleInfo(this.state.styleSelected)

  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }
  render() {

    const {
      loading,
      costingSketch,
    } = this.state;
    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';
    return (
      <div className="content">
        <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content"><div className="ui text loader">Loading</div></div>
            </div>
            <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>


        <Row className="justify-content-center mt-5" >
          <Col md={{ size: 11, order: 1 }}>
            <Row>
              {this.props.location.state.styleSelected.style &&
                <>
                  <Col sm='6'>
                    <Card>
                      <CardHeader>CostSheet Info</CardHeader>
                      <CardBody>
                        <FormGroup row>
                          <Label for="exampleEmail" className="text-info" sm={4}>Client</Label>
                          <Col sm={8}>{this.props.location.state.styleSelected.costing && this.props.location.state.styleSelected.costing.client.name}</Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label for="exampleEmail" className="text-info" sm={4}>Style</Label>
                          <Col sm={8}>{this.props.location.state.styleSelected.costing && this.props.location.state.styleSelected.costing.style}</Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label for="exampleEmail" className="text-info" sm={4}>Season</Label>
                          <Col sm={8}>{this.props.location.state.styleSelected.costing && this.props.location.state.styleSelected.costing.season.name}</Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label for="exampleEmail" className="text-info" sm={4}>BOM Style</Label>
                          <Col sm={8}>{this.props.location.state.styleSelected.style && this.props.location.state.styleSelected.style.name}</Col>
                        </FormGroup>
                        <FormGroup row>
                          <Label for="exampleEmail" className="text-info" sm={4}>Techpack</Label>
                          <Button
                            color="info"
                            className="btn-simple"
                            size="sm"
                            onClick={() =>
                              this.downloadTechPack(this.props.location.state.styleSelected.costing && this.props.location.state.styleSelected.costing.pathToTechpack)}
                          >
                            <i className="tim-icons icon-cloud-download-93" />
                            TECHPACK
                          </Button>
                        </FormGroup>
                      </CardBody>
                    </Card>
                  </Col>
                  <Col sm='6'>
                    <Card>
                      <CardBody>
                        <FormGroup row>
                          <Label for="exampleEmail" className="text-info" sm={4}>Sketch</Label>
                          <Col sm={8}>
                            <CardImg top
                              src={costingSketch && costingSketch}
                              alt='skecth'
                              style={{
                                maxWidth: '300px',
                                maxHeight: '300px'
                              }}
                            />

                          </Col>
                        </FormGroup>
                      </CardBody>
                    </Card>
                  </Col>
                  <>{
                    this.props.location.state.stylesCombo && this.props.location.state.stylesCombo.map((index, key) => (

                      <Col sm='4'>
                        <Card>
                          <CardBody>
                            <FormGroup row>
                              <Label for="exampleEmail" className="text-info" sm={12}>
                                DM:
                                <br>
                                </br>
                                <Label  >
                                  {index.dm}
                                </Label>
                              </Label>
                              <Label for="exampleEmail" className="text-info" sm={12}>
                                {'Component Name: '}
                                <Label  >
                                  {index.component}
                                </Label>
                              </Label>
                              <Label for="exampleEmail" className="text-info" sm={12}>
                                {'Color Style: '}
                                <Label  >
                                  {index.color_bom}
                                </Label>
                              </Label>
                              <Label for="exampleEmail" className="text-info" sm={12}>
                                {'Color Fabric: '}
                                <Label  >
                                  {index.color_fabric.color}
                                </Label>
                              </Label>
                              <Label for="exampleText" sm={12} className="text-primary">Description</Label>
                              <Col sm={12}>
                                <Label  >
                                  {index.description}
                                </Label>
                              </Col>
                            </FormGroup>
                          </CardBody>
                        </Card>
                      </Col>
                    )
                    )


                  } </>
                  {
                    this.props.location.state.flag === false &&
                    <>

                      <Col sm={{ size: '2', offset: 7 }}>
                        <Button className="btn-fill"
                          color="warning"
                          type="button"
                          onClick={event => {
                            this.rejectCombo(this.props.location.state.styleSelected.style && this.props.location.state.styleSelected.style.id)
                          }}
                        >
                          REJECT
                        </Button>
                      </Col>
                      <Col sm={{ size: '3', offset: 0 }}>
                        <Button className="btn-fill"
                          color="success"
                          type="button"
                          onClick={event => {
                            this.approveCombo(this.props.location.state.styleSelected.style && this.props.location.state.styleSelected.style.id)
                          }}
                        >
                          APPROVE
                        </Button>
                      </Col>
                    </>

                  }
                </>
              }
            </Row>
          </Col>
        </Row>
      </div>
    );
  }
}
export default PoClient;




