/*eslint-disable array-callback-return*/
import React from "react";
import ReactTable from "react-table";
import axios from "axios";
import Select from "react-select";
import { matchSorter } from "match-sorter";
import { downloadFileFromS3 } from "../helpers/awsS3";
import PoClientC from "./approveConsumptions";

// reactstrap components
import {
  Row,
  Col,
  FormGroup,
  Card,
  CardBody,
  Label,
  Button,
  Input,
  TabPane,
  TabContent,
  Nav,
  NavItem,
  NavLink,
} from "reactstrap";
import NotificationAlert from "react-notification-alert";

/*eslint-disable-next-line*/
const nameComponent = {
  S: "SELF",
  C1: "CONTRAST1",
  C2: "CONTRAST2",
  C3: "CONTRAST3",
  C4: "CONTRAST4",
  C5: "CONTRAST5",
  R: "RIB",
  R1: "RIB1",
  R2: "RIB2",
  R3: "RIB3",
  R4: "RIB4",
  J: "JERSEY",
  J1: "JERSEY1",
  J2: "JERSEY2",
  J3: "JERSEY3",
  W: "WOVEN",
  W1: "WOVEN1",
  WA: "WATTA",
  WA1: "WATTA1",
  M: "MESH",
  M1: "MESH1",
  A: "APLIQUE",
  A1: "APLIQUE1",
  I1: "INTERLINING",
  I2: "INTERLINING2",
  I3: "INTERLINING3",
  WAF: "WAFFLE",
  TRI: "TRICOT",
  TAF: "TAFFETA",
  TA1: "TAFFETA1",
};
class PoClient extends React.Component {
  constructor() {
    super();
    this.state = {
      vTabs: "vt1",
      loading: false,
      styleOptions: [],
      styleOptionsCombo: [],
      style: {},
      styleComboSelected: null,
      stylesCombo: [],
      stylesStyle: [],
      description: [],
      styles_po: [],
      costingSketch: null,
      techpack: null,
      combos: [],
      colorFabric: null,
      component_name: null,
      selectedStyle: [],
      validate: false,
      flag: "",
      DMselect: null,
      ColorSelect: null,
    };
    this.createService = React.createRef();
  }
  userToken = localStorage.getItem("userToken") || "";
  Access = JSON.parse(localStorage.getItem("Access") || "{}");
  Role = localStorage.getItem("role") || "";
  costingURL = process.env.REACT_APP_IP;

  addCard = (styles) => {
    return (
      <Card>
        <CardBody>
          <FormGroup row>
            <Label for="exampleEmail" className="text-info" sm={12}>
              DM:
              <br></br>
              <Label>
                <FormGroup>
                  <Select
                    // options={this.state.styles_po}
                    onChange={(e) => {
                      //combo[id][key].color_fabric = e.value
                    }}
                  />
                </FormGroup>
              </Label>
            </Label>
            <Label for="exampleEmail" className="text-info" sm={12}>
              {"Component Name: "}
              <Label>nameComponent[dm.component_name]</Label>
            </Label>
            <Label for="exampleEmail" className="text-info" sm={12}>
              {"Color Style: "}
              <Label>index.color</Label>
            </Label>
          </FormGroup>
          <FormGroup>
            <Select
              // options={this.state.colorFabric}
              onChange={(e) => {
                //   combo[id][key].color_fabric = e.value
              }}
            />
          </FormGroup>
          <FormGroup row>
            <Label for="exampleText" sm={12} className="text-primary">
              Description
            </Label>
            <Col sm={12}>
              <Input
                type="textarea"
                //   value={header.description}
                onChange={(e) => {
                  //    combo[id][key].description = e.target.value
                }}
              />
            </Col>
          </FormGroup>
        </CardBody>
      </Card>
    );
  };

  toggleLoading = () => {
    this._isMounted &&
      this.setState({
        loading: !this.state.loading,
      });
  };
  notify = (place, type, msg) => {
    let options = {};
    options = {
      place: place,
      message: (
        <div>
          <div>{msg}</div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    let notificationContainer = document.getElementsByClassName(
      "react-notification-alert-container"
    );
    // If the container child element is not clear then don't
    // push any other notification.
    if (notificationContainer[0].innerHTML === "<div></div>") {
      this.refs.notificationAlert.notificationAlert(options);
    }
  };

  logOut = () => {
    this._isMounted = false;
    localStorage.clear();
    this.componentWillUnmount();
    this.props.history.push("/auth/login");
  };

  componentWillUnmount() {
    this._isMounted = false;
  }
  handleError = (error, task = "unknown") => {
    let response = error.response ? error.response : {};
    let message = error.message ? error.message : "";
    //console.log(response,message,task)
    if (response.status !== undefined && response.status === 401) {
      this.logOut();
    } else {
      let errors = "";
      if (response.data !== undefined) {
        for (let [key, value] of Object.entries(response.data)) {
          errors += `${key}: ${value}\t`;
        }
      } else {
        errors = message;
      }
      this.notify("tr", "warning", errors);
    }
  };

  getStyle = () => {
    this.toggleLoading();
    let data = new FormData();
    data.append("flag", "A");
    axios
      .get(
        process.env.REACT_APP_IP + `/combos/?flag=C`,
        //data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          let boms = response.data;
          this.setState({
            styleOptions: boms.map((header) => {
              return {
                id: header.id,
                value: header.id,
                costing_id: header.styles,
                actions: (
                  <>
                    <div className="">
                      <Button
                        onClick={() => this.handleReview(header.id)}
                        color="info"
                        size="sm"
                      >
                        Send for Approval <i className="fa fa-edit" />
                      </Button>
                      <br></br>
                      {/*<Button
                    onClick={() => this.handleReject(header.id)}
                    color="danger"
                    size="sm"
                  >
                    Reject <i className={"fa fa-times"} />
                  </Button>*/}
                    </div>
                  </>
                ),
              };
            }),
          });
          this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  getStyleCombo = () => {
    this.toggleLoading();
    let data = new FormData();
    data.append("flag", "N");
    axios
      .post(
        process.env.REACT_APP_IP + `/combos_components/get_styles_list/`,
        data,
        {
          headers: { Authorization: "Token " + this.userToken },
        }
      )
      .then(
        function(response) {
          this._loadedBomsHeaders = true;
          this.setState({
            styleOptionsCombo: response.data.map((client) => {
              return {
                value: client.id,
                // idBom: client.bom_style.id,
                label: `${client.costing} `,
              };
            }),
          });
          // this.toggleLoading()
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "error Loading");
        }.bind(this)
      );
  };

  handleReview = (header_id) => {
    console.log("aqui estoy, esto traigo", header_id)
    const formData = new FormData();
    this.toggleLoading();
    let id = header_id;
    formData.append("style", id.id);
    axios
    .get(
      this.costingURL + `/combos/${header_id}/`,
      //formData,
      {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      }
      )
      .then(
        function(response) {
          console.log("response", response)
          this._clientsLoaded = true;
          this._isMounted &&
          this.setState({
            stylesCombo: response.data,
          });
          this.downloadSketch(
            this.state.styles_po.costing &&
            this.state.styles_po.costing.pathToSketch
            );
            this.toggleLoading();
            this.changepage(response.data, response.data);
          }.bind(this)
          )
          .catch(
            function(error) {
              this.toggleLoading();
              this.handleError(error, "clients");
        }.bind(this)
      );
  };

  
  getInfoStyle = (id, data) => {
    const formData = new FormData();
    formData.append("styles", id);
    axios
      .post(this.costingURL + "/combos_components/get_info_style/", formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              styleSelected: response.data,
            });
          // this.getdataSelect(response.data)
          this.downloadSketch(
            this.state.styles_po.costing &&
              this.state.styles_po.costing.pathToSketch
          );
          this.changepage(data, response.data);
          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  changepage = (data, data2) => {


    const path = "/admin/account_components"
    this.props.history.push({
      pathname: path,
      state: {
        styleSelected: data2,
        stylesCombo: data,
        styles_po: this.state.styles_po,
        flag: 'C'
      }
    })
  }

  getStyleInfoCombo = (id) => {
    //this.toggleLoading();
    const formData = new FormData();
    formData.append("style", id);
    axios
      .post(this.costingURL + "/combos_components/get_resume/", formData, {
        headers: {
          Authorization: "Token " + this.userToken,
          "Content-Type": "multipart/form-data",
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              stylesCombo: response.data["combos"],
              stylesStyle: response.data["styles"],
            });
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  getdataSelect = (style) => {
    this.setState({
      DMselect: style.map((client) => {
        return {
          value: client.id,
          label: `${client.dm} `,
        };
      }),
    });
  };

  getColorFabric = () => {
    //this.toggleLoading();
    axios
      .get(this.costingURL + "/colors_fabrics/", {
        headers: {
          Authorization: "Token " + this.userToken,
        },
      })
      .then(
        function(response) {
          this._clientsLoaded = true;
          this._isMounted &&
            this.setState({
              colorFabric: response.data.map((client) => {
                return {
                  value: client.id,
                  label: `${client.color} `,
                };
              }),
            });

          //this.toggleLoading();
        }.bind(this)
      )
      .catch(
        function(error) {
          //this.toggleLoading();
          this.handleError(error, "clients");
        }.bind(this)
      );
  };

  _sketchLoaded = false;
  downloadSketch = (file) => {
    const url = this.costingURL + "/costingHeader/download/?key=" + file;
    const headers = {
      params: { key: file },
      headers: { Authorization: "Token " + this.userToken },
    };
    axios.get(url, headers).then(
      function(response) {
        this._sketchLoaded = true;
        this.setState({
          costingSketch: response.data.url,
        });
      }.bind(this)
    );
  };

  //_sketchLoaded = false;
  downloadTechPack = (filekye) => {
    //console.log('download techpack', filekye);
    //event.preventDefault();
    let url = "";
    url = process.env.REACT_APP_IP + "/costingHeader/download/?key=" + filekye;
    //url = process.env.REACT_APP_IP + '/bom_header/download/?key=' + filekye;
    let config = {
      params: { key: filekye },
      headers: {
        Authorization: "Token " + this.userToken,
      },
    };
    downloadFileFromS3(url, config)
      .then(
        function(response) {
          const result = { file: response };
          window.open(result.file);
          this.notify("tr", "success", " downloaded!");
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "download file");
        }.bind(this)
      );
  };

  handleSelectChange = (event) => {
    let opts = [],
      opt;
    for (let i = 0, len = event.target.options.length; i < len; i++) {
      opt = event.target.options[i];
      if (opt.selected) {
        opts.push(opt.value);
        console.log(opt);
      }
    }
    this.setState({ selectedStyle: opts });
  };

  approveCombo = (combo, id) => {
    console.log(id);
    let order = { combos: null };
    let orde;
    let ids;
    ids = id.flatMap((x) => parseInt(x.split(" ")[0]));
    orde = combo.flatMap((x) => x.flatMap((y) => [y]));
    order.combos = orde;
    console.log(order);
    axios
      .post(
        this.costingURL + "/combos_components/",
        (order = {
          styles: ids,
          combos: orde,
        }),
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function() {
          this._isMounted && this.notify("tr", "success", "SAVED!");
          this.componentDidMount();
          const path = "/admin/create_combocomponent";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );

    const formData = new FormData();
    formData.append("style", ids);
    formData.append("flag", "A");
    axios
      .post(
        this.costingURL + "/combos_components/change_cc_status/",
        formData,
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function() {
          this._isMounted && this.notify("tr", "success", "APPROVED!");
          this.componentDidMount();
          const path = "/admin/create_combocomponent";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  };

  orderCombos = (combo, id) => {
    let order = { combos: null };
    let orde;
    let ids;
    ids = id.flatMap((x) => parseInt(x.split(" ")[0]));
    orde = combo.flatMap((x) => x.flatMap((y) => [y]));
    order.combos = orde;
    order = order + ids;
    console.log("order", order);
    axios
      .post(
        this.costingURL + "/combos_components/",
        (order = {
          styles: ids,
          combos: orde,
        }),
        {
          headers: {
            Authorization: "Token " + this.userToken,
          },
        }
      )
      .then(
        function() {
          this._isMounted && this.notify("tr", "success", "SAVED!");
          this.componentDidMount();
          const path = "/admin/create_combocomponent";
          this.props.history.push({
            pathname: path,
          });
        }.bind(this)
      )
      .catch(
        function(error) {
          this.handleError(error, "handleApprove");
        }.bind(this)
      );
  };

  validateCombos = (combo) => {
    let order = { combos: null };
    let orde;
    orde = combo.flatMap((x) => x.flatMap((y) => [y]));
    order.combos = orde;

    this.approveCombo(combo, this.state.selectedStyle);
  };

  componentDidMount() {
    this._isMounted = true;
    this.getStyle();
    //this.getColorFabric();
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains("white-content");
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  };
  render() {
    const {
      loading,
      styleOptions,
    } = this.state;

    // const selectedStyle = this.state.styleOptions.find(
    //  o=>o.value === this.state.ColorsForm.client
    // )

    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? "inverted" : "";
    return (
      <div className="content">
        <div
          className="blackdiv"
          id="blackdiv"
          style={loading ? { display: "" } : { display: "none" }}
        >
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content">
                <div className="ui text loader">Loading</div>
              </div>
            </div>
            <div style={{ minHeight: 400 }}>
              <p>&nbsp;</p>
            </div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>
        <Nav pills className="nav-pills-info nav-pills-icons">
          <NavItem>
            <NavLink
              className={this.state.vTabs === "vt1" ? "active" : ""}
              onClick={() => this.setState({ vTabs: "vt1" })}
            >
              Approve Combos
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={this.state.vTabs === "vt2" ? "active" : ""}
              onClick={() => this.setState({ vTabs: "vt2" })}
            >
              Combos History
            </NavLink>
          </NavItem>
          { /**
           * uID === "100" ?
            <NavItem>
              <NavLink
                className={this.state.vTabs === "vt3" ? "active" : ""}
                onClick={() => this.setState({ vTabs: "vt3" })}
              >
                Approve Consumptions
              </NavLink>
            </NavItem>
            :
            null
            **/
          }
        </Nav>

        <TabContent activeTab={this.state.vTabs}>
          <TabPane tabId="vt1">
            <Row>
              <Col>
                <ReactTable
                  responsive
                  data={styleOptions}
                  filterable
                  defaultFilterMethod={(filter, row) => {
                    const id = filter.pivotId || filter.id;
                    //console.log(filter,row[id],id);
                    const filterValue =
                      filter.id === "codigo"
                        ? parseFloat(filter.value)
                        : filter.value.toLowerCase();
                    const filterRow =
                      id === "codigo" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined
                      ? String(filterRow).startsWith(filterValue)
                      : true;
                  }}
                  columns={[
                    {
                      Header: "Style",
                      accessor: "costing_id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, {
                          keys: ["costing_id"],
                        }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "ACTION",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 250,
                    },
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true,
                    },
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="vt2">
            <Row>
              <Col>
                <ReactTable
                  responsive
                  data={this.state.styleWcombo}
                  filterable
                  defaultFilterMethod={(filter, row) => {
                    const id = filter.pivotId || filter.id;
                    //console.log(filter,row[id],id);
                    const filterValue =
                      filter.id === "codigo"
                        ? parseFloat(filter.value)
                        : filter.value.toLowerCase();
                    const filterRow =
                      id === "codigo" ? row[id] : row[id].toLowerCase();
                    return row[id] !== undefined
                      ? String(filterRow).startsWith(filterValue)
                      : true;
                  }}
                  columns={[
                    {
                      Header: "Style",
                      accessor: "id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, { keys: ["id"] }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "Costing",
                      accessor: "costing_id",
                      filterMethod: (filter, row) =>
                        matchSorter(row, filter.value, {
                          keys: ["costing_id"],
                        }),
                      filterAll: true,
                      sortable: true,
                      filterable: true,
                    },
                    {
                      Header: "ACTION",
                      accessor: "actions",
                      sortable: false,
                      filterable: false,
                      width: 250,
                    },
                  ]}
                  defaultPageSize={5}
                  showPaginationTop
                  showPaginationBottom={false}
                  defaultSorted={[
                    {
                      id: "codigo",
                      desc: true,
                    },
                  ]}
                  className="-striped -highlight primary-pagination"
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="vt3">
            <PoClientC></PoClientC>
          </TabPane>
        </TabContent>
      </div>
    );
  }
}
export default PoClient;
