import React from "react";
import {
  Card,
  CardHeader,
  CardBody,
} from "reactstrap";
import axios from "axios";
import ReactTable from "react-table";
import NotificationAlert from "react-notification-alert";
import { matchSorter } from 'match-sorter';
import Button from "reactstrap/lib/Button";

export default class HistoryDms extends React.Component {
  userToken = localStorage.getItem("userToken") || "";
  _isMounted = false;
  costingURL = process.env.REACT_APP_IP;
  styleId = 0;

  constructor() {
    super();
    this.state = {
      viewEdit: false,
      loading: false,
    }
  }

  componentDidMount() {
    this._isMounted = true;
    if (this.props.location.state) {
      this.GETREPORTDM(this.props.location.state.dm)
    }
  }

  checkPageMode = () => {
    let pageMode = document.body.classList.contains('white-content');
    let bgclass;
    let headerclass;
    switch (pageMode) {
      case true:
        bgclass = "bg-ligh";
        headerclass = "";
        break;
      default:
        bgclass = "bg-dark";
        headerclass = "text-primary";
        break;
    }
    return { bg: bgclass, hc: headerclass };
  }

  GETREPORTDM = (dm) => {
    axios.get(process.env.REACT_APP_IP + `/fabrics/get_historic_prices_dm/?dm=${dm}`, {
      headers: {
        "Authorization": "Token " + this.userToken
      }
    }).then(function (response) {
      this._loadingSeasons = true;
      this._isMounted && this.setState({
        tableDM: response.data
      });
    }.bind(this)).catch(function (error) {
      this.handleError(error);
    }.bind(this));
  }

  goBack = () => {
    const path = "/admin/fabric_reports"
    this.props.history.push({
      pathname: path,
    })
  }

  render() {
    const {
      loading
    } = this.state;

    const pageMode = this.checkPageMode();
    let inverted = pageMode.bg === "bg-ligh" ? 'inverted' : '';

    return (
      <div className="content">
         <Button
                    className="btn-simple"
                    color="info"
                    onClick={() => this.goBack()}
                  >
                    <i class="fas fa-arrow-left"></i> &nbsp; Back
                  </Button>
        <div className="blackdiv" id="blackdiv" style={loading ? { display: '' } : { display: 'none' }}>
          <div className="ui segment">
            <div className={`ui active transition ${inverted} visible dimmer`}>
              <div className="content"><div className="ui text loader">Loading</div></div>
            </div>
            <div style={{ minHeight: 400 }}><p>&nbsp;</p></div>
          </div>
        </div>
        <div className="react-notification-alert-container">
          <NotificationAlert ref="notificationAlert" />
        </div>

        <Card>
        
          <CardHeader style={{ alignSelf: "center" }}>
            
              DM REPORT 
              
          </CardHeader>
          <hr />
          <CardBody>
          <ReactTable responsive
            data={this.state.tableDM}
            filterable
            defaultFilterMethod={(filter, row,) => {
              const id = filter.pivotId || filter.id
              const filterValue = filter.id === "codigo" ? parseFloat(filter.value) : filter.value.toLowerCase();
              const filterRow = id === "codigo" ? row[id] : row[id].toLowerCase();
              return row[id] !== undefined ? String(filterRow).startsWith(filterValue) : true
            }}
            columns={[
              {
                Header: "DM",
                accessor: "dm_modified",
                /*filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["bom_style"] }),
                */
                filterAll: true,
                sortable: true,
                filterable: true,
                width: 500
              },
              {
                Header: "Modified By",
                accessor: "modified_by",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["modified_by"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              },
              {
                Header: "Price Dark",
                accessor: "priceDark",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["priceDark"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
                with: 100
              },
              {
                Header: "Price Medimum",
                accessor: "priceMedium",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["priceMedium"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              },
              {
                Header: "Price Special",
                accessor: "priceSpecial",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["priceSpecial"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              },
              {
                Header: "Price White",
                accessor: "priceWhite",
                filterMethod: (filter, row) =>
                  matchSorter(row, filter.value, { keys: ["priceWhite"] }),
                filterAll: true,
                sortable: true,
                filterable: true,
              }
            ]}
            defaultPageSize={10}
            showPaginationTop
            showPaginationBottom={false}
            defaultSorted={[
              {
                id: "dm_modified",
                desc: true
              }
            ]}
            className="-striped -highlight primary-pagination" />
            </CardBody>
        </Card>
      </div>
    );
  }
}
